var ResponsiveUtility = function() {
    var self = this;

    this.BOOTSTRAP_VERSION = 3;
    this.BOOTSTRAP_EXACT_VERSION = 3;

    var viewport,
        currentBreakpoint = '',
        previousBreakpoint = '';

    this.hiddenClassesToCheck = [];

    this.init = function(_viewport){
        viewport = _viewport;
        // console.log('ResponsiveUtility::init', viewport);
        
        if (this.BOOTSTRAP_VERSION == 4){

            var bootstrap4 = {
                'xs': $('<div class="device-xs visible-xs visible-xs-block"></div>'),
                'sm': $('<div class="device-sm visible-sm visible-sm-block"></div>'),
                'md': $('<div class="device-md visible-md visible-md-block"></div>'),
                'lg': $('<div class="device-lg visible-lg visible-lg-block"></div>'),
                'xlg': $('<div class="device-xlg visible-xlg visible-xlg-block"></div>')
            };
        
            viewport.use('bootstrap4', bootstrap4);
        }

        self.breakpoints = self.getBreakpoints();
        self.update();

        // previousBreakpoint = currentBreakpoint;
        // currentBreakpoint = viewport.current();

        // self.getHiddenClasses();

        $(window).resize(
            viewport.changed(function() {
                // fire event when breakpoint changes
                if (viewport.current() != currentBreakpoint){
                    self.update();
                    // console.log('ResponsiveUtility::DEVICE_BREAKPOINT_CHANGED: ', previousBreakpoint, currentBreakpoint);
                    //trigger event
                    $(document).trigger(courseEvent.DEVICE_BREAKPOINT_CHANGED);
                }
            })
        );
        
        $(window).resize(function(){
            self.resizeOverspillColumns();
        });

        $(document).on(courseEvent.SLIDE_SHOW_CONTENT, function(){
            
            // console.log('ResponsiveUtility::SLIDE_SHOW_CONTENT');
            self.resizeOverspillColumns();
        });
    }

    this.update = function(){
        // update previous breakpoint and current
        previousBreakpoint = currentBreakpoint;
        currentBreakpoint = viewport.current();
        // apply classes to body
        $('body').removeClass('device-'+previousBreakpoint);
        $('body').addClass('device-'+currentBreakpoint);

        // create array of hidden classes
        self.getHiddenClasses();

    }
    
    // BOOTSTRAP
    this.getBootstrapColumnsStrForNode = function(node){
        var colsObj = {
            offset: {
                xs: [],
                sm: [],
                md: [],
                lg: [],
                xlg: []
            },
            cols: {
                xs: [],
                sm: [],
                md: [],
                lg: [],
                xlg: []
            },
            classes: []
        }
        // console.log('ResponsiveUtility::nodeName', node.nodeName);

        for (x in colsObj.cols){
            // columns 
            // without col_ prefix
            if (node[x]){
                colsObj.cols[x][0] = node[x];
            } 
            // with col_ prefix
            if (node['col_'+x]){
                colsObj.cols[x][0] = node['col_'+x];
            } 

            // offsets 
            // without col_ prefix
            if (node[x+'_offset']){
                colsObj.offset[x][0] = node[x+'_offset'];
            }
            // with col_ prefix
            if (node['col_'+x+'_offset']){
                colsObj.offset[x][0] = node['col_'+x+'_offset'];
            }

            // bootstrap push
            // without col_ prefix
            if (node[x+'_push']){
                colsObj.classes.push('col-'+x+'-push-'+node[x+'_push']);
            }
            // with col_ prefix
            if (node['col_'+x+'_push']){
                colsObj.classes.push('col-'+x+'-push-'+node['col_'+x+'_push']);
            }

            // bootstrap pull
            // without col_ prefix
            if (node[x+'_pull']){
                colsObj.classes.push('col-'+x+'-pull-'+node[x+'_pull']);
            }
            // with col_ prefix
            if (node['col_'+x+'_pull']){
                colsObj.classes.push('col-'+x+'-pull-'+node['col_'+x+'_pull']);
            }
        }
        return this.getBootstrapColumnsClassStr(colsObj, 0);
    }

    this.getBootstrapColumnsClassStr = function(colsObj, index){
        var colStr = '';
        for (x in colsObj.cols){
            //console.log(colsObj[x], index);
            if (colsObj.cols[x].length > 0){
                colsObj.classes.push('col-'+x+'-'+colsObj.cols[x][index]);
            }else {
                if (this.BOOTSTRAP_VERSION == 3){
                } else if (this.BOOTSTRAP_VERSION == 4){
                    colsObj.classes.push('col');
                }
            }
			if (colsObj.offset[x]){
	            if (colsObj.offset[x].length > 0){
	                colsObj.classes.push('col-'+x+'-offset-'+colsObj.offset[x][index]);
	            }
			}
        }
        if (colsObj.classes){
            colStr += colsObj.classes.join(' ');
        }
        return colStr;
    }

    this.calculateChildBootstrapColumns = function(node){
        if (node.columns){
            //default is columns is for sm and up
            node.columns_sm = node.columns;
        }
        var classArray = [];
        for (var x in viewport.breakpoints){
            if (node['columns_'+x]){
                classArray.push('col-'+x+'-'+Math.ceil(12/node['columns_'+x]));
            }
        }
        return classArray.join(' ');
    }
    
    this.getBreakpoints = function(){
        // console.log('ResponsiveUtility::',Object.keys(viewport.breakpoints))
        return viewport.breakpoints;
    }

    this.isBreakpoint = function(breakpoint){
        // console.log('ResponsiveUtility::',Object.keys(viewport.breakpoints))
        return (viewport.breakpoints[breakpoint]) ? true : false;
    }

    this.getCurrentViewport = function(){
        //console.log('ResponsiveUtility::getCurrentViewport', viewport);
        return currentBreakpoint;
    }

    this.isCurrentViewport = function(breakpoint){
        return viewport.is(breakpoint);
    }

    this.getPreviousViewport = function(){
        return previousBreakpoint;
    }

    this.getHiddenClasses = function(){
        self.hiddenClassesToCheck = ['hidden', 'hidden-'+self.getCurrentViewport(), 'invisible', 'invisible-'+self.getCurrentViewport(), 'visibility-hidden'];
        for (var x in self.getBreakpoints()){
            if (x != self.getCurrentViewport()){
                // bootstrap has visible-X classes that will set visible on certain breakpoints
                self.hiddenClassesToCheck.push('visible-'+x);
                self.hiddenClassesToCheck.push('visible-'+x+'-block');
                self.hiddenClassesToCheck.push('visible-'+x+'-inline');
                self.hiddenClassesToCheck.push('visible-'+x+'-inline-block');
                self.hiddenClassesToCheck.push('visible-'+x+'-print');
            }
        }
        /*
        // different feedback classes
        self.hiddenClassesToCheck.push('feedbackCorrect', 'feedbackPartly', 'feedbackPartlyCorrect','feedbackIncorrect');
        for (var i = 0; i < 10; i++){
            self.hiddenClassesToCheck.push('feedbackOption'+i);
            self.hiddenClassesToCheck.push('feedback'+i);
        }
        */
    }

    this.isElementHidden = function($el){        
        var isHidden = false;
        // console.log('ResponsiveUtility::isElementHidden', self.hiddenClassesToCheck);
    //     for (var i = 0; i < self.hiddenClassesToCheck.length; i++){
    //         if ($el.hasClass(self.hiddenClassesToCheck[i])){// || $el.is)){
    //             isHidden = true;
    //             break;
    //         }
    //     }
    //     return isHidden;
    // }
    
    // this.isElementHiddenByParent = function($el){        
        isHidden = self.getClosestHiddenElement($el).length > 0;
        // console.log('ResponsiveUtility::isElementHidden', self.hiddenClassesToCheck);
        // check element and any parents for any of the hiddenClassesToCheck e.g hidden, invisible 
        // if ($el.parentsUntil('.slide').addBack().hasClass(self.hiddenClassesToCheck[i])){
        //         for (var i = 0; i < self.hiddenClassesToCheck.length; i++){
        //         isHidden = true;
        //         break;
        //     }
        // }
        return isHidden;
    }

    this.getClosestHiddenElement = function($el){
        // console.log('getClosestHiddenElement', $el);
        var $hiddenEl = [];
        // check element and any parents for any of the hiddenClassesToCheck e.g hidden, invisible 
        $el.parentsUntil('.slide').addBack().each(function(){
            var $this = $(this);
            // console.log('getClosestHiddenElement', $this.attr('class'), $this.css('display'));
            if ($this.css('display') == 'none'){
                $hiddenEl = $this;
                return false;
            } else {
                for (var i = 0; i < self.hiddenClassesToCheck.length; i++){

                    if ($this.hasClass(self.hiddenClassesToCheck[i])){
                        // isHidden = true;
                        $hiddenEl = $this;
                        return false;
                    } 
                    // break;
                }
            }
        });
        return $hiddenEl;
    }

    this.resizeOverspillColumns = function(){
        // console.log('ResponsiveUtility::resizeOverspillColumns');

        // lets check for overspill and apply new widths    
        $('.overspill-image').each(function(){
            var $this = $(this);
            var $col = $(this).closest('[class^="col"]');
            var $row = $(this).closest('.row');
            // reset width to auto
            $this.css('width', 'auto');
            // reset max-height to none
            $this.css('max-height', 'none');
            $col.removeClass('overspill-first-child overspill-last-child');
            $col.addClass('overspill-inactive');

            if ($col.outerWidth() != $row.width()){
                $col.removeClass('overspill-inactive');

                var isFirstChild = $col.is(':first-child');
                // check if pull or push
                if (isFirstChild && $col.is('[class*="col-'+currentBreakpoint+'-push-"]')){
                    isFirstChild = false;
                } else if (!isFirstChild && $col.is('[class*="col-'+currentBreakpoint+'-pull-"]')){
                    isFirstChild = true;
                }

                $col.addClass(isFirstChild ? 'overspill-first-child' : 'overspill-last-child');

                if (isFirstChild && languageConfig.languages[currentLanguage].direction == 'ltr' ||
                    !isFirstChild && languageConfig.languages[currentLanguage].direction == 'rtl'){
                    // console.log('Responsive overspill - first', $col.position().left, $col.width());
                    
                    $this.width($col.position().left+$col.outerWidth());
                } else if (isFirstChild && languageConfig.languages[currentLanguage].direction == 'rtl' ||
                    !isFirstChild && languageConfig.languages[currentLanguage].direction == 'ltr'){
                    // console.log('Responsive overspill - last', $(window).width(), $col.position().left);
                    
                    $this.width($(window).width()-$col.position().left);
                }

                if (!$this.hasClass('overspill-image-fill')){
                    $this.css('max-height', $this.find('img').height());
                }
            }
        });
    }

    this.getHiddenClassesUpToBreakpoint = function(breakpoint){
        // bootstrap has hidden-X classes that will hide on certain breakpoints - find all below this breakpoint
        var hiddenClasses = [];
        for (var x in self.getBreakpoints()){
            hiddenClasses.push('hidden-'+x);
            if (x == breakpoint){
                break;
            }
        }
        return hiddenClasses.join(' ');
    }

    this.getVisibleClassesUpToBreakpoint = function(breakpoint){
        // bootstrap has visible-X classes that will set visible on certain breakpoints - find all below this breakpoint
        var visibleClasses = [];
        for (var x in self.getBreakpoints()){
            visibleClasses.push('visible-'+x);
            if (x == breakpoint){
                break;
            }
        }
        return visibleClasses.join(' ');
    }
}