var UIController = function() {
    var self = this;
    var menuArray = [];
    
    this.setupUI = setupUI;
    
    // var navigationBtns = ['language','search','menu','resource','help','audio','glossary','exit']
    var navigationBtns = ['language','menu','resource','help','audio','exit'];
    var navigationBtnTimelines = []; // used for greensock
    
    function init(){
        
    }
    
    function setupUI(){
        createNavMenu();

        $('#acc_skiptocontent').html(courseData.getAccessibleLabel('skipToMainContent'));
        $('#content').attr('aria-label', courseData.getAccessibleLabel('mainContent'));

        $('.navbar-brand img').attr('src', courseData.getLogo());
        $('.navbar-brand img').attr('alt', courseData.getLogoAlt());
        //$('.sidemenu .btn_icon').html(svgUtils.createSVG('speechbubbles')); //attr('alt', courseData.getLogoAlt());
        
        for (var i = 0; i < navigationBtns.length; i++){
            var $btn = $(navigationButton[navigationBtns[i].toUpperCase()+'_BUTTON']);
            var btnTitle = $btn.eq(0).find('.btn_text').text();
            $btn.attr('aria-label', btnTitle);
            //console.log('UIController::setupUI', btnTitle);
            $btn.parent()
                .data('lang-title', btnTitle)
                .attr('data-original-title', btnTitle);
            //if (navigationConfig.buttons[navigationBtns[i]] || componentConfig[navigationBtns[i]]){
                var $btnIcon = $btn.find('.btn_icon');
                $btnIcon.attr('aria-hidden', 'true');
                $btnIcon.html(svgUtils.createSVG(navigationBtns[i])); //attr('alt', courseData.getLogoAlt());
                var timeline = svgUtils.animateSVG(navigationBtns[i], $btnIcon);
                timeline.pause();
                navigationBtnTimelines.push(timeline);
            //}
        }
        if (navigationBtns.indexOf('audio') != -1){
           self.audioIconSVGTimeline = navigationBtnTimelines[navigationBtns.indexOf('audio')];
        }
        
        $('.navbar [data-toggle="tooltip"]').tooltip();
        
        
        //create languages
        var numLanguages = 0;
        var htmlStr = '';
        for (var i = 0; i < languageConfig.languages.length; i++){
            if (languageConfig.languages[i].active){
                numLanguages++;
                htmlStr += '<li><a href="#" data-language-code="'+languageConfig.languages[i].code+'">'+languageConfig.languages[i].language+'</a></li>';            
            }
        }
        $('#language-dropdown .dropdown-menu').html(htmlStr);
        if (numLanguages == 1){
            $('.language-dropdown').hide().removeClass('visible-xs').addClass('hidden-xs');
        } else {
            $('#language-dropdown .dropdown-menu a').on('click keypress', utils.handleClickAndPress(function (e) {
                var $this = $(e.currentTarget);
                //console.log($this.data('language-code'));
                loadLanguage($this.data('language-code'));
            }));            
        }


        //$('.navbar-brand img').attr('src', xmlData.langXML.logo[0].Text);
        // $('.navbar-text').html(courseData.getCourseTitle());
        $('.navbar-text').html(courseData.data.title[0].Text);

        

        if (courseData.data.settings[0].navigation[0].footer){
            var currentDate = new Date();
            var year = currentDate.getFullYear();
            var footerHTML = '<span class="copyright">'+courseData.data.settings[0].navigation[0].footer[0].copyright[0].Text.split('{YEAR}').join(year)+'</span>';
            // var footerHTML = htmlUtils.getHTMLText(courseData.data.settings[0].navigation[0].footer[0], {anim: false});
            $('footer .footer-text').html(footerHTML);

            // check footer for modal
            $('footer .footer-text .footer-modal').on('click keypress', utils.handleClickAndPress(function (e) {
                var $this = $(e.currentTarget);
                if ($this.hasClass('disabled')){
                    return false;
                }
        
                $.Modal({
                    //color: courseData.navData.module[navigationController.moduleIndex].color ? courseData.navData.module[navigationController.moduleIndex].color : 'blue',
                    data: courseData.data.settings[0].navigation[0].footer[0].modal[0]
                });
                $('#modal').modal('show');
            }));
        } else {
            $('footer').addClass('hidden');
        }
        
        if (navigationConfig.incompletePageMessage){
        	$('.content-locked-message').html(htmlUtils.getHTMLText(courseData.data.settings[0].messages[0].contentLocked[0], {anim: false}));
        }

		$('.navbar-brand').on('click keypress', utils.handleClickAndPress(function (e) {
            var $this = $(e.currentTarget);
            if ($this.hasClass('disabled')){
                return false;
            }
            //navigationController.changePageToData(0,0,0);
        }));
        
//        $(navigationButton.MENU_BUTTON).data('target', )
        $(navigationButton.HELP_BUTTON)
            .attr('data-toggle', 'modal')
            .attr('data-target', '#modal');
        $(navigationButton.HELP_BUTTON).on('click keypress', utils.handleClickAndPress(function (e) {
            e.preventDefault();
            var $this = $(e.currentTarget);
            
            self.showHelp(false);
        }));
        
        $(navigationButton.RESOURCE_BUTTON)
            .attr('data-toggle', 'modal')
            .attr('data-target', '#modal');
        $(navigationButton.RESOURCE_BUTTON).on('click keypress', utils.handleClickAndPress(function (e) {
            e.preventDefault();
            var $this = $(e.currentTarget);

            self.showResources(false);
        }));
        
        $(navigationButton.MENU_BUTTON).on('click keypress', utils.handleClickAndPress(function (e) {
            e.preventDefault();
            var $this = $(e.currentTarget);
            if ($this.hasClass('disabled')){
                return false;
            }
            navigationController.showMenu();
        }));
        $(navigationButton.MENU_BUTTON).disable();

// action for click anywhere on page
        $(document).on('click', function(event) {
            //Hide the menus if visible
            
            /*var clickover = $(event.target);
            var _opened = $('.navbar-collapse').hasClass('navbar-collapse in');
            if (_opened === true && !clickover.hasClass('navbar-toggle')) {
                $('button.navbar-toggle').click();
            }*/
            
            $(window).trigger('WINDOW_CLICK');
        });
        
        
        $(window).on(courseEvent.DEVICE_BREAKPOINT_CHANGED, deviceBreakpointChanged);
        $(window).on('resize', windowResize);

    }
    
    this.showNav = function(){
        //console.log('show nav');
        for (var i = 0; i < navigationBtnTimelines.length; i++){
            if (navigationBtns[i] != 'audio'){
                navigationBtnTimelines[i].delay(1).play();
            }
        }
    }
    
    this.hideNav = function(){
        for (var i = 0; i < navigationBtnTimelines.length; i++){
            if (navigationBtns[i] != 'audio'){
                navigationBtnTimelines[i].reverse();
            }
        }        
    }
    
	this.updateUI = function(){
        if (componentConfig.search){
            //clear search data
            searchController.updateSearch();
        }
        
        var completeCount = 0;
        var numValidSlides = 0;
        for (var i = 0; i < navigationController.pageArray.length; i++){ 
            if (menuArray[navigationController.pageArray[i].module]){
                for (var j = 0; j < navigationController.pageArray[i].slides.length; j++){ 
                    if (navigationController.isValidRole(navigationController.pageArray[i].slides[j].roles)){
                        //console.log(i);
                        numValidSlides++;
                        if (storageController.isSlideComplete(i, j)){
                            completeCount++;
                        }  
                    }
                }
            }
        }
        
        $('#userProgress #points-score').text(Math.round(completeCount/numValidSlides*100));
        //console.log('COMPLETE', numValidSlides, completeCount)
        
        // update section marker
        if (componentConfig.progress.module){
            for (var i = 0; i < menuArray.length; i++){ 
                var $progressSection = $('#progress #progress-section'+i);
                if (storageController.isModuleActive(i) && navigationController.isModuleValidForRole(i)){ 
                    $progressSection.removeClass('hidden');                    
                    if (storageController.isModuleComplete(i)){
                        $progressSection.addClass('complete');
                    }
                } else {
                    $progressSection.addClass('hidden');                    
                }
            }
            $('#progress .progress-section').removeClass('active');
            if (menuArray[navigationController.moduleIndex]){    
                $('#progress #progress-section'+navigationController.moduleIndex).addClass('active');
            }
        } else {
            // calculate progress for whole course
            var courseProgress = storageController.getProgressData();

            if (componentConfig.progress.adjustments){
                // need to modify with assessment progress
                // if (componentConfig.progress.adjustments.pages){
                //     courseProgress.num += componentConfig.progress.adjustments.pages;
                //     courseProgress.num += componentConfig.progress.adjustments.pages;
                // }
                if (componentConfig.progress.adjustments.assessments){
                    
                    for (var i = 0; i < componentConfig.progress.adjustments.assessments.length; i++){ 
                        var assessment = window[componentConfig.progress.adjustments.assessments[i]+'Assessment'];
                        if (assessment){
                            courseProgress.num += assessment.data.questions.length;
                            for (var j = 0; j < assessment.data.questions.length; j++){ 
                                courseProgress.complete += assessment.data.questions[j].attempts.length > 0 ? 1 : 0;
                            }
                        }
                    }
                }
                courseProgress.perc = courseProgress.complete/courseProgress.num;
            }
            // console.log('PROGRESS', courseProgress);

            var perc = courseProgress.perc*100;

            $('#progress #progressBar').attr('aria-valuenow', Math.round(perc));
            $('#progress .progress-bar-status').css('width', perc+'%');

            storageController.setProgressMeasure(perc);
        }
        
        // activate menu button
        if ($(navigationButton.MENU_BUTTON).hasClass('disabled')){
            // if (storageController.isModuleComplete(0)){
            if (navigationController.moduleIndex > 0 && navigationController.moduleIndex != navigationController.menuModuleNum){
                $(navigationButton.MENU_BUTTON).enable();
            }
        }
	}
    
    function createNavMenu(){
        menuArray = courseData.getMenu();
        //console.log('menuArray', menuArray);
        
//        for (var i = 1; i < navTextXML.module.length; i++){
//        if (navTextXML.module[i].parentmodule == undefined){
//            var color = navTextXML.module[i].color;
//            htmlStr += '<div class="section '+color+'Bkg "></div>';
//        }
//    }
        
        var $navmenubar = $('#navmenubar');
        $navmenubar.addClass('menuStyle'+navigationConfig.menu.style);
        for (var x in navigationConfig.menu.numPerRow){
            $navmenubar.addClass('menuItemsPerRow-'+x+'-'+navigationConfig.menu.numPerRow[x]);
        }
        
        var progresshtmlStr = '';
        var htmlStr = '';
        for (var i = 0; i < menuArray.length; i++){ 
            if (componentConfig.progress.module){
                progresshtmlStr += '<div id="progress-section'+i+'" class="progress-section'+(menuArray[i] ? '' : ' hidden')+' '+courseData.navData.module[i].color+'"></div>';
            }
            if (menuArray[i]){    
                
                
                htmlStr += '<li>';
                if (menuArray[i].submenu.length == 0){     
                    htmlStr += '<a href="#" data-toggle="collapse" data-target="#navmenubar" data-module="'+menuArray[i].module+'">'+menuArray[i].title+'</a>';
                } else{
                    htmlStr += '<a href="#" data-module="'+menuArray[i].module+'">'+menuArray[i].title+'</a>';
                    htmlStr += '<div class="submenuWrap">';
                        htmlStr += '<ul class="submenu ">';
    
                        for (var j = 0; j < menuArray[i].submenu.length; j++){     
                            htmlStr += '<li><a href="#" data-toggle="collapse" data-target="#navmenubar" data-module="'+menuArray[i].module+'" data-page="'+menuArray[i].submenu[j].page+'" data-slide="'+menuArray[i].submenu[j].slide+'">'+menuArray[i].submenu[j].title+'</a>';
                        }
                        htmlStr += '</ul>';
                    htmlStr += '</div>';
    
                }
                htmlStr += '</li>';
            }
        }
        $('#navmenubar .navmenu').html(htmlStr);
        
        $('#navmenubar .navmenu > li > a').on('click', function(e){
            e.preventDefault();
            $('#navmenubar .navmenu > li > a').not($(this)).removeClass('active');
            $('#navmenubar .submenuWrap').slideUp();
            
            // check if has a submenu
            if ($(this).parent().find('.submenu').length){
                // show submenu
                $(this).toggleClass('active');
                if ($(this).hasClass('active')){
                    $(this).parent().find('.submenuWrap').delay(300).slideDown();
                }
                //navigationController.changePageToData(parseInt($(this).data('module')),0,0);
            } else{
                $(this).addClass('active');
                navigationController.changePageToData(parseInt($(this).data('module')),0,0);
            }
        });
        
        $('#navmenubar .navmenu .submenu > li > a').on('click', function(e){
            e.preventDefault();
            navigationController.changePageToData(parseInt($(this).data('module')), parseInt($(this).data('page')), parseInt($(this).data('slide')));
        });
        
        var $navmenubarInner = $('#navmenubarInner');
        //$navmenubarInner.removeClass(utils.getWordsByFragment($this[0].className, 'menuStyle').join(' '))
        
        //hover events for menuStyle 0
        $('#navmenubar.menuStyle0 .navmenu > li a').hover(function(){
            $navmenubarInner[0].className = '';
            $navmenubarInner.addClass('moduleBkg moduleBkg'+($(this).parent().index()+1));
        },function(){
            $navmenubarInner[0].className = '';
        });
        
        // progress bar
        
        if (componentConfig.progress.module){
            $('#progress #progress-sections').html(progresshtmlStr);
        } else {
            $('#progress #progress-sections').remove();
            $('#progress #progress-section-marker').remove();
        }
        $('#progress #progress-text').html(htmlUtils.getHTMLText(courseData.data.settings[0].navigation[0].progress[0]));
    }
    

    function deviceBreakpointChanged(){
            
        if (responsiveUtils.getCurrentViewport() == 'xs' || (responsiveUtils.prevViewport == 'xs' && responsiveUtils.getCurrentViewport() != 'xs')){
            //console.log(prevViewport+'>'+responsiveUtils.getCurrentViewport());
            // need to manually change the data-target attributes to allow the menus to appear
            $('#navbar, #navmenubar')
                .removeClass('in');
                // .attr('aria-expanded', false);
        }
        if (responsiveUtils.getCurrentViewport() == 'xs'){
            $('#search-dropdown').addClass('open');
        }
        if (responsiveUtils.getCurrentViewport() == 'xs'){
            //remove tooltips on mobile
            //$('.navbar [data-toggle="tooltip"]').tooltip('destroy');
            $('.navbar [data-toggle="tooltip"]').attr('data-original-title', '');
        } else {
            
            $('.navbar [data-toggle="tooltip"]').each(function(){
                //console.log('tooltip title', $(this).data('original-title'), $(this).data('lang-title'))
                $(this).attr('data-original-title', $(this).data('lang-title')); 
            });
            //$('.navbar [data-toggle="tooltip"]').tooltip();
        }
        
        // update any device specific classes
        htmlUtils.updateDeviceSpecificClasses();

        contentController.breakpointChanged();  
    }

    function windowResize(){
        contentController.resizeContent();    
        
        navigationController.checkForScroll();
    }

    this.showHelp = function(force){
        force = typeof force !== 'undefined' ? force : true;

        var modalData = courseData.data.help;

        $.Modal({
            class: 'help',
            data: modalData
        });
        
        for (var i = 0; i < navigationBtns.length; i++){
            var $btnIcon = $('.nav '+navigationButton[navigationBtns[i].toUpperCase()+'_BUTTON']+' .btn_icon');
            var $modalBtnIcon = $('#modal '+navigationButton[navigationBtns[i].toUpperCase()+'_BUTTON']+' .btn_icon');
        
            $modalBtnIcon.html($btnIcon.html());
        }

        if (force){
            $('#modal').modal('show');
        }
    }

    this.showResources = function(force){
        force = typeof force !== 'undefined' ? force : true;

        var modalData = courseData.data.resource;

        $.Modal({
            class: 'resource',
            data: modalData
        });

        if (force){
            $('#modal').modal('show');
        }
    }
    
    init();
}