function initUI(){
    if (courseConfig.responsive){
        $('body').addClass('responsive');
    } else {
        $('body').addClass('not-responsive');
        $('#content-wrapper').css({width: courseConfig.width, height: courseConfig.height});
    }
    if (courseConfig.animate.enable){
        $('body').addClass('content-animated');
    } else {
        $('body').addClass('not-animated');
    }
    $('#content-wrapper').load(coursePath+'content/UI/UI.html', function(response, status, xhr) {
        //console.log('Main::initUI', status);
		if (status == "success") {	

            // need to make sure image paths are correct in UI.html
            $('#content-wrapper').find('img').each(function(){
                var src = $(this).attr('src');
                if (src != ''){
                    $(this).attr('src', utils.getURL(src));
                }
            });

            onUILoadComplete();
        }
    });
}

function onUILoadComplete() {
    
    // load Storage controller
    storageController = new StorageController();
    
	//$('body').addClass('lang-'+courseConfig.defaultLanguageFolderName);
	//$('body').addClass(courseConfig.defaultLanguageDirection);
    
//    loadNavigationData();
    loadCourseData();
}

function loadCourseData(){
//    $(document).on(courseEvent.XMLDATA_LOAD_COMPLETE, onCourseDataLoadComplete);
    
    var numLanguages = 0;
    activeLanguages = [];
    for (var i = 0; i < languageConfig.languages.length; i++){
        if (languageConfig.languages[i].active){
            numLanguages ++;
            activeLanguages.push(languageConfig.languages[i].code);
        }
    }
    multiLanguage = (numLanguages > 1);
    
    // retrieve the language
    var lang = storageController.getLanguage();
    if (lang == '') {
        // use the ?lang= parameter from the URL
        var langURLParameter = parseInt(utils.getURLParameter('lang'));
        if (!isNaN(langURLParameter)){
            lang = langURLParameter;
        } else {
            lang = languageConfig.defaultLanguage;
        }
    }  
    //console.log(lang);
    if (!isNaN(lang)){
        // if language is a number, get the language code
        lang = languageConfig.languages[lang].code;
    }
    
    loadLanguage(lang);
    
    //loadFramework();
}

function loadLanguage(lang){
    //currentLanguage = lang;
    $('.preloader').show();
    
    // store current page
    if (navigationController){
        
        languageChangedPage = navigationController.currentPage;
    }
    
    if (contentController){
        contentController.unloadContent();
    }
    
    for (var i = 0; i < languageConfig.languages.length; i++){
        if (languageConfig.languages[i].active){
            if (languageConfig.languages[i].code == lang){
                currentLanguage = i;
                break;
            }
        }
    }
    
    
    //set the tracking Google ID
    gtagID = languageConfig.languages[currentLanguage].analytics;
    
    if (courseConfig.googleAnalytics.enabled){
        //add the gtag script
        var newScript = document.createElement("script");
        newScript.type = "text/javascript";
        newScript.setAttribute("async", "true");
        newScript.setAttribute("src", "https://www.googletagmanager.com/gtag/js?id="+gtagID);
        document.documentElement.firstChild.appendChild(newScript);
    }
    
    $(document).on(courseEvent.COURSE_DATA_LOAD_COMPLETE, onCourseDataLoadComplete);
    courseData = new CourseData();
    courseData.init(lang);
}

function onCourseDataLoadComplete(){
    $(document).off(courseEvent.COURSE_DATA_LOAD_COMPLETE, onCourseDataLoadComplete);
    
    loadFramework();
}

