function frameworkLoaded(){
    console.log('Main::frameworkLoaded');
    
    $('body').removeClass('ltr rtl');
    for (var i = 0; i < languageConfig.languages.length; i++){
        if (languageConfig.languages[i].active){
            $('body').removeClass('lang-'+languageConfig.languages[i].code);
        }
    }
    $('body').addClass('lang-'+languageConfig.languages[currentLanguage].code+' '+languageConfig.languages[currentLanguage].direction);
    
    navigationController.updateCourseTitle();
    
    // set up assessments
    for (var x in assessmentConfig.assessments){
        if (assessmentConfig.assessments[x].enable){
            window[x+'Assessment'].setupAssessment();
        }
    }
    
    if (componentConfig.search){
        searchController = new SearchController(); 
    }
    
    uiController.setupUI();
    navigationEvents.init();
    
    if (navigationController.currentPage > 1){
        if (usingPageURLParameter || languageChangedPage){
            languageChangedPage = undefined;
            // dont show bookmark popup if usingPageURLParameter
            navigationController.historyArray = storageController.getHistory();
            navigationController.changePage(navigationController.currentPage, 'replace');
        } else {
            // show bookmark confirm popup
            $.Alert({
                title: courseData.getMessageText('bookmark', 'title'), //xmlData.langXML.messages[0].bookmark[0].title[0].Text,
                message: courseData.getMessageText('bookmark', 'message'), //xmlData.langXML.messages[0].bookmark[0].message[0].Text,
                confirm: courseData.getMessageText('bookmark', 'confirm'), //xmlData.langXML.messages[0].bookmark[0].confirm[0].Text,
                cancel: courseData.getMessageText('bookmark', 'cancel'), //xmlData.langXML.messages[0].bookmark[0].cancel[0].Text,
                onConfirm:function(){
                    navigationController.historyArray = storageController.getHistory();
                    navigationController.changePage(navigationController.currentPage, 'replace');
                },
                onCancel:function(){
                    storageController.setHistory([]);
                    navigationController.historyArray = storageController.getHistory();
                    navigationController.changePage(0, 'replace');
                }		
            });
        }
    } else {
        storageController.setHistory([]);
        navigationController.historyArray = storageController.getHistory();
        navigationController.changePage(0, 'replace');
    }
}

/**
 * @function loadCertificateWin
 * @description This function is used to load the Certificate window.
 */
function loadCertificateWin(){
	if(!storageController.isPassed()){
		return false;
	} 
	var winwidth = 652;
	var winheight = 474;               
	var xOffset =10;//  screen.width/2 - winwidth/2;
	var yOffset =10;// screen.height/2 - winheight/2;       								
	var crWin = window.open('course/framework/html/certificate.html', 'cerWin', 'width=' + winwidth + ',height=' + winheight + ',scrollbars=0,screenX=0,screenY=0,resizable=yes,status=no,top=' + yOffset + ',left=' + xOffset + '');
	crWin.focus();
	return false;
}

function getCertificateData(){
    var completionDate = '';
    if (storageController.isPassed){
        var completionDateArr = storageController.getCompletionDate().split('_');
        var month = courseData.data.date[0].months[0].month[parseInt(completionDateArr[1])-1].Text;
        completionDate = courseData.data.date[0].format[0].Text.split('{D}').join(completionDateArr[0]).split('{M}').join(month).split('{Y}').join(completionDateArr[2]);
    }
    
    return {
        language: languageConfig.languages[currentLanguage],
        completionDate: completionDate,
        studentName: storageController.studentName,
        courseName: courseData.data.name[0].Text,
        courseID: courseData.data.id[0].Text,
        text: courseData.data.settings[0].certificate[0]
    }
}

