var CanvasController = function() {
    var self = this;

    self.id;
    
    self.isLoaded = false;

    self.loader = [];
    self.assets = [];
    
    self.elements = [];

    self.soundInstance;
   
    this.createCanvas = function(id, width, height){
        self.id = id;
        self.minWidth = width;
        self.minHeight = height;

        var htmlStr = '';
        htmlStr += '<div class="canvas-wrap">';
            htmlStr += '<canvas id="'+id+'" width="'+width+'" height="'+height+'"></canvas>';
        htmlStr += '</div>';

        return htmlStr;
    }

    this.setupCanvas = function($el){
        self.$el = $el;
        self.$canvas = $el.find('#'+self.id);

        self.stage = new createjs.Stage(self.id);

        // grab canvas width and height for later calculations:
        self.w = self.stage.canvas.width;
        self.h = self.stage.canvas.height;
        self.aspectRatio = self.w / self.h;

        self.container = new createjs.Container();
        self.stage.addChild(self.container);
        // console.log(self.container);

        // var startBlock = new createjs.Bitmap(loader.getResult('start_block'));
        
        //         self.addElement(self.competitors[i].container, startBlock, {
        //             scaleX: 0.5,
        //             scaleY: 0.5,
        //             x: 20,
        //             y: 0,
        //             regX:374,
        //             regY:54
        //         });
        //     }

        createjs.Ticker.framerate = 60;
        createjs.Ticker.on("tick", handleTick);
        
        function handleTick(event) {    
            self.stage.update(event);
        }

        $(window).on('resize', function(){
            self.resize();
        });
    }

    this.loadSounds = function(manifest){
        self.sounds = {
            path:'course/content/media/',
            manifest: []
        };
        self.sounds.manifest = self.sounds.manifest.concat(manifest);

        console.log(self.sounds.manifest)

        // console.log('ComponentFTC2022_RaceCanvas sounds.isReady()', createjs.Sound.isReady());
        if (createjs.Sound.isReady()){
            createjs.Sound.addEventListener("fileload", soundsLoadedHandler);

            function soundsLoadedHandler(event) {
                console.log('soundsLoaded');
            }
        }
        createjs.Sound.registerSounds(self.sounds);

        if (createjs.Sound.isReady()){
        }
    }

    // this.createContainer = function(){
    //     self.container = new createjs.Container();
    // }

    this.loadManifest = function(manifest, completeCallback){
        // console.log('loadManifest');
        self.loader = new createjs.LoadQueue(false);
        self.loader.addEventListener("fileload", handleFileLoad);
        self.loader.addEventListener("complete", function(){
            canvasSetupComplete(completeCallback);
        });
        self.loader.loadManifest(manifest, true, "course/content/images/");
    }
    
    this.getBitmap = function(bitmap){
        return new createjs.Bitmap(self.loader.getResult(bitmap));
    }

    function handleFileLoad(event){
        // console.log('handleFileLoad');
        self.assets.push(event);
    }
    
    function canvasSetupComplete(completeCallback){
        self.isLoaded = true;

        // console.log('CanvasController:canvasSetupComplete');
        completeCallback.call();
    }
    
    this.addElement = function (container, el, properties){    
        var obj = {
            el: el,
            properties: properties
        }
        for (var x in properties){
            el[x] = properties[x];
        }
    
        container.addChild(el);
        self.elements.push(obj);
        // return.push(obj);
        return obj;
    }
    
    this.updateElement = function (el, properties){    
        // var obj = {
        //     el: el,
        //     properties: properties
        // }
        for (var x in properties){
            el[x] = properties[x];
        }
        self.stage.update();
    }

    this.resize = function(){
        console.log('resize')
        // var windowRatio = self.w/self.h;

        var w = self.$el.width(); //self.w;//window.innerWidth-2; // -2 accounts for the border
        var h = w/self.aspectRatio;//self.self.h;//Math.min(500, w); //window.innerHeight-2;
        
        self.$canvas.css({width:w,height:h});
        self.stage.canvas.width = w * window.devicePixelRatio;
        self.stage.canvas.height = h * window.devicePixelRatio;

        var bounds = self.container.getTransformedBounds();
    //        if (window.innerHeight * aspect < w)
    //            gameContainer.scale = window.innerHeight/bounds.height;
    //        else
    //            gameContainer.scale = w/bounds.width;
        if (w < self.minWidth && h < self.minHeight){
            if (w/self.minWidth < h/self.minHeight)
                self.container.scale = w/self.minWidth;
            else
                self.container.scale = h/self.minHeight;
        } else if(w < self.minWidth){
            self.container.scale = w/self.minWidth;
        } else if (h < self.minHeight){
            self.container.scale = h/self.minHeight;
        }
        
    //        gameContainer.x = self.minWidth/2;
    //        gameContainer.y = self.minHeight/2;
        self.container.x = (w-(self.minWidth*self.container.scale))/2;
        self.container.y = (h-(self.minHeight*self.container.scale))/2;
        
        self.stage.scale = window.devicePixelRatio;

        self.stage.update();
    }

    this.unload = function(){
        self.soundInstance.stop();

        self.stage.removeAllChildren();
        self.stage.removeAllEventListeners();
        self.stage.enableDOMEvents(false);
    }
}