var InstructionController = function(opts) {
    var self = this;
    this.$instruction;
    
    this.init = init;
    this.change = change;
    
    this.prevInstruction = '';
    
    init();
    
    function init(){
        self.$instruction = $('.instructionText');
    }
    
    function change(_text){
        //console.log('instructionController::change',_text);
        
        if(_text != self.prevInstruction){
            self.$instruction.fadeTo(300, 0, function(){
                //$(this).css("visibility", "hidden");   
                self.$instruction.html(_text);
                self.$instruction
                    .css('opacity', 0)
                    //.css('visibility', 'visible')
                    .fadeTo(300, 1);
            }); // duration, opacity, callback
        }
        self.prevInstruction = _text;
    }
};