var StorageController = function(opts) {
    var self = this;

    this.getScormTime = getScormTime;
    
    var scormController;
    this.storageAPI;
    
    var lmsConnected = false;
    var lmsDataToSave = false;

    var inExitProcess = false;
    
    var unloaded = false;
    
    var STANDALONE      = 1;
    var SCORM_1_2       = 2;
    var SCORM_2004      = 3;
    var XAPI            = 4; // Tincan / xAPI - NOT IMPLEMENTED
    var LOCALSTORAGE    = 5; // use browsr local storage
    
    
	this.SESSION_TIME       = "cmi.core.session_time";
    this.LESSON_LOCATION    = "cmi.core.lesson_location";
    this.LESSON_STATUS      = "cmi.core.lesson_status";
    this.SUCCESS_STATUS     = "";
    this.SUSPEND_DATA       = "cmi.suspend_data";
    this.STUDENT_ID         = "cmi.core.student_id";
    this.STUDENT_NAME       = "cmi.core.student_name";
    this.STUDENT_LANG       = "cmi.student_preference.language";
    this.STUDENT_AUDIO      = "cmi.student_preference.audio";
    this.SCORE              = "cmi.core.score.raw";
    this.SCORE_MAX          = "cmi.core.score.max";
    this.SCORE_MIN          = "cmi.core.score.min";
    this.SCORE_SCALED       = "cmi.score.scaled";
    
    var initalTime;

    var saveTimeout = null,
        suspendDataTimeout = null;

    this.dataObj = {
        completion: '',
        version: '',
        language: '',
        audio: 1,
        // captions: false,
        captions: null,
        roles: [],
        bookmark: [],
        position: {},
        history: [],
        slideProgress: [],
        slideData: [],
        menu: {
            active: [],
            mandatory: [],
            locked: []
        },
        assessment: {},
        cmi:{
            i:{}, // cmi.interaction IDs
            o:{} // cmi.objective IDs
        },
        custom:{},
        system:{},
        exit:[]
    }

    var commitObj = {};
    
    this.studentID = '';
    this.studentName = '';
    this.score = 0;

    this.isLastSlideComplete = false;
    
    function init(){
        console.log('StorageController::init', courseConfig.storage.complianceType)

        initalTime = new Date();

        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
                try{ 
                    if(courseConfig.storage.complianceType == SCORM_1_2){
                        pipwerks.SCORM.version = "1.2";
                    }else{
                        pipwerks.SCORM.version = "2004";
                    }		

                }catch(err){}
                
                self.storageAPI = pipwerks.SCORM;
                                
                break;
            case XAPI: 
                break;
        }
        
        if (courseConfig.storage.complianceType == SCORM_2004 || courseConfig.storage.complianceType == LOCALSTORAGE){
			self.SESSION_TIME        = "cmi.session_time";
            self.LESSON_LOCATION     = "cmi.location";
			self.LESSON_STATUS       = "cmi.completion_status";
			self.SUCCESS_STATUS      = "cmi.success_status";
			self.SUSPEND_DATA        = "cmi.suspend_data";
            self.STUDENT_ID          = "cmi.learner_id";
			self.STUDENT_NAME        = "cmi.learner_name";
			self.STUDENT_LANG        = "cmi.learner_preference.language";
			self.STUDENT_AUDIO       = "cmi.learner_preference.audio_level";
			self.SCORE               = "cmi.score.raw";
			self.SCORE_MAX           = "cmi.score.max";
			self.SCORE_MIN           = "cmi.score.min";
    		self.PROGRESS_MEASURE    = "cmi.progress_measure";
        }
        
        if (courseConfig.storage.complianceType == STANDALONE) {
			lmsConnected = false;
        } else {
            if (courseConfig.storage.complianceType != LOCALSTORAGE){
                lmsConnected = self.storageAPI.init();
            
		    } 
            
            self.studentID = self.getStudentID();
            self.studentName = self.getStudentName();
            self.score = self.getScore();
            self.completionStatus = self.getComplete();
            self.successStatus = self.getSuccess();
            if (self.completionStatus != 'complete' && courseConfig.storage.complianceType == SCORM_2004){
                //self.setSuccess('unknown');
            }
            
            // TEST Interactions
            //console.log('Interaction', self.getInteraction());
            
            window.onbeforeunload = unloadHandler;
            window.onunload = unloadHandler;
		}
        
        console.log('StorageController::init lmsConnected', lmsConnected);
        
    }
    
    this.setLanguage = function (lang){
        self.dataObj.language = lang;
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                setToStorageAPI(self.STUDENT_LANG, String(lang));
                break;
        }
    }
    
    this.getLanguage = function (){
        var lang = '';
        //console.log('StorageController::getLanguage ', lang);
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                lang = getFromStorageAPI(self.STUDENT_LANG);
                    //lang = (lang === "") ? languageConfig.defaultLanguage : lang;  
                
                break;
        }
        return lang;
    }

    this.setAudio = function (audioLevel){
        self.dataObj.audio = audioLevel;
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                setToStorageAPI(self.STUDENT_AUDIO, String(audioLevel));
                break;
        }
    }
    
    this.getAudio = function (){
        var audioLevel = 1;
        //console.log('StorageController::getAudio ', audioLevel);
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                audioLevel = getFromStorageAPI(self.STUDENT_AUDIO);
                audioLevel = (audioLevel === "") ? 1 : audioLevel;  
                break;
        }
        return audioLevel;
    }

	this.setCaptions = function (arg){
        self.dataObj.captions = arg;
        self.setData();
    }
    
    this.getCaptions = function (){
        return self.dataObj.captions;
    }
    
    this.setRoles = function (roles){
        self.dataObj.roles = roles;
        self.setData();
    }
    
    this.getRoles = function (){
        return self.dataObj.roles;
    }
    
    this.setLocation = function (location){
        if (!courseConfig.storage.bookmark){
            location = 0;
        }
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                //scormController.instance.setLocation(location);
                setToStorageAPI(self.LESSON_LOCATION, String(location));
                break;
        }
        
        // set bookmark as index in current module
        self.dataObj.bookmark[navigationController.moduleIndex] = navigationController.pageIndex;
        self.setData();
    }
    
    this.getLocation = function (){
        var location = '';
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE: 
//                location = scormController.instance.location();
                location = getFromStorageAPI(self.LESSON_LOCATION);
                location = (location === "") ? 0 : location;   
                break;
        }
        return location;
    }
    
    this.setSlideData = function(_pageIndex, _slideIndex, data){
        self.dataObj.slideData[_pageIndex][_slideIndex] = data;
        //console.log('setSlideData', data, self.dataObj.slideData)
        self.setData();
    }
    
    this.getSlideData = function(_pageIndex, _slideIndex){
        return self.dataObj.slideData[_pageIndex][_slideIndex];
    }
    
    this.setSlideVisited = function(_pageIndex, _slideIndex){
        // if slide not complete (2) set to visited (1)
        if (self.dataObj.slideProgress[_pageIndex][_slideIndex] != 2){
            self.dataObj.slideProgress[_pageIndex][_slideIndex] = 1;
            self.setData();
        }
    }
    
    this.isSlideVisited = function(_pageIndex, _slideIndex){
        // is slide visited (1) or complete (2)
        return (self.dataObj.slideProgress[_pageIndex][_slideIndex] > 0);
    }
    
    this.setSlideComplete = function(_pageIndex, _slideIndex){
        //console.log('StorageController:setSlideComplete', _pageIndex, _slideIndex);
        self.dataObj.slideProgress[_pageIndex][_slideIndex] = 2;

        // update submenus
        courseData.updateSubmenus();

        self.setData();
    }
    
    this.isSlideComplete = function(_pageIndex, _slideIndex){
        return (self.dataObj.slideProgress[_pageIndex][_slideIndex] == 2);
    }

    this.setSubmenuPageComplete = function(_pageIndex){

        for (var i = 0; i < navigationController.pageArray[_pageIndex].slides.length; i++){
            // user should have visited the slide containing the submenu
            if (self.dataObj.slideProgress[_pageIndex][i] == 1 && navigationController.isValidRole(navigationController.pageArray[_pageIndex].slides[i].roles)){
                self.setSlideComplete(_pageIndex, i);
            }
        }
    }

    this.initModuleMenu = function(_moduleIndex, inMenu){
        if (self.dataObj.menu.active[_moduleIndex] == undefined){
            self.dataObj.menu.active[_moduleIndex] = utils.booleanToInt(inMenu);
            self.setData();
        }
    }

    this.setModuleActive = function(_moduleIndex, isActive){
        self.dataObj.menu.active[_moduleIndex] = utils.booleanToInt(isActive);
        self.setData();
    } 

    this.isModuleActive = function(_moduleIndex){
        return (self.dataObj.menu.active[_moduleIndex] == 1);
    }

    this.initModuleMandatory = function(_moduleIndex, isMandatory){
        if (self.dataObj.menu.mandatory[_moduleIndex] == undefined){
            self.dataObj.menu.mandatory[_moduleIndex] = utils.booleanToInt(isMandatory);
            self.setData();
        }
    }

    this.setModuleMandatory = function(_moduleIndex, isMandatory){
        self.dataObj.menu.mandatory[_moduleIndex] = utils.booleanToInt(isMandatory);
        self.setData();
    } 

    this.isModuleMandatory = function(_moduleIndex){
        return (self.dataObj.menu.mandatory[_moduleIndex] == 1);
    }    

    this.initModuleLocked = function(_moduleIndex, isLocked){
        if (self.dataObj.menu.locked[_moduleIndex] == undefined){
            self.dataObj.menu.locked[_moduleIndex] = utils.booleanToInt(isLocked);
            self.setData();
        }
    }
    this.setModuleLocked = function(_moduleIndex, isLocked){
        self.dataObj.menu.locked[_moduleIndex] = utils.booleanToInt(isLocked);
        self.setData();
    } 

    this.isModuleLocked = function(_moduleIndex){
        return (self.dataObj.menu.locked[_moduleIndex] == 1);
    }    
    
    this.getMenuData = function(){
        return self.dataObj.menu ;
    }
        
    this.isPageComplete = function(_pageIndex){
        var complete = true;
        
        for (var i = 0; i < navigationController.pageArray[_pageIndex].slides.length; i++){
            
            if (self.dataObj.slideProgress[_pageIndex][i] != 2 && navigationController.isValidRole(navigationController.pageArray[_pageIndex].slides[i].roles)){
                complete = false;
                break;
            }
        }
        return complete;
    }
    
    this.isModuleComplete = function(_moduleIndex){
        var complete = true;
        var pageHasSetModuleComplete = false;
        // check if there are any pages that set the module complete on their completion
        for (var i = 0; i < navigationController.pageArray.length; i++){
            if (navigationController.isValidRole(navigationController.pageArray[i].roles)){
                if (navigationController.pageArray[i].module == _moduleIndex){
                    if (navigationController.pageArray[i].completeModuleOnCompletion && self.isPageComplete(i)){
                        pageHasSetModuleComplete = true;
                        break;
                    }
                }
            }
        }
        if (!pageHasSetModuleComplete){
            for (var i = 0; i < navigationController.pageArray.length; i++){
                if (navigationController.pageArray[i].module == _moduleIndex){
                    if (!multiLanguage){
                        //console.log('isModuleComplete', _moduleIndex, i)
                        if (courseData.getPageData(_moduleIndex, navigationController.pageArray[i].page).multiLanguageOnly == "true"){
                            //skip any pages hidden for multiLanguageOnly e.g.language selection page
                            continue;
                        }
                    }
                    // check if page is mandatory - if not then skip
                    if (courseData.checkMandatory(navigationController.pageArray[i])){
                        // if page not complete then module not complete
                        if (!self.isPageComplete(i)){
                            complete = false;
                            break;
                        } 
                    }
                }
            }
        }
        return complete;
    }
    
    this.checkCourseComplete = function(){
        // check all pages complete
        var pagesComplete = false;
        if (courseConfig.storage.courseCompletion == 0 || courseConfig.storage.courseCompletion == 2){
            pagesComplete = true;
            for (var i = 0; i < self.dataObj.slideProgress.length; i++){
                if (navigationController.isValidRole(navigationController.pageArray[i].roles)){
                    if (!multiLanguage){
                        //console.log('isModuleComplete', _moduleIndex, i)
                        if (courseData.getPageData(navigationController.pageArray[i].module, navigationController.pageArray[i].page).multiLanguageOnly == "true"){
                            //skip any pages hidden for multiLanguageOnly e.h.language selection page
                            continue;
                        }
                    }
                    if (!self.isPageComplete(i)){
                        pagesComplete = false;
                        break;
                    }
                }
            }
        }
        
        var scoreComplete = false;
        if (courseConfig.storage.courseCompletion == 1){
            // completion based on score
            if (assessmentConfig.scormScoreAssessmentID != ''){
                
                if (assessmentConfig.assessments[assessmentConfig.scormScoreAssessmentID].enable){
                    scoreComplete = (self.dataObj.assessment[assessmentConfig.scormScoreAssessmentID].perc >= assessmentConfig.assessments[assessmentConfig.scormScoreAssessmentID].passingPerc);
                }
            } else {
                scoreComplete = (self.score == 100);
            }
        }        
          
        var complete = false;
        switch (courseConfig.storage.courseCompletion){
            case 0:
                complete = pagesComplete;
                break;
            case 1:
                complete = scoreComplete;
                break;
            case 2:
                complete = pagesComplete && scoreComplete;
                break;
            case 3:
                // use last page to check course complete
                var completionSlide = {page: 0, slide: 0}
                // check if there are any pages that set the course complete on their completion
                for (var i = 0; i < navigationController.pageArray.length; i++){
                    for (var j = 0; j < navigationController.pageArray[i].slides.length; j++){
                        if (navigationController.isValidRole(navigationController.pageArray[i].slides[j].roles)){
                            completionSlide = {page: i, slide: j}
                            if (navigationController.pageArray[i].slides[j].navData.setsCourseComplete == 'true'){
                                break;
                            }
                        }
                    }
                }
                complete = (self.isSlideComplete(completionSlide.page, completionSlide.slide));
                self.isLastSlideComplete = complete;
                break;        
        }
        
        //console.log('StorageController::checkCourseComplete', courseConfig.storage.courseCompletion, pagesComplete, scoreComplete, complete)
        if (complete){
            this.setComplete();
        } 
    }
    
    this.isCourseComplete = function(){
        var complete = true;
        for (var i = 0; i < navigationController.pageArray.length; i++){
            if (navigationController.isValidRole(navigationController.pageArray[i].roles)){
                if (!multiLanguage){
                    //console.log('isModuleComplete', _moduleIndex, i)
                    if (courseData.getPageData(navigationController.pageArray[i].module, navigationController.pageArray[i].page).multiLanguageOnly == "true"){
                        //skip any pages hidden for multiLanguageOnly e.h.language selection page
                        continue;
                    }
                }
                if (!self.isPageComplete(i)){
                    complete = false;
                    break;
                }
            }
        }
        return complete;
    }
    
    this.setAssessmentData = function(){
        // get assessment data
        for (var x in assessmentConfig.assessments){
            //self.dataObj.assessment[x].score = window[x+'Assessment'].score;
            //self.dataObj.assessment[x].perc = window[x+'Assessment'].perc;
            //self.dataObj.assessment[x].answers = window[x+'Assessment'].answerArray;
            //self.dataObj.assessment[x].selected = window[x+'Assessment'].selectedArray;
            //self.dataObj.assessment[x].correct = window[x+'Assessment'].correctArray;
                //.push(window[x+'Assessment'].correctArray);
            if (assessmentConfig.assessments[x].enable){
                self.dataObj.assessment[x] = window[x+'Assessment'].getData();
            }
        }
        
        self.setData();
    }
    
    this.setData = function (){

        self.dataObj.version = LaunchConfig.course.version;        
        self.dataObj.history = navigationController ? navigationController.historyArray : self.dataObj.history;
        self.dataObj.custom = customDataController.data;
        self.dataObj.system = getSystemInfoObject();
        
        var data = JSON.stringify(self.dataObj);
        
        //console.log('StorageController', data, data.length);
        
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                setToStorageAPI(self.SUSPEND_DATA, data);
                break;
        }
    }
    
    this.getData = function (){
		// check we haven't already got data
        if (self.dataObj.slideProgress.length == 0){
        var data = null;
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                data = getFromStorageAPI(self.SUSPEND_DATA);
                break;
        }
        
        //console.log('StorageController::getData', data);
        
        if (data != null && data != ''){
            var jsonData = JSON.parse(data);
            // suspend data is JSON
            self.dataObj = jsonData;
            
            for (var x in assessmentConfig.assessments){
                //window[x+'Assessment'].answerArray = self.dataObj.assessment[x].answers;
                //window[x+'Assessment'].selectedArray = self.dataObj.assessment[x].selected;
                //window[x+'Assessment'].correctArray = self.dataObj.assessment[x].correct;
                
                if (assessmentConfig.assessments[x].enable){
                    window[x+'Assessment'].updateFromLMSData(self.dataObj.assessment[x]);
                }
            }
        } else {
            for (var i = 0; i < courseData.getNumModules(); i++ ){
                self.dataObj.bookmark[i] = 0;
            }

            for (var i = 0; i < navigationController.pageArray.length; i++){
                self.dataObj.slideProgress[i] = [];
                self.dataObj.slideData[i] = [];
                for (var j = 0; j < navigationController.pageArray[i].slides.length; j++){
                    //console.log('SLIDE complete', navigationController.pageArray[i].slides[j].complete)
                    //var slideData = courseData.getSlideData(navigationController.pageArray[i].module, navigationController.pageArray[i].page, j);
//                    if (slideData.complete){
//                        self.setSlideComplete(i,j,k);
//                    }
                    
                    var slideNavXMLData = navigationController.pageArray[i].slides[j].navData; 
                    // set complete if nav XML has complete attribute
                    self.dataObj.slideProgress[i][j] = (slideNavXMLData.complete == 'true') ? 2 : 0;
                    self.dataObj.slideData[i][j] = 0;
                }
            }
            
            for (var x in assessmentConfig.assessments){
                if (assessmentConfig.assessments[x].enable){
                    self.dataObj.assessment[x] = {};
                }
            }
        }   
		}     
    }
    
    this.getCustomData = function(){
        return self.dataObj.custom;
    }
    
    this.setCustomData = function(arg){
        self.dataObj.custom = arg;
        self.setData();
    }
    
    this.getCompletionDate = function(){
        return self.dataObj.completion;
    }
    
    this.isPassed = function(){
        return (self.dataObj.completion != '');
    }
    
    this.getHistory = function(){
        return self.dataObj.history;
    }
    
    this.setHistory = function(arg){
        self.dataObj.history = arg;
        self.setData();
    }
    
    this.getScore = function(){
        var score = null;
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                score = getFromStorageAPI(self.SCORE);
                break;
        }
        return score;
    }
    
    this.setScore = function(score, min, max){
        min = min || 0;
        max = max || 100;
        self.score = score;
        self.setData();
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                setToStorageAPI(self.SCORE_MIN, min);
                setToStorageAPI(self.SCORE_MAX, max);
                setToStorageAPI(self.SCORE, score);
                break;
        }
        switch (courseConfig.storage.complianceType){
            case SCORM_2004:
            case LOCALSTORAGE:
                // set scaled score as raw sometimes doesn't work
                setToStorageAPI(self.SCORE_SCALED, (score/max).toFixed(2));
                break;
        }
    }

    this.getProgressData = function(){
        var progressObj = {num: 0, complete: 0, perc: 0};
        for (var i = 0; i < self.dataObj.slideProgress.length; i++){
            if (navigationController.isValidRole(navigationController.pageArray[i].roles)){
                if (!multiLanguage){
                    //console.log('isModuleComplete', _moduleIndex, i)
                    if (courseData.getPageData(navigationController.pageArray[i].module, navigationController.pageArray[i].page).multiLanguageOnly == "true"){
                        //skip any pages hidden for multiLanguageOnly e.h.language selection page
                        continue;
                    }
                }
                progressObj.num ++;
                if (self.isPageComplete(i)){
                    progressObj.complete ++;
                }
            }
        }
        progressObj.perc = progressObj.complete/progressObj.num;
        return progressObj;
    }

    this.setProgressMeasure = function(perc){
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
                setToStorageAPI(self.PROGRESS_MEASURE, (perc/100).toFixed(2));
                break;
        }
    }
    
    this.getComplete = function(){
        var completeStatus;
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                completeStatus = getFromStorageAPI(self.LESSON_STATUS);
                break;
        }
        return completeStatus;
    }
    
    this.setComplete = function(){
        addTrackDataTrace('StorageController::setComplete called');
        if (this.dataObj.completion == ''){
            this.dataObj.completion = utils.getTodayDate();
        
            if (courseConfig.storage.courseCompletion == 3 && self.isLastSlideComplete){
                // if completion set on last page, set score to 100 too
                var min = courseConfig.storage.scoring.min ? courseConfig.storage.scoring.min : 0;
                min = typeof(min) == 'string' ? utils.evaluateVariable(min) : min;
                var max = courseConfig.storage.scoring.max ? courseConfig.storage.scoring.max : 100;
                max = typeof(max) == 'string' ? utils.evaluateVariable(max) : max;
                var score = courseConfig.storage.scoring.score ? courseConfig.storage.scoring.score : 100;
                score = typeof(score) == 'string' ? utils.evaluateVariable(score) : score;
                console.log(score, min, max);
                this.setScore(score, min, max);
            }
            switch (courseConfig.storage.complianceType){
                case SCORM_1_2: 
                case SCORM_2004:
                case LOCALSTORAGE:
                    self.setSuccess('passed');
                    setToStorageAPI(self.LESSON_STATUS, 'completed');
                    // Let's force the save/commit to the LMS
                    self.saveDataToStorageAPI();
                    break;
            }
        }
    }
    
    this.getSuccess = function(){
        var successStatus;
        switch (courseConfig.storage.complianceType){
            case SCORM_2004:
            case LOCALSTORAGE:
                successStatus = getFromStorageAPI(self.SUCCESS_STATUS);
                break;
        }
        return successStatus;
    }
    
    this.setSuccess = function(success){
        switch (courseConfig.storage.complianceType){
            case SCORM_2004:
            case LOCALSTORAGE:
                setToStorageAPI(self.SUCCESS_STATUS, success);
                break;
        }
    }
    
    this.saveData = function(){
        //console.log('StorageController::saveData ', lmsDataToSave);
        // only save if there is new data to save
        
        // utils.testConnection();
        self.dataObj.position = {loc: navigationController.currentPage, m: navigationController.moduleIndex, p: navigationController.pageIndex, s:navigationController.currentSlide};
        // addTrackDataTrace('StorageController::saveData current location ('+navigationController.moduleIndex+', '+navigationController.pageIndex+', '+navigationController.currentSlide+')');

        // lets save the suspend data object
        self.setData();            
        
        self.saveDataToStorage();
    }

    this.saveDataToStorage = function(){
        if (lmsDataToSave){
            if (!inExitProcess){
                if (saveTimeout == null){
                    saveTimeout = setTimeout(function(){
                        clearTimeout(saveTimeout);
                        saveTimeout = null;

                        self.saveDataToStorageAPI();
                    }, 2000);
                } 
            } else {
                self.saveDataToStorageAPI();
            }
        }
    }

    this.saveDataToStorageAPI = function(){
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                if (checkConnectionToLMS()){
                    var success = self.storageAPI.save();
                    if (success){
                        // prevent any unecessary saves
                        lmsDataToSave = false;
                    }
                }
                break;
        }
    }
    
    this.getStudentID = function(){
         var studentID= 'Student ID';
         switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                studentID = getFromStorageAPI(self.STUDENT_ID);
                break;
        }
        
        return studentID;
    }
    
    this.setStudentName = function(name){
        self.studentName = name;
    }
    
    this.getStudentName = function(){
         var studentName = '[Your_Name]';
         switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
            case LOCALSTORAGE:
                studentName = getFromStorageAPI(self.STUDENT_NAME) || studentName;
                break;
        }
        // turn comma spearated studentName into correct order e.g. 'Name, Student' into 'Student Name'
        if (studentName.indexOf(",") != -1){
            var student_name_arr = studentName.split(",");
        	// remove any spaces and then split based on the comma
            studentName = student_name_arr[1].trim() +' '+ student_name_arr[0].trim();
        }
        
        return studentName;
    }

    this.getStudentFirstName = function(){
        var studentName = this.getStudentName();
        if (studentName.indexOf(' ') != -1){
            return studentName.substring(0, studentName.indexOf(' '));
        } else { 
            return studentName;
        }
    }
    
    /**
     * @function setInteraction
     * @description This function is used to send user interaction to LMS API.
     * @param questionId {String} Question id - Unique label for the interaction
     * @param qType {String} Question type - true-false, choice, fill-in, long-fill-in, matching, performance, sequencing, likert, numeric or other, RW
     * @param qText {String} Question text
     * @param learnerResponse {String} learner response - format depends on interaction type, RW. Data generated when a learner responds to an interaction
     * @param correctPattern {String} correct answer in '1,0' formate
     * @param isCorrect correct, incorrect, unanticipated, neutral
 */	
    // this.setInteraction = function(questionId, qType, qText, learnerResponse, correctPattern, isCorrect){
    this.setInteraction = function(interactionObj){
        //console.log('StorageController::setInteraction', questionId, qType, qText, learnerResponse, correctPattern, isCorrect);
        var correctStr = '';
        var questionId = interactionObj.id.split(' ').join('');
        if (lmsConnected && courseConfig.storage.cmi.interactions.enabled){
            var interactionCount = self.dataObj.cmi.i[questionId];
            // check if there is an interaction to update for this questionId
            if (interactionCount == undefined || courseConfig.storage.cmi.interactions.unique){
                interactionCount = getFromStorageAPI("cmi.interactions._count");
                // store latest id in suspend data so we can retrieve later
                self.dataObj.cmi.i[questionId] = parseInt(interactionCount);
            }

            if (typeof(interactionObj.correct) == 'string'){
                // e.g. 'neutral' string received
                correctStr = interactionObj.correct;
            } else {
                if (interactionObj.correct){
                    correctStr = 'correct';
                } else {
                    if (courseConfig.storage.complianceType == SCORM_1_2){
                        correctStr = 'wrong';
                    } else {
                        correctStr = 'incorrect';
                    }
                }
            }
            var qText = interactionObj.question;
            if (qText.length>=255){
                qText = qText.slice(0,250);
                qText += "...";
            }
        
            // data need to be in the correct formats
            var correctPatterns = self.getCorrectInteractionPatterns(interactionObj.type, interactionObj.answers);
            var learnerResponse = self.adjustInteractionPattern(interactionObj.type, interactionObj.response);

            switch (courseConfig.storage.complianceType){
                case SCORM_1_2: 
                case SCORM_2004:
                case LOCALSTORAGE:
                    setToStorageAPI('cmi.interactions.'+interactionCount+'.id', 'Question-'+questionId);
                    setToStorageAPI('cmi.interactions.'+interactionCount+'.type', interactionObj.type);
                    if (courseConfig.storage.complianceType == SCORM_1_2){
                        setToStorageAPI('cmi.interactions.'+interactionCount+'.objectives.0.id', qText);
                    } else if (courseConfig.storage.complianceType == SCORM_2004){
                        setToStorageAPI('cmi.interactions.'+interactionCount+'.description', qText);
                    }
                    // store timestamp
                    setToStorageAPI('cmi.interactions.'+interactionCount+'.timestamp', ConvertDateToIso8601TimeStamp(interactionObj.timestamp));
                    setToStorageAPI('cmi.interactions.'+interactionCount+'.latency', getScormTime(utils.getCurrentTime()-interactionObj.timestamp));
                    
                    for (var i = 0; i < correctPatterns.length; i++){
                        setToStorageAPI('cmi.interactions.'+interactionCount+'.correct_responses.'+i+'.pattern', correctPatterns[i]);
                    }
                    if (courseConfig.storage.complianceType == SCORM_1_2){
                        setToStorageAPI('cmi.interactions.'+interactionCount+'.student_response', learnerResponse);
                    } else if (courseConfig.storage.complianceType == SCORM_2004){
                        setToStorageAPI('cmi.interactions.'+interactionCount+'.learner_response', learnerResponse);
                    }
                    setToStorageAPI('cmi.interactions.'+interactionCount+'.result', correctStr);
                    break;
            }
        }
    }

    this.getCorrectInteractionPatterns = function(type, value){
        var patterns = [];
        var combinations = [];
        if (Array.isArray(value[0])){
            // if 2d array - there are multiple patterns
            combinations = utils.get2dCombinations(value);
         } else {
            if (Array.isArray(value)){
                combinations.push(value.slice());
            } else {
                combinations.push(value);
            }
         }

        for (var i = 0; i < combinations.length; i++){
            patterns.push(self.adjustInteractionPattern(type, combinations[i]));
        }

        return patterns;
    }

    this.adjustInteractionPattern = function(qType, value){
        var returnStr = value.toString();
        if (courseConfig.storage.complianceType == SCORM_2004){
            switch (qType){
                case 'choice':
                    // 'choice' format needs to be choice1[,]choice1 - each choice must be unique
                    if (Array.isArray(value)){
                        var returnArray = [];
                        for (var i = 0; i < value.length; i++){
                            if (value[i] == 1){
                                returnArray.push(i);
                            }
                        }
                        returnStr = returnArray.join('[,]');
                    }
                    break;
                case 'matching':
                    // 'matching' format needs to be source[.]target[,]source[.]target[,]source[.]target
                    if (Array.isArray(value)){
                        var returnArray = [];
                        for (var i = 0; i < value.length; i++){
                            returnArray[i] = i+'[.]'+value[i];
                        }
                    }
                    returnStr = returnArray.join('[,]');
                    break;
            }
        }
        return returnStr;
    }

    this.getInteractionData = function(questionId, property){
        questionId = questionId.split(' ').join('');
        var returnStr = '';
        if (lmsConnected){
            var interactionCount = self.dataObj.cmi.i[questionId];
            returnStr = getFromStorageAPI('cmi.interactions.'+interactionCount+'.'+property);
        }
        return returnStr;
    }
	
    this.setObjective = function(objectiveId, status, description){
        if (lmsConnected  && courseConfig.storage.cmi.objectives.enabled){
            var objectiveCount = self.dataObj.cmi.o[id];
            var id = objectiveId.split(' ').join('');
            console.log('StorageController::setObjective', id, objectiveCount);
            var objectiveStatus = 'incomplete';
            if (objectiveCount == undefined){
                // || courseConfig.storage.cmi.objectives.unique){ // note objective id can't be the same
                // check if there is a objective to update for this questionId
                objectiveCount = getFromStorageAPI("cmi.objectives._count");
                // store latest id in suspend data so we can retrieve later
                self.dataObj.cmi.o[id] = parseInt(objectiveCount);
                
                setToStorageAPI('cmi.objectives.'+objectiveCount+'.id', id);
            } else {
                objectiveStatus = getFromStorageAPI('cmi.objectives.'+objectiveCount+'.completion_status');
            }
            console.log('StorageController::setObjective', id, objectiveCount, objectiveStatus);
            if (objectiveStatus != 'completed'){
                status = status.toLowerCase() == 'complete' ? 'completed' : status.toLowerCase();
                switch (courseConfig.storage.complianceType){
                    case SCORM_1_2: 
                        //cmi.objectives.n.status (passed, completed, failed, incomplete, browsed, not attempted, RW) Indicates whether the learner has completed or satisfied the objective
                        setToStorageAPI('cmi.objectives.'+objectiveCount+'.status', status);
                        break;
                    case SCORM_2004:
                    case LOCALSTORAGE:
                        //cmi.objectives.n.success_status (passed, failed, unknown, RW) Indicates whether the learner has mastered the objective
                        //setToStorageAPI('cmi.objectives.'+objectiveCount+'.success_status', status);                        
                        //cmi.objectives.n.completion_status (completed, incomplete, not attempted, unknown, RW) Indicates whether the learner has completed the associated objective
                        setToStorageAPI('cmi.objectives.'+objectiveCount+'.completion_status', status);
                        //cmi.objectives.n.description (localized_string_type (SPM: 250), RW) Provides a brief informative description of the objective
                        setToStorageAPI('cmi.objectives.'+objectiveCount+'.description', description);
                        break;
                }
            }
        }
    }

    function getFromStorageAPI(key){
        var value = '';
        switch (courseConfig.storage.complianceType){
            case SCORM_1_2: 
            case SCORM_2004:
                if (checkConnectionToLMS()){
                    value = self.storageAPI.get(key);
                    commitObj[key] = value;
                }
                break;
            case LOCALSTORAGE: 
                value = getFromLocalStorage(key);
                break;
        }
        return value;
    }

    function setToStorageAPI(key, value){
        var newDataToSet = false;
        // lets make sure we're only saving new data
        if (commitObj[key] != value){
            commitObj[key] = value;
            newDataToSet = true;
        } 
        
        if (newDataToSet){
            addTrackDataTrace('StorageController::setToStorageAPI : '+newDataToSet+' - '+key);
            // console.log('StorageController::setToStorageAPI',key, commitObj[key])
            if (courseConfig.storage.complianceType == LOCALSTORAGE){             
                setToLocalStorage(key, value);
            } else if (checkConnectionToLMS()){             

                if (!inExitProcess && key == self.SUSPEND_DATA){
                    // as suspend data updates regularly lets add a 10s delay in case any more data is adjusted - NOT when in exit process
                    if (suspendDataTimeout == null){
                        suspendDataTimeout = setTimeout(function(){
                            clearTimeout(suspendDataTimeout);
                            suspendDataTimeout = null;
                            setNewDataToStorageAPI(key);
                        }, 10000);
                    }
                } else {
                    setNewDataToStorageAPI(key);
                }                
            }
        }
    }

    function setNewDataToStorageAPI(key){
        //utils.testConnection();
        var result = self.storageAPI.set(key, commitObj[key]);
        //console.log('setToStorageAPI', key, value, result);
        notifyDataToSave();
    }

    function notifyDataToSave(){
        lmsDataToSave = true;
    }

    function getFromLocalStorage(key){
        
        var key = getCourseKey()+key.split('.').join('_');
        var value = window.localStorage.getItem(key);
        // console.log('localStorage', 'GET', key, key.split('.').join('_'), value);
        value = value == null ? '' : value;
        return value;
    }

    function setToLocalStorage(key, value){
        // console.log('localStorage', 'SET', key, key.split('.').join('_'), value);
        window.localStorage.setItem(getCourseKey()+key.split('.').join('_'), value);
    }

    function getCourseKey(){
        var courseKey = '';
        if (LaunchConfig.course){
            courseKey = (LaunchConfig.course.uid || LaunchConfig.course.code);
        } else {
            courseKey = courseConfig.uid || courseConfig.code;
        }
        return courseKey;
    }
        
    function checkConnectionToLMS(){
        if (lmsConnected){
            if (self.storageAPI.connection.isActive){
                return true;
            } else {
                // show alert - LMS connection lost
                $.Alert({
                    title: utils.lmsConnectionError.title,
                    message: utils.lmsConnectionError.message,
                    confirm: utils.lmsConnectionError.confirm,
                    onConfirm:function(){
                        //console.log('exit');
                        navigationController.exitCourse();
                    }	
                });
                return false;
            }
        } else {
            return false;
        }
    }
    
    function unloadHandler(e){
        addTrackDataTrace('StorageController::unloadHandler called', unloaded);
        if (!unloaded){
            // mailto and tel links seem to fire the unloadhandler events - let's block them
            // https://stackoverflow.com/questions/9740510/mailto-link-in-chrome-is-triggering-window-onbeforeunload-can-i-prevent-this
            var activeElement = $(document.activeElement);
            // console.log('activeElement', activeElement)
            // added data 'link-mailto' to ActionUtility
            var isMailto = activeElement && activeElement.is('[href^="mailto:"]') || activeElement && activeElement.data('link-mailto');
            var isTel = activeElement && activeElement.is('[href^="tel:"]');

            if (isMailto || isTel){
                console.warn('StorageController::unloadHandler caught link unload ['+(isMailto ? 'mailto' : 'tel')+']');
            }
            if (!isMailto && !isTel){
                inExitProcess = true;
                // add exit to exit array - only keep 5
                self.dataObj.exit.push({
                    t: new Date().toISOString(),
                    l: navigationController ? navigationController.pageIndex : 0, 
                    e: userInitiatedWindowClose ? 'course' : 'browser/other'
                });
                // only keep 5
                if (self.dataObj.exit.length > 5){
                    self.dataObj.exit.shift();
                }

                switch (courseConfig.storage.complianceType){
                    case SCORM_1_2: 
                    case SCORM_2004:
                        if (checkConnectionToLMS()){

                            self.saveData();

                            // save session time
                            var timeSpent = getSessionTime();
			                setToStorageAPI(self.SESSION_TIME, timeSpent);
                            
                            self.storageAPI.save(); //save all data that has already been sent
                            self.storageAPI.quit(); //close the SCORM API connection properly
                            unloaded = true;
                        }
                        break;
                    case LOCALSTORAGE:
                        break;
                }
            } else {
                addTrackDataTrace('StorageController::unloadHandler blocked unloadHandler due to a[mailto] or a[tel]');
            }
        }
    }

    /**
 * @function getSessionTime
 * @description This function returns the session time.
 * @returns {String} Time in string format
 */	
	function getSessionTime() {
		var dtm = new Date();		
		var n = dtm.getTime() - initalTime.getTime();
		return getScormTime(n);
    }
    
    /**
 * @function getScormTime
 * @description This function returns the current time.
 * @returns {String} Time in string format
 */	
	function getScormTime(time) {
		if (courseConfig.storage.complianceType == SCORM_2004){
			return ConvertMilliSecondsIntoSCORM2004Time(time);
		}else{
			return MillisecondsToCMIDuration(time);
		}
	}

    

/**
 * @function MillisecondsToCMIDuration
 * @description This function is used to convert duration from milliseconds to 0000:00:00.00 format.
 * @returns {String} Time string in 0000:00:00.00 format
 */	
	function MillisecondsToCMIDuration(LessonDuration) {
		var hr ="000" + Math.floor(LessonDuration/(60*60*1000));
		var temp = (LessonDuration-(hr*60*60*1000));
		var min = "0" + Math.floor(temp/(60*1000));
		var sec = temp-(min*60*1000);
		temp = temp - (sec*1000)
		var ms = "0" + Math.floor(temp/1000);
		sec = "0" + Math.floor(sec/1000);
		var LessonDurationFormat = hr.substr(hr.length-4)+":"+min.substr(min.length-2)+":"+sec.substr(sec.length-2)+"."+ms.substr(ms.length-2);
		return LessonDurationFormat
	}
/**
 * @function ConvertMilliSecondsIntoSCORM2004Time
 * @description This function is used to convert duration from milliseconds to SCORM 2004 time format.
 * @returns {String} Time in SCORM 2004 time format
 */	
	function ConvertMilliSecondsIntoSCORM2004Time(intTotalMilliseconds){	
		var ScormTime = "";
		var HundredthsOfASecond;
		var Seconds;
		var Minutes;
		var Hours;
		var Days;
		var Months;
		var Years;	
		var HUNDREDTHS_PER_SECOND = 100;
		var HUNDREDTHS_PER_MINUTE = HUNDREDTHS_PER_SECOND * 60;
		var HUNDREDTHS_PER_HOUR   = HUNDREDTHS_PER_MINUTE * 60;
		var HUNDREDTHS_PER_DAY    = HUNDREDTHS_PER_HOUR * 24;
		var HUNDREDTHS_PER_MONTH  = HUNDREDTHS_PER_DAY * (((365 * 4) + 1) / 48);
		var HUNDREDTHS_PER_YEAR   = HUNDREDTHS_PER_MONTH * 12;    	
		HundredthsOfASecond = Math.floor(intTotalMilliseconds / 10);    	
		Years = Math.floor(HundredthsOfASecond / HUNDREDTHS_PER_YEAR);
		HundredthsOfASecond -= (Years * HUNDREDTHS_PER_YEAR);    	
		Months = Math.floor(HundredthsOfASecond / HUNDREDTHS_PER_MONTH);
		HundredthsOfASecond -= (Months * HUNDREDTHS_PER_MONTH);    	
		Days = Math.floor(HundredthsOfASecond / HUNDREDTHS_PER_DAY);
		HundredthsOfASecond -= (Days * HUNDREDTHS_PER_DAY);
		Hours = Math.floor(HundredthsOfASecond / HUNDREDTHS_PER_HOUR);
		HundredthsOfASecond -= (Hours * HUNDREDTHS_PER_HOUR);
		Minutes = Math.floor(HundredthsOfASecond / HUNDREDTHS_PER_MINUTE);
		HundredthsOfASecond -= (Minutes * HUNDREDTHS_PER_MINUTE);
		Seconds = Math.floor(HundredthsOfASecond / HUNDREDTHS_PER_SECOND);
		HundredthsOfASecond -= (Seconds * HUNDREDTHS_PER_SECOND);
		if (Years > 0) {
			ScormTime += Years + "Y";
		}
		if (Months > 0){
			ScormTime += Months + "M";
		}
		if (Days > 0){
			ScormTime += Days + "D";
		}
		if ((HundredthsOfASecond + Seconds + Minutes + Hours) > 0 ){
			ScormTime += "T";
			if (Hours > 0){
				ScormTime += Hours + "H";
			}    		
			if (Minutes > 0){
				ScormTime += Minutes + "M";
			}    		
			if ((HundredthsOfASecond + Seconds) > 0){
				ScormTime += Seconds;
				
				if (HundredthsOfASecond > 0){
					ScormTime += "." + HundredthsOfASecond;
				}    			
				ScormTime += "S";
			}    		
		}    	
		if (ScormTime == ""){
			ScormTime = "0S";
		}    	
		ScormTime = "P" + ScormTime;
		return ScormTime;
    }
    
    function ConvertDateToIso8601TimeStamp(dtm){
	
        var strTimeStamp;
        
        dtm = new Date(dtm);
        
        var Year   = dtm.getFullYear();
        var Month  = dtm.getMonth() + 1;
        var Day    = dtm.getDate();
        var Hour   = dtm.getHours();
        var Minute = dtm.getMinutes();
        var Second = dtm.getSeconds();
        
        Month  = ZeroPad(Month, 2);
        Day    = ZeroPad(Day, 2);
        Hour   = ZeroPad(Hour, 2);
        Minute = ZeroPad(Minute, 2);
        Second = ZeroPad(Second, 2);
        
        strTimeStamp = Year + "-" + Month + "-" + Day + "T" + Hour + ":" + Minute + ":" + Second;
        
        return strTimeStamp;
        
    }

    function ZeroPad(intNum, intNumDigits){
	
        var strTemp;
        var intLen;
        var i;
        
        strTemp = new String(intNum);
        intLen = strTemp.length;
        
        if (intLen > intNumDigits){
            strTemp = strTemp.substr(0,intNumDigits);
        }
        else{
            for (i=intLen; i<intNumDigits; i++){
                strTemp = "0" + strTemp;
            }
        }
        
        return strTemp;
    }
    
    init();
}
