function NavigationButton(){
    var self = this;

	this.TRANSCRIPT_ON_CLASS="transcriptOn";
	this.TRANSCRIPT_OFF_CLASS="transcriptOff";
	this.AUDIO_ON_CLASS ="audioOn";
	this.AUDIO_OFF_CLASS ="audioOff";
	
    this.DISABLED ="disabled";
	this.SELECTED ="selected";
    
	this.DEBUG_NEXT_BUTTON =".debug_btn_Next";
	this.DEBUG_BACK_BUTTON =".debug_btn_Back";
	
	this.LOGO_BUTTON =".navbar-brand";
	this.HOME_BUTTON =".btn_Home";
	this.MENU_BUTTON =".btn_Menu";
	this.REPLAY_BUTTON =".btn_Replay";
	this.NEXT_BUTTON =".btn_Next";
	this.BACK_BUTTON =".btn_Back";
	this.GLOSSARY_BUTTON =".btn_Glossary";
	this.RESOURCE_BUTTON =".btn_Resource";
	this.HELP_BUTTON =".btn_Help";
	this.EXIT_BUTTON =".btn_Exit";
	this.LANGUAGE_BUTTON =".btn_Language";
	this.SEARCH_BUTTON =".btn_Search";
	this.SEARCHGO_BUTTON =".btn_SearchGo";
	this.AUDIO_BUTTON =".btn_Audio";
    
	this.SEARCH_INPUT =".search-input";
    
	this.CONTACT_BUTTON =".btn_Contact";
	this.DOWNLOAD_COC_BUTTON =".btn_coc_download";
    
    this.updateNavButtons = function(){
        $(navigationButton.HOME_BUTTON+' .btn_text').html(courseData.getBtnText('Home'));
        $(navigationButton.MENU_BUTTON+' .btn_text').html(courseData.getBtnText('Menu'));
        $(navigationButton.REPLAY_BUTTON+' .btn_text').html(courseData.getBtnText('Replay'));
        $(navigationButton.NEXT_BUTTON+' .btn_text').html(courseData.getBtnText('Next'));
        $(navigationButton.BACK_BUTTON+' .btn_text').html(courseData.getBtnText('Back'));
        $(navigationButton.GLOSSARY_BUTTON+' .btn_text').html(courseData.getBtnText('Glossary'));
        $(navigationButton.RESOURCE_BUTTON+' .btn_text').html(courseData.getBtnText('Resource'));
        $(navigationButton.HELP_BUTTON+' .btn_text').html(courseData.getBtnText('Help'));
        $(navigationButton.EXIT_BUTTON+' .btn_text').html(courseData.getBtnText('Exit'));
        
        $(navigationButton.SEARCH_BUTTON+' .btn_text').html(courseData.getBtnText('Search'));
        $(navigationButton.SEARCH_INPUT).attr("placeholder", courseData.getBtnText('Search'));
        $(navigationButton.SEARCHGO_BUTTON+' .btn_text').html(courseData.getBtnText('SearchGo'));
        
        $(navigationButton.LANGUAGE_BUTTON+' .btn_text').html(courseData.getBtnText('Language'));
        
        $(navigationButton.AUDIO_BUTTON+' .btn_text').html(courseData.getBtnText('AudioOn'));
        
        /* if (!navigationConfig.buttons.home){
            $(navigationButton.HOME_BUTTON).hide();
        }
        if (!navigationConfig.buttons.menu){
            $(navigationButton.MENU_BUTTON).hide();
        }
        
        if (!navigationConfig.buttons.back){
            $(navigationButton.BACK_BUTTON).hide();
        }
        if (!navigationConfig.buttons.replay){
            $(navigationButton.REPLAY_BUTTON).hide();
        }
        if (!navigationConfig.buttons.next){
            $(navigationButton.NEXT_BUTTON).hide();
        } */
        self.hideNavButton('home');
        self.hideNavButton('menu');
        self.hideNavButton('back');
        self.hideNavButton('replay');
        self.hideNavButton('next');

        if (!componentConfig.glossary){
            $(navigationButton.GLOSSARY_BUTTON).hide();
        } else {
            self.hideNavButton('glossary');
        }
        if (!componentConfig.help){
            $(navigationButton.HELP_BUTTON).hide();
        } else {
            self.hideNavButton('help');
        }
        if (!componentConfig.resource){
            $(navigationButton.RESOURCE_BUTTON).hide();
        } else {
            self.hideNavButton('resource');
        }
        if (!componentConfig.exit){
            $(navigationButton.EXIT_BUTTON).hide();
        } else {
            self.hideNavButton('exit');
        }
        if (!componentConfig.progress){
            $('#userProgress').hide();
        }
        if (!componentConfig.search){
            $('.search-dropdown').hide();
        }
        if (!courseConfig.audio){
            $(navigationButton.AUDIO_BUTTON).hide();
        } else {        
            self.hideNavButton('audio');
        }
       
    }

    this.hideNavButton = function(btnName){
        if (navigationConfig.nav.top){
            // top nav is active
            if (navigationConfig.nav.top.buttons[btnName]){
                // button is active - lets add a class to the body to say this nav feature is active
                $('body').addClass('nav-top-active');
            } else {
                // console.log('NavigationButton::hide top nav button: '+btnName);
                $('.navbar '+navigationButton[btnName.toUpperCase()+'_BUTTON']).hide();
            }
        }
        if (navigationConfig.nav.bottom){
            // bottom nav is active
            if (navigationConfig.nav.bottom.buttons[btnName]){
                // button is active - lets add a class to the body to say this nav feature is active
                $('body').addClass('nav-bottom-active');
            } else {
                // console.log('NavigationButton::hide bottom nav button: '+btnName);
                $('.bottomNavigation '+navigationButton[btnName.toUpperCase()+'_BUTTON']).hide();
            }
        }
    }
}