var NavigationEvents = function(opts) {
    var self = this;

    this.init = init;
    
    var keys = [];
    var islogPop = false;
    
    // init();
    
    function init(){
        $(navigationButton.DEBUG_NEXT_BUTTON).on("click", onDebugNextClick);	
        $(navigationButton.DEBUG_BACK_BUTTON).on("click", onDebugBackClick);	
        
        $(navigationButton.NEXT_BUTTON).on("click", onNextClick);	
        $(navigationButton.REPLAY_BUTTON).on("click", onReplayClick);	
        $(navigationButton.BACK_BUTTON).on("click", onBackClick);	
        
        $(navigationButton.EXIT_BUTTON).on("click", onExitClick);

        $(navigationButton.AUDIO_BUTTON).on("click", onAudioClick);
        
        //register key handeling:
		$(document).keydown(function(e){
            // console.log(e.keyCode);
            keys[e.keyCode] = true;
			onkeyDown();
        });
        $(document).keyup(function(e){
            keys[e.keyCode] = false;
        });
        
        $(window).on('blur', function() {
            // This is a fix to the bug where after opening the communcation log and closing it, any key pressed would trigger the log to open again.
            keys = keys.map(value => {
                if(value === true || value === false){
                    return false;
                } else {
                    return value;
                }
            });
        });
    }
    
    function onDebugNextClick(){
        if ($(this).hasClass(navigationButton.DISABLED)){
            return false;
        }
        
        navigationController.debugNextPage();
    }
    
    function onDebugBackClick(){
        if ($(this).hasClass(navigationButton.DISABLED)){
            return false;
        }
        
        navigationController.debugPreviousPage();
    }
    
    function onMenuClick(){
        if ($(this).hasClass(navigationButton.DISABLED)){
            return false;
        }
        
        navigationController.showMenu();
    }
    
    function onNextClick(){
        if ($(this).hasClass(navigationButton.DISABLED)){
            return false;
        }
        
        navigationController.nextPage();
    }
    
    function onReplayClick(){
        if ($(this).hasClass(navigationButton.DISABLED)){
            return false;
        }
        
        navigationController.replayPage();
    }
    
    function onBackClick(){
        if ($(this).hasClass(navigationButton.DISABLED)){
            return false;
        }
        
        navigationController.previousPage();
    }
    
    function onAudioClick(){
        if ($(this).hasClass(navigationButton.DISABLED)){
            return false;
        }
        //console.log('onAudioClick');
        audioController.muteAudio();

        var btnText;
        var $audioTooltip = $(navigationButton.AUDIO_BUTTON).closest('[data-toggle="tooltip"]');
        if(audioController.isMuted){
            $(this).addClass('muted');
            uiController.audioIconSVGTimeline.play();
            btnText = courseData.getBtnText('AudioOff');
            $(document).trigger(courseEvent.AUDIO_OFF);
        } else{
            $(this).removeClass('muted');
            uiController.audioIconSVGTimeline.reverse();
            btnText = courseData.getBtnText('AudioOn');
            $(document).trigger(courseEvent.AUDIO_ON);
        }
        $(navigationButton.AUDIO_BUTTON+' .btn_text').html(btnText);
        // update the tooltip text
        $audioTooltip.attr('title', btnText); 
        $audioTooltip.attr('data-original-title', btnText);
        $audioTooltip.tooltip('show');
    }

    function onExitClick(){
        if ($(this).hasClass(navigationButton.DISABLED)){
            return false;
        }
        
        navigationController.confirmExitCourse();
    }
    
    function onkeyDown(){
        // console.log(keys[17], keys[16], keys[67]);
        // 16 = SHIFT & 73 = I
        // 17 = CTRL & 16 = SHIFT & 67 = C
	 	if (courseConfig.lmsLogEnabled && keys[17] && keys[16] && keys[67] && !islogPop){
//			keys[16]=false;
//			keys[73]=false;
			loadCommunicationWin();	
            return false;
		}
    }
}