var liveEditReq = {
    //    courseDataLoadComplete: false,
        contentDataLoadComplete: false,
        liveEditAllowed: false
    }
    var loadComplete, liveEditAllowed;
    
    //$(document).on("courseDataLoadComplete", function(){
    //    liveEditReq.courseDataLoadComplete = true;
    //    setupLiveEdit();
    //});
    
    $(document).on("contentDataLoadComplete", function(){
        //console.log("CONTENTDATALOADCOMPLETE", liveEditReq.courseDataLoadComplete && liveEditReq.liveEditAllowed)
        liveEditReq.contentDataLoadComplete = true;
        if (!liveEditReq.liveEditAllowed)
            setupLiveEdit();
    //     else {
    //         console.log("CONTENTDATALOADCOMPLETE fetching changes");
    // //        liveEditReq.contentDataLoadComplete = false;
    //         liveEditController.fetchChanges();
    //     }
            
    });
    
    function setupLiveEdit(){
        if (liveEditReq.contentDataLoadComplete && liveEditReq.liveEditAllowed && !liveEditController.initialized){
            //console.log("setup");
    
    //        liveEditReq.contentDataLoadComplete = false;
            liveEditController.init();
            // liveEditController.getCSS();
            // liveEditController.initGUI();
            // liveEditController.clearEdits();
            // liveEditController.attachHandlers();  
            // if (liveEditController.webToken && liveEditController.user)
            //     liveEditController.verifyToken();
            // else 
            //     liveEditController.enableEdit(false); 
        }
    }
    
    // show live edit?
    if (liveEditConfig.enabled){
    //    var liveEditController = new LiveEditController();
        //console.log("enabled");
        var url = liveEditConfig.rootURL + 'api/liveEditEnabled?courseId=' + LaunchConfig.course.id + '&version=' + LaunchConfig.course.version;
        if (window.location.search.match(/liveedit=true/i))
            url += '&LIVEEDITENABLED=true';
        //console.log(url);
        $.ajax({
            type: 'GET',
            url: url,
            dataType: 'json',    
            success: function(response){
                //console.log("LIVEEDITENABLED: ", response);
                if (response.enabled){
                    getLiveEditScript();
                } else {
                    //console.log('live edit script not loaded');
                    liveEditConfig.enabled = false;
                    // alert('Live edit not enabled on this course or version!');
                }    
            },
            error: function(xhr, type){
                //console.log(xhr.responseText, type);
                //console.log('live edit script not loaded');
                liveEditConfig.enabled = false;
            }
        });
      
    }
    
    function getLiveEditScript(){
        $.ajax(
            {
                type: 'GET',
                url: liveEditConfig.rootURL + 'api/liveEditScript?courseId=' + encodeURIComponent(LaunchConfig.course.id) + '&version=' + LaunchConfig.course.version,
                dataType: 'script',
                success: function(script, textStatus){
                    //console.log('live edit script loaded');
                    liveEditReq.liveEditAllowed = true;
                    if (liveEditReq.contentDataLoadComplete)
                        setupLiveEdit();
                }
            });
    }