var ActionUtility = function () {
    var self = this;

    this.setupActions = function (component, actions) {

        // aria-controls="'+self.id+'-collapse'+i+'" aria-expanded="false"

        var $el = component.$component;
        
        var componentIndex = parseInt($el.data('componentindex'));

        // var args = actions[action];
        // // make args into array
        // if (!Array.isArray(args)) {
        //     args = [args];
        // }

        for (var action in actions){


            switch(action.toLowerCase()){
                case 'reveal':
                case 'show':
                case 'fadein':
                case 'slidedown':
                    // console.log(componentIndex, action, actions)
                    var componentIndexes = component.componentController.getComponentIndexes(componentIndex, actions[action]);
                    if (componentIndexes.length == 1){
                        var $target = component.componentController.componentObjectsArray[componentIndexes[0]].$component;

                        $el.attr('aria-controls', $target.attr('id'));
                        $el.attr('aria-expanded', 'false');

                        // $target.attr('aria-labelledby', $el.attr('id'));
                    }
                    break;
            }
        }
    }
    
    this.fireActions = function(component, actions){
        // console.log('ActionUtility',actions)
        
        for (var action in actions){
            //console.log(action)
            var actionRef = actions[action];

            self.applyAction(component, action, actionRef);
            //console.log('ActionUtility::fireActions', action, actionRef);
             
            // Google analytics - track user interaction
//            gtag('event', 'engagement', {
//              'event_category' : 'Click '+navigationController.moduleIndex+','+navigationController.pageIndex+','+component.componentController.slideObj.slideNum,
//              'event_label' : action+' '+actionRef
//            });
            
            
        }      
        
        this.removePulses(component.$component);
    }

    this.applyAction = function(component, action, args){
        // make args into array
        if (!Array.isArray(args)) {
            args = [args];
        }

        var $el = component.$component;
        var componentIndex = parseInt($el.data('componentindex'));

        // action = action.toLowerCase();

        // console.log('ActionUtility',action);
        switch(action.toLowerCase()){
            case 'disable':
                // var componentIndexes = component.componentController.getComponentIndexes(componentIndex, actionRef);
                // if ()
                $el.disable();
                break;
            case 'enable':
                $el.enable();
                break;
            case 'next':
                $el.disable();
                navigationController.nextPage();
                break;
            case 'back':
                $el.disable();
                navigationController.previousPage();
                break;
            case 'showmenu':
                navigationController.showMenu();
                break;
            case 'goto':
                $el.disable();

                var moduleIndex = self.getModifiedInt(navigationController.moduleIndex, args[0]);
                var pageIndex = self.getModifiedInt(navigationController.pageIndex, args[1]);
                var slideIndex = self.getModifiedInt(navigationController.slideIndex, args[2]);
                
                // console.log('goto', moduleIndex, pageIndex, slideIndex)
                navigationController.changePageToData(moduleIndex, pageIndex, slideIndex);
                break;
            case 'gotopage':
                $el.disable();
                
                var nextPageIndex = self.getModifiedInt(navigationController.pageIndex, args[0]);

                if (!isNaN(nextPageIndex)){
                    navigationController.changePageToData(navigationController.moduleIndex, nextPageIndex, navigationController.slideIndex);                        
                }
                break;
            case 'submenunext':
                $el.disable();

                var nextPageIndex = courseData.getSubmenuNextPage();
                if (nextPageIndex){
                    var nextPageObj = navigationController.pageArray[courseData.getSubmenuNextPage()];
                    console.log('submenunext', nextPageObj);
                    navigationController.changePageToData(nextPageObj.module, nextPageObj.page, 0);
                } else {
                    navigationController.showMenu();
                }
                
                break;
            case 'submitanswers':
                var componentsArr = [];
                if (args[0] == ''){
                    for (var i = 0; i < component.componentController.componentObjectsArray.length; i++){
                        if (component.componentController.componentObjectsArray[i].submitAnswers){
                            componentsArr.push(i);
                        }
                    }
                } else if (args[0] == 'closest'){
                    $el.parent().find('.component').not($el).each(function(){
                        if (component.componentController.componentObjectsArray[componentIndex].submitAnswers){
                            // console.log($el, parseInt($(this).data('componentindex')));
                            componentsArr.push(componentIndex);
                        }
                    })
                } else {
                    componentsArr = args;
                }

                for (var i = 0; i < componentsArr.length; i++){
                    // console.log(i, componentsArr[i], parseInt(componentsArr[i]));
                    if (component.componentController.componentObjectsArray[parseInt(componentsArr[i])].submitAnswers){
                        component.componentController.componentObjectsArray[parseInt(componentsArr[i])].submitAnswers();
                    }
                }
                //var $components = //component.componentController.getComponents(actionRef.split(','));
                //console.log('submitAnswers', actionRef.split(','), $components);
                break;
            case 'checkanswers':
                component.componentController.checkAnswers();
                break;
            case 'reveal':
            case 'show':
            case 'fadein':
            case 'slidedown':
                if (args[0].indexOf('#') != -1){
                    // HTML element
                    var $element = $(args[0]);
                    $element.removeClass('hidden').hide()[(action == 'reveal') ? 'slideDown' : action](
                        function(){
                            //navigationController.scrollIntoView($(this));
                        }
                        );
                        component.componentController.showHiddenComponents($element);
                    
                } else {
                    // component
                    var componentIndexes = component.componentController.getComponentIndexes(componentIndex, args);
                    var options = {};
                    // if (args[1]){
                    //     if (args[1].indexOf('{') != -1 && args[1].indexOf('}') != -1){
                    //         options = JSON.parse(args[1]);
                    //     }
                    // } 
                    options.effect = action;
                    //console.log('ActionUtility', action, options);
                    component.componentController.revealComponents(componentIndexes, options);
                }
                $el.attr('aria-expanded', 'true');
                break;
            case 'hide':
            case 'fadeout':
            case 'slideup':
                if (args[0].indexOf('#') != -1){
                    // HTML element
                    $(args[0])[action]();
                } else {
                    var componentIndexes = component.componentController.getComponentIndexes(componentIndex, args);
                    var options = {};
                    // if (args[1]){
                    //     if (args[1].indexOf('{') != -1 && args[1].indexOf('}') != -1){
                    //         options = JSON.parse(args[1]);
                    //     }
                    // } 
                    options.effect = action;
                    console.log('ActionUtility', action, options);
                    component.componentController.hideComponents(componentIndexes, options);
                }
                break;
            case 'hideandreveal':
                // let's check number of arguments - if 1 use componentController hide and reveal to hide previously revealed. If 2 only hide element indexes provided
                // if (arg
                var componentIndexes = component.componentController.getComponentIndexes(componentIndex, args);
                component.componentController.hideAndRevealComponents(componentIndexes, {effect: 'slideUp'}, {effect: 'slideDown'});
                break;
            case 'hidethenreveal':
                // console.log(action)
                // hide then show after delay
                var componentIndexesToHide = component.componentController.getComponentIndexes(componentIndex, args[0]);
                var componentIndexesToReveal = component.componentController.getComponentIndexes(componentIndex, args[1]);
                component.componentController.hideComponents(componentIndexesToHide, {effect: 'fadeOut'});
                setTimeout(function(){
                    component.componentController.revealComponents(componentIndexesToReveal, {effect: 'fadeIn'});
                }, 500);
                break;
            case 'addclass':
                // var args = actionRef.split(',');
                var componentIndexes = component.componentController.getComponentIndexes(componentIndex, args[0]);
                for (var i = 0; i < componentIndexes.length; i++){
                    // console.log('ActionUtility addClass', args, componentIndexes)
                    component.componentController.componentObjectsArray[componentIndexes[i]].$component.addClass(args[1]);
                }
                break;
            // case 'slidedown':
            //     var $element = $(args[0]);
            //     // console.log('slidedown', $element)
            //     $element.removeClass('hidden').hide().slideDown({
            //         start: function(){
            //             // let's tell any components they are now displayed
            //             component.componentController.slideObj.showElementContents($(this));
            //         }
            //     });
            //     break;
            // case 'slideup':
            //     var $element = $(args[0]);
            //     $element.slideUp();
            //     break;
            case 'slidetoggle':
                var $element = $(args[0]);
                if ($element.hasClass('hidden')){
                    $element.removeClass('hidden').hide();
                }
                $element.slideToggle();
                break;
            case 'setcomplete':
                var componentIndexes = component.componentController.getComponentIndexes(componentIndex, args);
                for (var i = 0; i < componentIndexes.length; i++){
                    component.componentController.setComponentComplete(component.componentController.componentObjectsArray[componentIndexes[i]]);
                }
                break;
            case 'setslidecomplete':
                for (var i = 0; i < component.componentController.componentObjectsArray.length; i++){
                // console.log('setslidecomplete', component.componentController.componentObjectsArray[i]);
                    if (!component.componentController.componentObjectsArray[i].isComplete){
                        component.componentController.setComponentComplete(component.componentController.componentObjectsArray[i]);
                    }
                }
                break;
            case 'setpagecomplete':
                // find submenu number from nav XML
                var pageIndex = parseInt(args[0]);
                // console.log('setpagecomplete', pageIndex);
                if (!isNaN(pageIndex)){
                    pageIndex = navigationController.getModulePageIndex(navigationController.moduleIndex, pageIndex);
                    for (var i = 0; i < navigationController.pageArray[pageIndex].slides.length; i++){
                        storageController.setSlideComplete(pageIndex, i);
                    }
                }
                break
            case 'hideslide':
                var slideNum = !isNaN(parseInt(args[0].split('+').join(''))) ? parseInt(args[0].split('+').join('')) : 0;
                contentController.hideSlide(component.componentController.slideObj.slideNum+slideNum);
                break;
            case 'resumeslide':
                // contentController.hideSlide(component.componentController.slideObj.slideNum+parseInt(args[0].split('+').join(''))
                contentController.slideArray[component.componentController.slideObj.slideNum+parseInt(args[0].split('+').join(''))].resumeContent();
                break;
            case 'role':
                navigationController.setRoles(args);
                break;
            case 'restart':
                //gameController.restart();
                navigationController.changePageToData(0,0,0);
                break;
            case 'download':
            case 'link':
                // note download is internal and link is an external resource
                var link = action == 'link' ? courseData.getLink(args[0]) : utils.getURL(langPath+args[0]);
                if (link.toLowerCase().indexOf('mailto') == 0){
                    component.$component.data('link-mailto', true)
                }
                utils.openLink(link);
                break;
            case 'retry':
                // find closest interaction
                component.componentController.retry();
                break;
            case 'modal':

                break;
            case 'certificate':
                loadCertificateWin();
                break;
            case 'exit':
                navigationController.confirmExitCourse();
                break;
            case 'instruction':
                component.componentController.changeInstruction(args[0]);
                break;
            case 'help':
                uiController.showHelp();                
                break;
            case 'resources':
                uiController.showResources();
                break;
            // case 'skip-pa':
            //     // custom action for Adverse Events 2021 - disable Preassessment if skipped
            //     storageController.setMenuModuleActive(3, false);
            //     navigationController.showMenu();
                break;
        }
    }
    
    this.getActions = function( xmlNode){
        // check if item has an action
        self.createActionsArray(xmlNode, 'actions');
        self.createActionsArray(xmlNode, 'completeActions');
    }

    this.createActionsArray = function(xmlNode, actions){
    // check if item has an action
        if (xmlNode[actions]){
            if (typeof(xmlNode[actions]) == 'string'){
                //console.log ('action',xmlNode.actions);
                var actionsObj = {};
                var tmpActions = xmlNode[actions].split('|');
                for (var i = 0; i < tmpActions.length; i++){
                    // get action name and remove parentheses
                    var actionName = tmpActions[i].toLowerCase().replace(/ *\([^)]*\) */g, "");
                    var parenthesesStr = '';
                    if (tmpActions[i].indexOf('(') != -1){
                        parenthesesStr = tmpActions[i].substring(tmpActions[i].indexOf('(')+1, tmpActions[i].length-1);
                    }
                    //var argsArray = (parenthesesStr != '') ? parenthesesStr.split(',') : [];


                    actionsObj[actionName] = parenthesesStr.split(',');
                }

                xmlNode[actions] = actionsObj;
            }
        }
    }
    
    this.getModifiedInt = function(value, newValue){
        var returnValue;
                
        if (newValue.indexOf('+') != -1){
            //forward X pages
            returnValue = value+parseInt(newValue.split('+').join(''));
        } else if (newValue.indexOf('-') != -1){
            //back X pages
            returnValue = value-parseInt(newValue.split('-').join(''));
        } else {
            returnValue = parseInt(newValue);
        }

        return returnValue;
    }

    this.removePulses = function($el){
        // remove any pulse classes
        //$el.removeClass(utils.getWordsByFragment($el[0].className, 'pulse').join(' '));
        htmlUtils.removeClasses($el, 'pulse');
    }
}