function StorageComms(){
    var self = this;

    var commsLogStr = '<h1 class="titleText">'+LaunchConfig.course.author+' Test Report - '+LaunchConfig.course.id+' ['+LaunchConfig.course.version+'] - Start</h1>';
    var commsLogObj = {trace: [], func: []};


    this.addTrace = function(msg){
        //console.log('TrackDataTrace',msg);
        var d = new Date().toISOString();
        
        commsLogObj.trace.push({
            date: d,
            msg: msg
        })
        var str = '';
        str += '<div class="data-item data-trace-item">';
            str += '<p><span class="valLable" >#'+commsLogObj.trace.length+'</span> <span class="valLable" >Time:</span>'+d+'</p>';
            str += '<p><span class="valLable" >Trace: </span>'+msg+'</p>';
            str += '<p class="botBorder"></p>';
        str += '</div>';
        commsLogStr += str;
        // console.log('communicationWin', communicationWin)
        if (communicationWin != null && islogPop) {
		    communicationWin.updateLog(self.getLogString());
	    }
    }

    this.addFunc = function(func,param,paramValue,resultText,result){
        //_data
    //    if (infoText == ''){
    //        infoText += '<h1 class="titleText">'+LaunchConfig.course.author+' Test Report - '+LaunchConfig.course.id+' ['+LaunchConfig.course.version+'] - Start</h1>';
    //    }

    
        commCount++;
        var d = new Date().toISOString();
        commsLogObj.func.push({
            date: d,
            func: func,
            param: param,
            value: paramValue,
            result: resultText
        })

        var str = '';
        str += '<div class="data-item data-function-item">';
            str+='<p><span class="valLable" >#'+commsLogObj.func.length+'</span> <span class="valLable" >Time:</span>'+d+'</p>';
            str+='<p><span class="valLable" >Function: </span>'+func+'</p>';

            if(param!=null && param!=''){
                str+='<p><span class="valLable" >Parameter: </span>'+param+'</p>';
            }
            if(paramValue!=null && paramValue!=''){
                str+='<p><span class="valLable" >Parameter Value: </span>'+paramValue+'</p>';
            }
            str+='<p><span class="valLable" >Return: </span>'+resultText+'</p>';
            if(result){
                str+='<p><span class="valLable" >Result: </span><span class="passResult" >Success</span></p>';
            }else{
                str+='<p><span class="valLable" >Result: </span><span class="failResult" >Fail</span></p>';
            }
            str+='<p class="botBorder"></p>';
        str+='</div>';
        commsLogStr += str;
        if (communicationWin != null && islogPop) {
            communicationWin.updateLog(self.getLogString());
        }
    }

    // this.addText = function(_str){	
    //     // _str=_str+"<br/><br/>"+systemInfo(screenSize);			
    //     // $('.logtext').html(_str);
    //     // var str = _str;
    //     // str += '<h2 class="titleText">'+LaunchConfig.course.author+' Test Report - Start</h2>';
    //     // str += screenSize;
    //     // str += '<h2 class="titleText">'+LaunchConfig.course.author+' Test Report - End</h2>';
    //     updateLog(_str);
    // }

    // this.updateLog = function(_str){
    //     // var st=window.opener.infoText+;

    //     // var str = _str;
    //     // str += '<h2 class="titleText">System Report</h2>';
    //     // str += window.opener.getSystemInfoStr();
    //     // str += screenSize;

    //     $('.logtext').html(_str);
    // }

    this.getLogString = function(){
        return commsLogStr+getSystemInfoStr();
    }
    this.getLogObj = function(){
        return commsLogObj;
    }
}