    var nodesToIgnore = ['typeOf', 'RootName', '_children', '_childorder'];
    
    function addPathToObject(obj, path, dbid){
    //    console.log('addPathToObject', path);    
        for (var x in obj){
            if (nodesToIgnore.indexOf(x) == -1){
                if (x == 'Text'){
                    if (obj[x] != ''){
                        self.textPathArray.push(path);
                        obj.xmlPath = path;
                        obj.dbid = dbid;

                        if (courseConfig.debrand){
                            obj[x] = self.debrand(obj[x]);
                        }
                    }
                } else if (obj[x].length){
                    for (var i = 0; i < obj[x].length; i++){
                        if (typeof(obj[x][i]) == 'object'){
                            // store node name
                            obj[x][i].nodeName = x;
                            // create xml path
                            var newPath = (path == '' ? x : path+'.'+x);
                            var newDbid = obj[x][i].dbid || dbid;
                            addPathToObject(obj[x][i], newPath+'['+i+']', newDbid);
                        }
                    }
                } else {
                    // store node name
                    obj[x].nodeName = x;
                    // create xml path
                    var newPath = (path == '' ? x : path+'.'+x);
                    var newDbid = obj[x].dbid || dbid;
                    addPathToObject(obj[x], newPath, newDbid);
                }
            }
        }
    }

    this.insertIntoXML = function(node, nodeToInsert, pos){
        // console.log('XMLData::insertIntoXML', nodeToInsert.nodeName);
        var performInsert = true;
        if (node._childorder[pos]){
            if (node._childorder[pos].node == nodeToInsert.nodeName){
                if (node._childorder[pos].insert == true){
                    // make sure this insertion hasn't already been done
                    performInsert = false;
                }
            }
        }
        // console.log('XMLData::insertIntoXML', performInsert);
        if (performInsert){
            var insertIndex = 0;
            if (node[nodeToInsert.nodeName]){
                node[nodeToInsert.nodeName].push(nodeToInsert);
                insertIndex = node[nodeToInsert.nodeName].length-1;
            } else {
                node[nodeToInsert.nodeName] = nodeToInsert;
                node._children.push(nodeToInsert.nodeName);
            }
            node._childorder.splice(pos, 0, {node: nodeToInsert.nodeName, pos: insertIndex, insert: true});
        }
        // insertions should be removed from XML on page change, to prevent being done again
        $(document).one(courseEvent.PAGE_CHANGE, function(){
            self.deleteFromXML(node, nodeToInsert.nodeName, pos);
        });
    }

    this.deleteFromXML = function(node, nodeName, pos){
        // console.log('XMLData::deleteFromXML', nodeName);
        node[nodeName].splice(node._childorder[pos].pos, 1);
        node._childorder.splice(pos, 1);
    }

    this.findInXML = function(node, element, attributeName, attributeValue){
        var results = this.recurseFindInXML(node, element, attributeName, attributeValue, []);

        return results;
    } 

    this.recurseFindInXML = function(node, element, attributeName, attributeValue, results){
        if (node._childorder){
            if (node._childorder.length){            
                for (var i = 0; i < node._childorder.length; i++){
                    var nodeName = node._childorder[i].node;
                    var nodeData = node[nodeName][node._childorder[i].pos];

                    if (nodeName == element){
                        // console.log(i, 'element '+element+' found', nodeData);
                        if (nodeData[attributeName]){
                            // console.log('element '+element+' attribute '+attributeName+' found', nodeData[attributeName]);
                            if (nodeData[attributeName] == attributeValue){
                                // console.log('element attributeValue match', nodeData[attributeName]);
                                var result = {
                                    parent: node,
                                    element: nodeData, 
                                    pos: i
                                }
                                results.push(result);
                            }
                        }
                    }
                    
                    results.concat(this.recurseFindInXML(nodeData, element, attributeName, attributeValue, results));
                }
            }
        }
        return results;
    }

    this.debrand = function(str){
        var loremArray = [
            'Lorem ipsum dolor sit amet, consectetur adipiscing elit.', 
            'Nam in fringilla mi. Nullam iaculis ex risus, ut sodales tortor fringilla non.', 
            'Nunc nec mi tincidunt magna dapibus tristique. Aenean in bibendum lectus.', 
            'Nam a pretium orci, a imperdiet urna.', 
            'Suspendisse aliquet dui non quam semper pharetra.', 
            'Praesent porttitor commodo erat, eu consequat purus tincidunt sed.', 
            'Etiam suscipit vehicula cursus. Mauris quis suscipit lectus.', 
            'Nulla ut mauris quis risus porta ornare ac sit amet lectus.', 
            'Nullam aliquam in urna quis dignissim.', 
            'Etiam non viverra leo.', 
            'Aliquam erat volutpat.', 
            'Curabitur scelerisque, tellus non aliquam hendrerit, justo tellus malesuada metus, ut hendrerit mauris odio et nunc.', 
            'Nam vel augue volutpat metus rhoncus elementum sed a dui.', 
            'Mauris tincidunt ac enim a tincidunt.', 
            'Curabitur eleifend tellus ante, nec pellentesque massa consequat in.', 
            'Curabitur elementum urna non odio feugiat rutrum. Proin id rutrum arcu.'
        ];
        var returnStr = '';
        while (returnStr.length < str.length){
            returnStr += loremArray[Math.floor(Math.random()*loremArray.length)]+' ';
        }
        returnStr = returnStr.substring(0, str.length);
        if (str.charAt(str.length-1) == '.' && returnStr.charAt(returnStr.length-1) != '.'){
            returnStr += '.';
        }
        return returnStr;
    }
    
