var AudioController = function() {
    var self = this;
    
    this.isMuted = false;
    this.audioObj = {};

    this.useCreateJS = false;
    
    function init(){
        // console.log('AudioController init')

        this.isMuted = parseInt(storageController.getAudio()) == 0;

        // create audio canvas for create JS?
        // $('#content-wrapper').append('<canvas id="audioCanvas" width="100" height="100"></canvas>');

        
        // createjs.Sound.alternateExtensions = ["mp3"];
        // createjs.Sound.on("fileload", loadSound);
        // createjs.Sound.registerSounds(sounds, assetsPath);
        // after load is complete
        // createjs.Sound.play("sound2");

        // var sounds = {
        //     path: coursePath+'content/media/',
        //     manifest: [
        //         {
        //             id: "metalhit", src: {mp3: 'components/metalhit3.mp3'}
        //         }
        // ]};

        // registerSound("sound.mp3", "soundId", 4);

        // createjs.Sound.alternateExtensions = ["mp3"];

        
        if (createjs){
            self.useCreateJS = true;
        }

        createjs.Sound.addEventListener("fileload", function(){
            // console.log('AudioController sounds loaded');
            // createjs.Sound.play("metalhit");
        });

        // var audioPath = coursePath+'content/media/components/';
        // var sounds = [{
        //     id: "metalhit",
        //     src: "metalhit3.mp3"
        // }];
        // createjs.Sound.registerSound(coursePath+'content/media/components/metalhit.mp3', 'metalhit');
        // createjs.Sound.registerSounds(sounds);
    }
    
    this.loadAudio = function(name, file){
        if (self.useCreateJS){
            createjs.Sound.registerSound(file, name);
        } else {
            if (this.audioObj[name] == undefined){            
                var audio = new Audio(file);
                audio.onended = function() {
                }
            }

            this.audioObj[name] = audio;
        }
    }
    
    this.playAudio = function(name){
        if (self.useCreateJS){
            if (createjs.Sound.loadComplete(name)) {
                createjs.Sound.play(name);
            }
        } else {
            if (this.audioObj[name]){
                this.audioObj[name].muted = this.isMuted;
                this.audioObj[name].play();
            }
        }
    
    this.playAudioFromStart = function(name){
        if (this.audioObj[name]){
            this.audioObj[name].muted = this.isMuted;
            if (!isNaN(audioObj.duration)) {
                this.audioObj[name].currentTime = 0;
            }
            this.audioObj[name].play();
        }
    }
    }
    this.playNewAudio = function(name){
        if (this.audioObj[name]){
            var newAudio = this.audioObj[name].cloneNode();
            newAudio.muted = this.isMuted;
            if (!isNaN(newAudio.duration)) {
                newAudio.currentTime = 0;
            }
            newAudio.onended = function() {
                //this.remove();
                $(this).remove();
            }
            newAudio.play();
        }
    }
    
    this.muteAudio = function(){
        this.isMuted = !this.isMuted;
        for (x in this.audioObj){
            this.audioObj[x].muted = this.isMuted;
        }
        if (createjs){
            createjs.Sound.muted = this.isMuted;
        }
        storageController.setAudio(this.isMuted ? 0 : 1);
    }
    
    init();
}