    this.checkComponentsComplete = function(){
        if (self.slideObj){
            self.getFirstIncompleteComponent();

	        if (!self.slideObj.hidden){
	                // only check complete if not hidden
	    //        console.log('checkComponentsComplete()', self.slideObj.slideNum, self.componentObjectsArray.length)
	            var isComplete = true;
	            var completeCount = 0;
	    //        for (var x in self.componentsObj){
	    //            if (self[x+'Manager']){
	    //                console.log('ComponentController::checkComponentsSatisfied',x, self[x+'Manager'].isComplete);
	    //                if (!self[x+'Manager'].isComplete){
	    //                    isComplete = false;                    
	    //                }                
	    //            }
	    //        }
	
	            // add any component actions
	            var validCount = 0;
	            for (var i = 0; i < self.componentObjectsArray.length; i++){
                    var component = self.componentObjectsArray[i];
	                if (self.isComponentValid(component)){
                        validCount ++;
                        if (self.checkComponentComplete(component)){
                            completeCount ++;
                        } else {
                            isComplete = false;
                            break;
                        }
                        // console.log('isComponentValid', component.name, component.isComplete, component.showOnComplete, component.enableOnComplete)
	                    // if (!component.isComplete && !component.showOnComplete && !component.enableOnComplete){
	                    //     isComplete = false;
	                    //     break;
	                    // } else {
	                    //     completeCount++;
	                    // }
	                }
	            }
	
	            //console.log('ComponentController::checkComponentsComplete - slide', self.slideObj.slideNum, validCount, completeCount, self.componentObjectsArray.length, isComplete);
	            ////console.log('ComponentController::checkComponentsComplete - slide', self.slideObj.slideNum, completeCount, self.componentObjectsArray, isComplete);
	            if (isComplete){
	                for (var i = 0; i < self.componentObjectsArray.length; i++){
	                    var component = self.componentObjectsArray[i];
                        if (!component.$component.hasClass('conditional-hidden')){
                            if ((component.showOnComplete && component.$component.hasClass('hidden')) || (component.visibleOnComplete && component.$component.hasClass('invisible'))){
                                self.revealComponents([i]);
                            }
                            if (component.enableOnComplete && component.$component.hasClass('disabled')){
                                if (component.$component.hasClass('animating')){
                                    component.$component.addClass('enable_on_anim_complete');
                                } else {
                                    component.$component.enable();
                                }
                            }
                            if (component.hideOnComplete){
                                component.$component.slideUp(function(){
                                    $(this).addClass('hidden');
                                });
                            }
                            if (component.invisibleOnComplete){
                                component.$component.fadeOut(function(){
                                    $(this).addClass('invisible');
                                });
                            }
                        }
	                }
	                
	                // let's change any instruction to last available
	                self.changeInstruction(99, false);
	                
	                self.slideObj.slideComponentsComplete();                
	            }
	        }
		}
    }

    this.checkComponentComplete = function(component){
        // if (!component.isComplete && !component.showOnComplete && !component.enableOnComplete && !component.animsComplete){
        if (component.isComplete && component.animsComplete){// && !component.showOnComplete && !component.enableOnComplete){
            return true;
        } else {
            if (component.showOnComplete || component.visibleOnComplete || component.enableOnComplete){
                return true;
            }
            return false;
        }
    }
    
    this.isComponentValid = function(component){
        var $component = component.$component;
        var valid = true,
            visible = true;
        
        if (self.isComponentValidForRole(component, navigationController.roles)){
            
            // check if has classes hidden or hidden-xs,hidden-sm,hidden-md,hidden-lg
            if (self.isComponentHidden(component) && component.revealOnComponentsCompleteArray.length > 0){
                visible = false;
                valid = false;
            }

            // check if invisible and within any node with selectiveContent class
            if (valid && !$component.is(':visible')) {
                 if ($component.closest('.selectiveContent').length > 0){
                    valid = false;
                 }
            } 

            //check for any with visible set at a different breakpoint meaning they're hidden
            // if (visible && !$component.hasClass('visible-'+responsiveUtils.getCurrentViewport())){
            // if (visible && !$component.hasClass('visible-'+responsiveUtils.getCurrentViewport())){
            //     for (var x in responsiveUtils.getBreakpoints()){
            //         if (x != responsiveUtils.getCurrentViewport()){
            //             if ($component.hasClass('visible-'+x)){
            //                 valid = false;
            //             }
            //         }
            //     }
            // }
            if (valid && self.isComponentHidden(component)){
                valid = false;
            }
        } else {
            valid = false;
        }
        
        
        // console.log('ComponentController::isComponentValid', component, valid);
        return valid;
    }

    // chekc if a component is hidden or within a parent that is hidden by bootstrap hidden classes
    this.isComponentHidden = function(component){
        var $component = component.$component;
        return responsiveUtils.isElementHidden($component);
    }

    this.checkComponentAnimsComplete = function(component){
        // console.log('ComponentController::checkComponentAnimsComplete', component.name);
        var $component = component.$component;
        var $componentAnims = htmlUtils.findAnims($component, ':visible');//$component.find('.anim:visible').filter(); // exclude invisible
        if (component.animsComplete || $componentAnims.length == $componentAnims.filter('.animated').length){
            component.animsComplete = true;
            return true;
        } else {
            return false;
        }
    }
    
    this.getComponentRoles = function(component){
        if (component.dataNode.roles){
            component.roles = component.dataNode.roles.split(',').toNumberArray();
        }
    }

    this.isComponentValidForRole = function(component, roles){
        var $component = component.$component;
        var isValid = true;
        // var rolesArr = utils.getWordsByFragment($component[0].className, 'role');
        // if (rolesArr.length){
        //     console.log('rolesArr', rolesArr)
        //     isValid = false;
        //     for (var i = 0; i < roles.length; i++){
        //         if ($component.hasClass('role'+roles[i])){
        //             isValid = true;
        //             break;
        //         }
        //     }
        // }
        if (component.roles.length){
            isValid = false;
            for (var i = 0; i < roles.length; i++){
                if (component.roles.indexOf(roles[0]) != -1){
                    isValid = true;
                    break;
                }
            }

        }

        //if (!component.roles.length
        return isValid;
    }
    
    this.setComponentComplete = function(component, forceComplete){
        // console.log('setComponentComplete', component.name, forceComplete);
        forceComplete = forceComplete;
        if (component instanceof jQuery){
            var index = parseInt(component.data('componentindex'));
            component = self.componentObjectsArray[index];
        }
        
        var isComplete = true;
        
        if (!component.isComplete){
            if (forceComplete){
                // set animations to complete
                component.animsComplete = true;
            } else {
                
                // console.log('setComponentComplete', component.name, component.childComponents);
                if (component.childComponents.length){
                    component.completeOnChildrenComplete = true;
                    // does component have children? If so need to wait
                    for (var i = 0; i < component.childComponents.length; i++){
                        var childComponent = self.componentObjectsArray[component.childComponents[i]];
                        if (!childComponent.isComplete){
                            if (self.isComponentValid(childComponent)){
                                // need to check if child component is enabled on complete, or shown
                                // if (component.visibleOnComponentsComplete ||
                                if (self.isComponentRevealedByCompletion(childComponent) ||                                     
                                    childComponent.enableOnComplete){
                                } else {
                                    isComplete = false;
                                    // console.log('setComponentComplete children', component.componentID, isComplete);
                                    break;
                                }
                            }
                        }
                    }
                }
                // console.log('setComponentComplete', component.name, isComplete);
                if (!self.checkComponentAnimsComplete(component)){
                    // console.log('checkComponentAnimsComplete', component.name, isComplete);
                    component.$component.find('.anim').on('inview_anim_complete', function(){
                        // this anim is a child of a component
                        var component = self.getComponentObject($(this).closest('.component').data('componentindex'));
                        if (self.checkComponentAnimsComplete(component)){
                            // check internal animations are complete
                            self.setComponentComplete(component);
                        }
                    });
                    // console.log('setComponentComplete checkComponentAnimsComplete', isComplete);
                    isComplete = false;
                }
            }
            // console.log('setComponentComplete', component.name, isComplete);
            if (isComplete){
                component.isComplete = true;
                component.$component.addClass('complete');
                
                // fire any completion actions
                //console.log('setComponentComplete:completeActions', component.dataNode.completeActions)
                if (component.dataNode.completeActions){
                    actionUtils.fireActions(component, component.dataNode.completeActions);
                }
                
                // fire event
                component.$component.trigger(courseEvent.COMPONENT_COMPLETE);

                // check any other components that rely on this being complete
                for (var i = 0; i < self.componentObjectsArray.length; i++){
                    var otherComponent = self.componentObjectsArray[i];
                    //console.log(i, component.revealOnComponentsCompleteArray)
                    
                    if (self.isComponentHidden(otherComponent) && otherComponent.revealOnComponentsCompleteArray.length > 0){
                        var completeCount = 0;
                        for (var j = 0; j < otherComponent.revealOnComponentsCompleteArray.length; j++){
                            if (self.componentObjectsArray[otherComponent.revealOnComponentsCompleteArray[j]].isComplete){
                                completeCount++;
                            }
                        }
                        if (completeCount == otherComponent.revealOnComponentsCompleteArray.length){
                            self.revealComponents([i]);
                        }
                    }
                }

                // does component have parent?
                if (component.parentComponent !== false){
                    var parentComponent = self.componentObjectsArray[component.parentComponent];
                    if (parentComponent.completeOnChildrenComplete){
                        self.setComponentComplete(parentComponent);
                    }
                }

                
                // lets save component save data
                self.setSaveData(component);
            }
            
            self.checkComponentsComplete();
        } else {
            //console.log('setComponentComplete already complete')
            component.$component.addClass('complete');
        }
    }

    
