    this.revealComponentsWithEffect = function(component, options){
        var effects = ['show', 'fadeIn', 'slideDown'];

        var effect = options.effect || 'slideDown';
        if (effects.indexOf(effect) == -1){
            effect = 'slideDown'
        }

        var instruction; 
        if (component.dataNode.i){
            instruction = parseInt(component.dataNode.i);
        } else if (component.dataNode.instruction){
            instruction = parseInt(component.dataNode.instruction);
        }

        component.$component[effect]({
            duration: options.duration,
            complete: function(){
                setTimeout(function(){
                    navigationController.checkForScroll();
                },1000);
                // component.componentDisplayed();
                self.matchComponentHeights();

                if (instruction){
                    self.changeInstruction(instruction);
                }
                self.showChildComponents(component.$component);

                if (component.revealOnComponentsCompleteArray.length == 0){
                    //only autoscroll to if not auto revealed
                    navigationController.scrollIntoView($(this));
                }
                //$(this)[0].scrollIntoView({behavior: "smooth", block: 'end'});
            }
        });  
    }
    
    this.hideComponents = function(components, options){
        var defaults = {
            delay: 0,
            duration: 400,
            effect: undefined
        }
        options = $.extend(defaults, options);

        var effects = ['hide', 'fadeOut', 'slideUp'];
        for (var i = 0; i < effects.length && options.effect; i++){
            if (effects[i].toLowerCase() == options.effect.toLowerCase()){
                options.effect = effects[i];
                break;
            }
        }

        var $components = getComponents(components);

        if (!options.effect){
            options.effect = 'hide';
        }
        options.duration = options.effect == 'hide' ? 0 : options.duration;

        console.log('ComponentController::hideComponents', $components, options.effect);

        function hideComponentsWithEffect(){
            $components[options.effect]({
                duration: options.duration,
                complete:function(){
                    navigationController.checkForScroll();
                    
                    var $component = $(this);
                    
                    $component.removeClass('revealed');
                    if ($component.hasClass('revealed-hidden')){
                        $component.removeClass('revealed-hidden').addClass('hidden');                        
                    }
                    if ($component.hasClass('revealed-invisible')){
                        $component.removeClass('revealed-invisible').addClass('invisible');                        
                    }
                }
            });
        }

        if (options.delay){
            setTimeout(hideComponentsWithEffect, options.delay);
        } else {
            hideComponentsWithEffect();
        }
    }
    
    this.hideAndRevealComponents = function(args, hideOpts, revealOpts){
        // hide all revealed
        self.hideComponents($slide.find('.component.revealed'), hideOpts);
        // reveal all components in args
        self.revealComponents(args, revealOpts);
    }
    
    function getComponents(args){
        var $components = $();
        if (args instanceof jQuery){
            $components = args;
        } else {
            for (var i = 0; i < args.length; i++){
                var $component;
                if (args[i] instanceof jQuery){
                    $component = args[i];
                } else {
                   // console.log('reveal', args[i], $('#component'+self.slideObj.slideNum+'-'+args[i]));
                    // find element without custom ID
                    $component = $('#component-'+self.slideObj.slideNum+'-'+args[i]);    
                    if ($component.length == 0){
                        // find element with custom ID
                        $component = $('#component-'+args[i]);                         
                    }
                }
                //$component.addClass('revealed').removeClass('hidden').hide().slideDown();
                $components = $components.add($component);
                if ($component.data('clone')){
                    //console.log('CLONE FOUND', $component.data('clone'));
                    $components = $components.add($('#'+$component.data('clone')));
                }
            }
        }
        return $components;
    }

