    this.showChildComponents = function($el){
        var $components = $el.find('.component');
        
        $components.each(function(){
            
            // console.log('ComponentController::showChildComponents', $(this).data('componentindex'));
            if (!$(this).parents().addBack().hasClass('hidden')){
                console.log('ComponentController::showChildComponents', $(this).data('componentindex'));
                // this anim is a child of a component
                var component = self.getComponentObject($(this).data('componentindex'));
                component.componentDisplayed();
            }
        });
    }
    
    this.getComponentObject = function(num){
        // if is jquery object
        if (num instanceof jQuery){
            num = parseInt(num.data('componentindex'));
        }
        return self.componentObjectsArray[num];
    }
    
    this.retry = function(){
        // check any other components that rely on this being complete
        for (var i = 0; i < self.componentObjectsArray.length; i++){
            var component = self.componentObjectsArray[i];
            if (component.retry){
                component.retry();
            }
        }
    }
    
