    this.getAbilities = function(componentObj, xmlNode){
        // check if component has an action
        actionUtils.getActions(xmlNode, componentObj.componentController);
        
        componentObj.draggable = (xmlNode.draggable === "true");
        
        // check if component is set complete on completion of other components
        if (xmlNode.showOnComponentsComplete || xmlNode.visibleOnComponentsComplete){
            componentObj.revealOnComponentsCompleteArray = [];
            if (xmlNode.showOnComponentsComplete){
                componentObj.showOnComponentsComplete = true;
                componentObj.revealOnComponentsCompleteArray = xmlNode.showOnComponentsComplete.split(',');
            } else if (xmlNode.visibleOnComponentsComplete){
                componentObj.visibleOnComponentsComplete = true;
                componentObj.revealOnComponentsCompleteArray = xmlNode.visibleOnComponentsComplete.split(',');
            }
            for (var i = 0; i < componentObj.revealOnComponentsCompleteArray.length; i++){
                // componentObj.revealOnComponentsCompleteArray[i] = parseInt(componentObj.revealOnComponentsCompleteArray[i]);
                componentObj.revealOnComponentsCompleteArray[i] = self.getComponentIndexes(componentObj.num, (componentObj.revealOnComponentsCompleteArray[i]));
            }
        }
        
        //componentObj.showOnComplete = false;
        // check if component is set complete on completion of other components
        componentObj.showOnComplete = (xmlNode.showOnComplete === "true"); 
        componentObj.visibleOnComplete = (xmlNode.visibleOnComplete === "true"); 
        componentObj.hideOnComplete = (xmlNode.hideOnComplete === "true"); 
        componentObj.invisibleOnComplete = (xmlNode.invisibleOnComplete === "true"); 
        componentObj.enableOnComplete = (xmlNode.enableOnComplete === "true"); 
    }
    
