    this.getComponentIndexes = function(componentIndex, str){
        // console.log('getComponentIndexes', componentIndex, strArr)
        var componentIndexes = [];

        var strArr = Array.isArray(str) ? str : str.split(',');
        // console.log(strArr);
        for (var i = 0; i < strArr.length; i++){
            if (strArr[i].indexOf('+') != -1){
                // based on current index +
                componentIndexes.push(componentIndex+parseInt(strArr[i].split('+').join('')));
            } else if (strArr[i].indexOf('-') != -1){
                // based on current index -
                componentIndexes.push(componentIndex+parseInt(strArr[i]));
            } else {
                componentIndexes.push(strArr[i]);//.split(',');
            }
        }
        return componentIndexes;
    }

