    /**
     * @function updateXMLText
     * @description This function update XML CDATA in required formate. 
     * @param obj {Document}
     */
    function updateXMLText(node){
        var cnode;	//Current Node
        if(node.hasChildNodes()) {			
            var nodeCount = node.childNodes.length - 1;	
            var n = 0;
            do {
                cnode = node.childNodes[n];
                switch(cnode.nodeType) {
                    case 1:
                    if(cnode.hasChildNodes()) {
                        updateXMLText(cnode); //Recursive Call
                    }
                    break;
                    case 3: //Text Value
                        cnode.nodeValue = filterText(cnode.nodeValue);
                    break;
                    case 4: //CDATA
                        if(cnode.text){
                            cnode.text = filterText(cnode.text);
                        }else{
                            cnode.nodeValue = filterText(cnode.nodeValue);
                        }					
                    break;
                }
            } while(n++ < nodeCount);
        }
    }

    /**
     * @function filterText
     * @description This function is used to find and replace trquired XML Tag. 
     * @param _text {String}
     * @returns _text {String}
     */
    function filterText(_text){
         if(_text.indexOf('liIcon')>0){ return _text;}
        _text = _text.replaceAll('<li>','<li><span class="liIcon" ></span><span class="liText">');
        _text = _text.replaceAll('</li>','</span></li>');
        return _text;
    }
    
