    function getDocHeight(){
        var D = document;
        return Math.max(
            D.body.scrollHeight, D.documentElement.scrollHeight,
            D.body.offsetHeight, D.documentElement.offsetHeight,
            D.body.clientHeight, D.documentElement.clientHeight
        );
    }
    
    function handleClickAndPress(myfunc) {
        return function (e) {
            if (e.type != "keypress" || e.keyCode == 13) {
                myfunc(e);
            }
        };
    }
    
    
    this.css_time_to_milliseconds = function (time_string) {
       var num = parseFloat(time_string, 10),
           unit = time_string.match(/m?s/),
           milliseconds;

       if (unit) {
           unit = unit[0];
       }

       switch (unit) {
           case "s": // seconds
               milliseconds = num * 1000;
               break;
           case "ms": // milliseconds
               milliseconds = num;
               break;
           default:
               milliseconds = 0;
               break;
       }

       return milliseconds;
    }
    
    this.getWordsByFragment = function(str, fragment) {
        var regexp = new RegExp("\\b" + fragment + "\\w*\\b", "gi");
        return str.match(regexp) || [];
    }
    
    this.removeWordsByFragment = function(str, fragment) {
        var regexp = new RegExp("\\b" + fragment + "\\w*\\b", "gi");
        return str.replace(regexp, "").trim() || [];
    }
    
    this.getLongestWord = function(str) {
        var stringArray = str.split(" ");
        var longestWord = stringArray.reduce(function(a, b){
            if (b.length > a.length) {
              return b;
            } else {
              return a;
            }
        }, '');
        return longestWord;
    }
    
    /**
     * @function closestNumber
     * @description This function is used to find the closest number from the array. 
     * @param array {array}
     * @param num {Number}
     * @returns {Number}
    */
    this.closestNumber = function (array,num){
        var i=0;
        var minDiff=1000;
        var ans;
        for(i in array){
             var m=Math.abs(num-array[i]);
             if(m<minDiff){ 
                    minDiff=m; 
                    ans=array[i]; 
                }
          }
        return ans;
    }
    
