    /**
     * @function getViewportTop
     * @description This function is used to get the viewport offset - as IE is incorrect using pageOffsetY
     * @returns {Boolean} return offset object
     */
    this.getViewportTop = function() {
        var offsetTop = 0;
        // when zoomed at 75% and 125% we occasionally see w.pageYOffset=1 and documentElement.scrollTop=1 in IE
        if (window.pageYOffset > 1){
            offsetTop = window.pageYOffset;
        } else if (document.documentElement.scrollTop > 1){
           offsetTop = document.documentElement.scrollTop;
        } else {
           offsetTop = document.body.scrollTop; 
        }  

        return offsetTop;
    }
    
    /**
     * @function findElementTotalOffset
     * http://watcheyesolutions.blogspot.co.uk/2012/10/in-ie-jquery-offsettop-not-returning.html
     * @description This function is used to get total offset as offset() in IE is incorrect
     * @returns {Boolean} return offset object
     */
    this.findElementTotalOffset = function(obj) {
       var oleft = otop = 0;
       if (obj.offsetParent) {
         do {
           oleft += obj.offsetLeft;
           otop += obj.offsetTop;
         }while (obj = obj.offsetParent);
       }
       return {left : oleft, top : otop};
    }
    
    /**
     * @function shuffle
     * @description This function is used to randomly shuffle an array
     * @returns array
     */
    Array.prototype.shuffle = function() {
        for(var j, x, i = this.length; i; j = parseInt(Math.random() * i), x = this[--i], this[i] = this[j], this[j] = x);
        return this;
    };

    Array.prototype.toNumberArray = function(){
        return this.map(Number);
    }
    
    this.checkArraysEqual = function(a, b) {
        if (a === b) return true;
        if (a == null || b == null) return false;
        if (a.length !== b.length) return false;
      
        // If you don't care about the order of the elements inside
        // the array, you should sort both arrays here.
        // Please note that calling sort on an array will modify that array.
        // you might want to clone your array first.
      
        for (var i = 0; i < a.length; ++i) {
          if (a[i] !== b[i]) return false;
        }
        return true;
    };
    
    /**
     * @function shuffle
     * @description This function is used to randomly shuffle jquery elements
     * @returns array
     */
    $.fn.shuffle = function(selector){
        (selector ? this.find(selector) : this).parent().each(function(){
            $(this).children(selector).sort(function(){
                return Math.random() - 0.5;
            }).detach().appendTo(this);
        });

        return this;
    };

    this.get2dCombinations = function(sets){
        var firstSet = sets[0];
        if (sets.length === 1){
            return firstSet.map(function(element){
                return [element];
            });
        }
       
        var combinations = [];
        for (var i = 0; i < firstSet.length; i++){
            var combNMinusOne = utils.get2dCombinations(sets.slice(1));
            for (var j = 0; j < combNMinusOne.length; j++){
                combinations.push([firstSet[i]].concat(combNMinusOne[j].slice()));
            }
        }
        return combinations;
    }

