	this.raphaelGetTextDetail = function(paper, text, x, y, fontSize, maxWidth){
        
        var words = text.replace('<br>', '\n').split(' ');
        
        //var words = text.replaceAll('<br>', '\n ').split(' ');
        
        //var words = text.replaceAll('<br>', '\n ').split(' ');
        var textStr = '';
        var line = '';

        var t;

        for (var n = 0; n < words.length; n++) {
            var testLine = line + words[n] + ' ';
            t = paper.text(x, y, testLine);
            t.attr("font-size", fontSize);
            //console.log(paper, 'raphaelGetTextDetail',t.getBBox().width)
            if (t.getBBox().width > maxWidth && n > 0) {
                textStr += line+'\n';
                line = words[n] + ' ';
            } else {
                line = testLine;
            }
            t.remove();
        }
        textStr += line;
        return {
            textStr: textStr,
            x: x,
            y: y
        }
    }

    this.raphaelAddText = function(paper, text, x, y, fontSize, maxWidth){
        //var set = paper.set();

        var textObj = utils.raphaelGetTextDetail(paper, text, x, y, fontSize, maxWidth);
        t = paper.text(textObj.x, textObj.y, textObj.textStr);
        t.attr("font-size", fontSize);
        return t;
    }

