    //used by json
    function recurseData(node, customNodeAction){
        //console.log('recurseData', node);
        
        var customNodeActionTerm = '';
        if (customNodeAction){
            customNodeActionTerm = customNodeAction.term;
        }
        
        var htmlStr = '';

        //for (var i = 0; i < node.length; i++){
        for (var x in node){
            switch(x){
                case 'sections':
                case 'rows':
                    // either a row or container
                    var nodeName = '';

                    for (var i = 0; i < node[x].length; i++){



                        if (x == 'rows'){
                            nodeName = 'row';
                        } else if (x == 'sections'){
//                                console.log('fullwidth', node[x][i] )
                            if (node[x][i].fullwidth == "true"){
                                nodeName = 'container-fluid';                                
                            } else {
                                nodeName = 'container';
                            }
                        }
                        if (x == 'sections'){
                            htmlStr += '<div class="section'+htmlUtils.getClasses(node[x][i])+'" '+htmlUtils.getAttributes(node[x][i])+'>';
                        }
                            htmlStr += '<div class="'+nodeName+'">';
                                htmlStr += htmlUtils.recurseData(node[x][i]);
                            htmlStr += '</div>';
                        if (x == 'sections'){
                            htmlStr += '</div>';
                        }
                    }
                    break;
                case 'columns':
                    // need to analyse bootstrap here
                    // var colsObj = responsiveUtils.getBootstrapColumnsObject(node[x]);
                    // for (var i = 0; i < node[x].length; i++){
                    //     var colStr = responsiveUtils.getBootstrapColumnsClassStr(colsObj, i);
                    //     htmlStr += '<div class="'+colStr+htmlUtils.getClasses(node[x])+'" '+htmlUtils.getAttributes(node[x])+'>';
                    //     htmlStr += htmlUtils.recurseData(node[x][i]);
                    //     htmlStr += '</div>';
                    // }
                    for (var i = 0; i < node[x].length; i++){
                        var colStr = responsiveUtils.getBootstrapColumnsStrForNode(node[x]);
                        htmlStr += '<div class="'+colStr+htmlUtils.getClasses(node[x])+'" '+htmlUtils.getAttributes(node[x])+'>';
                        htmlStr += htmlUtils.recurseData(node[x][i]);
                        htmlStr += '</div>';
                    }
                    break;
                case customNodeActionTerm:
                    //console.log('components');
                    for (var i = 0; i < node[x].length; i++){
                        //console.log('components', node[x][i]);
                        htmlStr += customNodeAction.callback(node[x][i].type, node[x][i]);
                    }
                    break;
            }
        }
        
        return htmlStr;
    }
    
    function recurseXML(node, opts, customNodeAction){

        // console.log('recurseXML', node);
        var customNodeActionTerm = '';
        if (customNodeAction){
            customNodeActionTerm = customNodeAction.term;
        }
        
        var htmlStr = '';
        if (node._childorder){
            if (node._childorder.length){            
                for (var i = 0; i < node._childorder.length; i++){
                    var closeTag = '';
                    //recurseXML(self.slideObj.xmlData.container[i]);
                    var nodeName = node._childorder[i].node;
                    var thisNodeName = nodeName;
                    var nodeData = node[nodeName][node._childorder[i].pos];
                    //console.log('recurseXML', nodeName);
                    // if (nodeData.anim=""){
                    //     // allow ability for node to kill anims of children
                    //     opts.anim = false;
                    // }
                    
                    
    //                
                    switch (nodeName){
                        case 'transcript':
                        case 'contentBackground':
                        case 'instructionText':
                            // don't want to check these tags
                            break;
                        
                        case 'section':
                        case 'container':
                        case 'row':
                            // either a row or container
                            htmlStr += '<div'+htmlUtils.getID(nodeData)+' class="'+nodeName+htmlUtils.getClasses(nodeData, opts)+'" '+htmlUtils.getAttributes(nodeData, opts)+'>';
                            if (nodeName == 'section' || nodeName == 'container'){
                                if (nodeData.fullwidth == "true"){
                                    thisNodeName = 'container-fluid';                                
                                } else {
                                    thisNodeName = 'container';
                                }
                                //htmlStr += '<div class="section'+htmlUtils.getClasses(nodeData)+'" '+htmlUtils.getAttributes(nodeData)+'>';
                                htmlStr += '<div class="'+thisNodeName+'">';
                            } 
                            //closeTag = '</div>';
                            htmlStr += htmlUtils.recurseXML(nodeData, opts, customNodeAction);                        
                            htmlStr += '</div>';
                        
                            if (nodeName == 'section' || nodeName == 'container'){
                                htmlStr += '</div>';
                            }
                            //htmlStr += '<div class="'+nodeName+htmlUtils.getClasses(nodeData)+'" '+htmlUtils.getAttributes(nodeData)+'>';
                            //closeTag = '</div>';
                            //htmlStr += self.recurseXML(nodeData);
                            break;
                            
                        case 'column':
                        case 'col':
                            // need to analyse bootstrap here
                            // var colsObj = responsiveUtils.getBootstrapColumnsObject(node.column);
                            // var colStr = responsiveUtils.getBootstrapColumnsClassStr(colsObj, node._childorder[i].pos);
                            // nodeName is either 'column' or 'col'
                            var colStr = responsiveUtils.getBootstrapColumnsStrForNode(node[nodeName][node._childorder[i].pos]);
    //                        console.log(colStr);
                            htmlStr += '<div'+htmlUtils.getID(nodeData)+' class="'+colStr+htmlUtils.getClasses(nodeData, opts)+'" '+htmlUtils.getAttributes(nodeData, opts)+'>';
                            //closeTag = '</div>';
                            htmlStr += htmlUtils.recurseXML(nodeData, opts, customNodeAction);
                            htmlStr += '</div>';
                            break;
                            
                        case customNodeActionTerm:
                            //console.log('customNodeActionTerm')
                            htmlStr += customNodeAction.callback(nodeData.type, nodeData);
                            break;
                        
                        case 'text':
                            htmlStr += htmlUtils.recurseXML(nodeData, opts, customNodeAction);     
                            break
                        //case 'component':
                            //console.log('createComponent', nodeName, nodeData.type)
                            //htmlStr += self.createComponent(nodeData.type, nodeData);
                            //break;
                        default:
                            //console.log('generateHTML');
                            htmlStr += htmlUtils.generateHTML(nodeName, nodeData, opts, customNodeAction);
                            break;
                    }
                    //htmlStr += closeTag;
                    //}
                }
            } else {
                htmlStr += htmlUtils.getHTMLText(node, opts);                 
            }
        }
        return htmlStr;
    } 
    
    function getHTMLText(textData, opts){
        var text = '';
        // console.log('getHTMLText', textData, textData.Text)
        if (textData.Text){
            text = '<span data-dbid="'+textData.dbid+'"';
            if (textData.xmlPath && liveEditConfig.enabled){
                text += ' data-xmlref="'+textData.xmlPath+'"';
            }
            text += '>'+checkTextForVariables(textData.Text)+'</span>';
        } else {
            text = htmlUtils.extractHTML(textData, opts);
        }
        return text;
    }
    
    function checkTextForVariables(str){

        //[svg
        str = str.replace(/\[svg="[^"]+"\]/gi, function(extract){
            //console.log('SVG: ', extract.replace('[svg="','').replace('"]',''));
            return '<span class="inline-svg">'+svgUtils.createSVG(extract.replace('[svg="','').replace('"]',''), 'span')+'</span>';
        });

        str = str.replace(/\$\{[^]+?\}/gi, function(extract){
            return '<span class="textVariable" data-var="'+extract+'">'+utils.evaluateVariable(extract)+'</span>';
        });

        // get any HTML links
        str = str.replace(/href="(.*?)"/g, function(m, $1) {
            if ($1.indexOf ('#') != 0 && $1.indexOf ('javascript') != 0){
                // replace link where not used with an id starting # or javascript
                var link = courseData.getLink($1);
                return 'href="'+link+'" onclick="utils.onClickOpenLink(event)"';
            } else {
                return 'href="'+$1+'"';
            }

            //return 'href="' + courseData.getLink($1) + '"';
        });

        return str;
    }
    
    function updateTextVariables(){
        $('.textVariable').each(function(){
            $(this).text(utils.evaluateVariable($(this).data('var')))
        });
    }
    
    function extractHTML(node, opts){
        var htmlStr = '';
        if (!node._childorder){
            return htmlUtils.generateHTML('', node, opts);
            return '';
        }
        for (var i = 0; i < node._childorder.length; i++){
            var nodeName = node._childorder[i].node;
            var nodeData = node[nodeName][node._childorder[i].pos];
            
            htmlStr += htmlUtils.generateHTML(nodeName, nodeData, opts);
        }
        return htmlStr;
    }
    
    function generateHTML(nodeName, nodeData, opts, customNodeAction){
        var htmlStr = '';
        var idStr = htmlUtils.getID(nodeData);
        
        var classStr = htmlUtils.getClasses(nodeData, opts);
        if (classStr != ''){
            if (nodeName == ''){
                nodeName = 'span';
            }
            classStr = ' class="'+classStr+'"';
        }
        
        if (nodeName){
            htmlStr += '<'+nodeName+idStr+classStr+' '+htmlUtils.getAttributes(nodeData, opts);
        }
        switch (nodeName){
            case 'br':
            case 'hr':
                if (nodeName){
                    //self closing tags
                    htmlStr += ' />';
                }
                break;
            default:
                if (nodeName){
                    htmlStr += '>';
                }
                var injectionFound = false;
                if (nodeData.useModuleTitle){
                    // if using the module title we can get title from the module object
                    var module = courseData.data.module[navigationController.moduleIndex];
                    if (module.title){
                        htmlStr += getHTMLText(module.title[0], opts);
                        injectionFound = true;
                    }
                } else if (nodeData.usePageTitle){
                    // if using the page title we can get title from the page object
                    var page = courseData.data.module[navigationController.moduleIndex].page[navigationController.pageIndex];
                    if (page.title){
                        htmlStr += getHTMLText(page.title[0], opts);
                        injectionFound = true;
                    }
                } else if (nodeData.useSlideTitle){
                    // if using the slide title we can get title from the slide object
                    var slide = contentController.getFirstUnloadedSlide();
                    if (slide.data.title){
                        htmlStr += getHTMLText(slide.data.title[0], opts);
                        injectionFound = true;
                    }
                } else if (nodeData.inject){

                    try {
                        if (nodeData.inject.indexOf('courseData') == -1){
                            // inject from shared                            
                            htmlStr += recurseXML(_.get(courseData.data.shared[0], nodeData.inject), opts, customNodeAction);
                        } else {
                            // inject from elsewhere
                            htmlStr += recurseXML(_.get(window, nodeData.inject), opts, customNodeAction);
                        }

                        injectionFound = true;
                    } catch (e){
                        console.error('generateHTML injection failed', nodeData.inject, e);
                    }
                }
                if (nodeData._childorder && !injectionFound){
                    if (nodeData._childorder.length){
                        if (courseConfig.dataFormat == 'xml'){
                            htmlStr += htmlUtils.recurseXML(nodeData, opts, customNodeAction);
                        } else {
                            htmlStr += htmlUtils.extractHTML(nodeData, opts);
                        }
                    } else {
                        htmlStr += getHTMLText(nodeData, opts);
                    }
                }
                if (nodeName){
                    htmlStr += '</'+nodeName+'>';
                }
            break;
        }
        return htmlStr;
    }
    
    
    function getCSSString(_xml){
        //console.log(_xml)
        var stylestr = getStyles(_xml);
        if (stylestr != ""){
            stylestr = ' style="'+stylestr+'"';
        }
        return stylestr;
    } 
    /**
     * @function getCSSString
     * @description This function is used to generate the CSS style from JSON object. 
     * @param _xml {JSON object}
     * @returns stylestr {String}
     */
    function getStyles(_xml){
        var stylestr="";

        if (_xml.style){
            // allow xml style attribute
            stylestr += _xml.style;
        }
        for (var x in self.stylesObject){
            if(_xml[x]!=null){
                if (x == 'backgroundImage' || x == 'bkgImage'){
                    stylestr += self.stylesObject[x]+': url('+utils.getImagePath(_xml[x])+'); ';
                } else {
                    stylestr += self.stylesObject[x]+':'+_xml[x]+'; ';
                }
            }
        }

        return stylestr;
    }

