    this.getClasses = function(nodeData, opts){//, nodeName){
        opts = opts || {};
        //console.log('getClasses', nodeData.nodeName ? nodeData.nodeName : nodeData);//  nodeData.nodeName);
        var htmlStr = '';
        // get animation classes
        var anim = nodeData['anim'];
        var rolesArr = [];
        if (nodeData['roles']) {
            rolesArr = nodeData['roles'].split(',');
            for (var i = 0; i < rolesArr.length; i++){
                htmlStr += ' role'+parseInt(rolesArr[i]);
            }
        } 
        
        // get classes
        if (nodeData['class']){
            var classes = nodeData['class'];
            if (anim != 'none'){
                // pulse effects use same css as anims so we need to delay addition of classes
                var classArray = utils.getWordsByFragment(classes, 'pulse');
                if (classArray != null){
                    for (var i = 0; i< classArray.length; i++){
                        classArray[i] = 'suppress-'+classArray[i];
                    }
                    classes = utils.removeWordsByFragment(classes, 'pulse')+' '+classArray.join(' ');
                }
            }
            htmlStr += ' '+classes;
        }
        
        // showIf conditions
        if (nodeData['showIf']){
            if (!utils.checkConditionStr(nodeData['showIf'])){
                htmlStr += ' hidden conditional-hidden';
            }
        }

         // hideIf conditions
         if (nodeData['hideIf']){
            console.log('hideIf', nodeData['hideIf'], utils.checkConditionStr(nodeData['hideIf']))
            if (utils.checkConditionStr(nodeData['hideIf'])){
                htmlStr += ' hidden conditional-hidden';
            }
        }
        
        //this.getDeviceSpecificClasses(nodeData)
        for (var x in responsiveUtils.getBreakpoints()){
            if (nodeData['class_'+x] || nodeData['class_not_'+x]){
                htmlStr += ' device-specific-class';
                break;
            }
            if (nodeData['style_'+x] || nodeData['style_not_'+x]){
                htmlStr += ' device-specific-style';
                break;
            }
        }
        
        //console.log(nodeData.type, anim)
        if (anim){
            if (anim != 'none'){
                htmlStr += ' anim '+anim;
            }
        } else if (nodeData.nodeName){
            if (opts.anim == false){
                // dont animate
            } else {
                var defaultAnim = animConfig.presets[nodeData.nodeName];
                if (defaultAnim){
                    htmlStr += ' anim '+defaultAnim;
                }
            }
        }
        
        
        return htmlStr;
    }
    
    this.getDeviceSpecificAttributes = function(nodeData){
        var htmlStr = '';
        var classesObj = {};
        var stylesObj = {};

        for (var x in responsiveUtils.getBreakpoints()){
            classesObj[x] = [];
            stylesObj[x] = [];
        }
        
        var found = false;
        for (var x in responsiveUtils.getBreakpoints()){
            // get Device specific Classes
            if (nodeData['class_'+x]){
                found = true;
            //console.log('getDeviceSpecificAttributes', x);
                classesObj[x] = classesObj[x].concat(nodeData['class_'+x].split(' '));
            }
            
            if (nodeData['class_not_'+x]){
                found = true;
                for (var y in responsiveUtils.getBreakpoints()){
                    if (x != y){
                        classesObj[y] = classesObj[y].concat(nodeData['class_not_'+x].split(' '));
                    }
                }
            }
        }
        if (found){
            for (var x in responsiveUtils.getBreakpoints()){
                if (classesObj[x].length){
                    htmlStr += 'data-class-'+x+'="'+classesObj[x].join(' ')+'" ';
                }
                
            }
            //console.log('getDeviceSpecificAttributes', classesObj, htmlStr);
        }

        // now get device specific styles
        found = false;
        for (var x in responsiveUtils.getBreakpoints()){
            // get Device specific Classes
            if (nodeData['style_'+x]){
                found = true;
                classesObj[x] = classesObj[x].concat(nodeData['style_'+x].split(' '));
            }
            
            if (nodeData['style_not_'+x]){
                found = true;
                for (var y in responsiveUtils.getBreakpoints()){
                    if (x != y){
                        classesObj[y] = classesObj[y].concat(nodeData['style_not_'+x].split(' '));
                    }
                }
            }
        }
        if (found){
            for (var x in responsiveUtils.getBreakpoints()){
                if (classesObj[x].length){
                    htmlStr += 'data-style-'+x+'="'+classesObj[x].join(' ')+'" ';
                }
                
            }
            //console.log('getDeviceSpecificAttributes', classesObj, htmlStr);
        }
        
        
        return htmlStr;
    }
    
    this.updateDeviceSpecificClasses = function(){
        // console.log('updateDeviceSpecificClasses');
        var $elements = $('.device-specific-class');
        $elements.each(function(){
            var $el = $(this);
            var classes = '';
            for (var x in responsiveUtils.getBreakpoints()){
                var data = $el.data('class-'+x);
                if (data){
                    classes += data+' ';
                }
            }
            // console.log('updateDeviceSpecificClasses', classes);
            if (classes != ''){
                $el.removeClass(classes);
            }
            
            var data = $el.data('class-'+responsiveUtils.getCurrentViewport());
            if (data){
                $el.addClass(data);
            }
        });
    }
    
