    this.stylesToObject = function(str){
        // https://stackoverflow.com/questions/9518956/javascript-convert-css-style-string-into-js-object
        var regex = /([\w-]*)\s*:\s*([^;]*)/g;
        var match, 
            stylesObj={};
        while(match=regex.exec(str)) stylesObj[match[1]] = match[2].trim();
        // console.log('styles', stylesObj)
        return stylesObj;
    }
    
    
    this.getAttributes = function(nodeData, opts){
        var htmlStr = htmlUtils.getCSSString(nodeData);
        
        // get data attributes
        if (nodeData.animDelay){
            htmlStr += ' data-anim-delay="'+nodeData.animDelay+'"';
        }

        // add aria attributes
        if (nodeData.ariaHidden){
            htmlStr += ' aria-hidden="'+(nodeData.ariaHidden == 'true' ? 'true' : 'false')+'"';
        }
        
        // get getDeviceSpecificClasses 
        htmlStr += ' '+this.getDeviceSpecificAttributes(nodeData);
        
        return htmlStr;
    }  
    
    this.removeClasses = function($el, str){
        // remove any classes
        $el.removeClass(utils.getWordsByFragment($el[0].className, str).join(' '));
    }

    this.addAnimationDelayCSS = function($el, delay){
        var secondsDelay = delay/1000+'s';
        $el.css({
            '-webkit-animation-delay': secondsDelay,
            '-moz-animation-delay': secondsDelay,
            '-o-animation-delay': secondsDelay,
            'animation-delay': secondsDelay
        })

    }

	this.replaceClassesBasedOnTextDirection = function($el, classesObj){
        classesObj = classesObj || {
            'left': 'right', 
            'right': 'left', 
            'top-left': 'top-right', 
            'top-right': 'top-left', 
            'bottom-left': 'bottom-right', 
            'bottom-right': 'bottom-left', 
            'left-top': 'right-top', 
            'right-top': 'left-top'
        };
        
        $el.each(function(){
            var $this = $(this);
            var classesChangedArr = [];
            for (cl in classesObj){
                var oldClass = cl;
                var newClass = classesObj[cl];
                if (classesChangedArr.indexOf(oldClass) == -1){
                    // replace device breakpoint specific classes
                    for (var x in responsiveUtils.getBreakpoints()){
                        var hasDeviceSpecificClass = false;
                        // if $el has device specific classes e.g. class_xs = 'right', add to $el data 
                        if ($this.data('class-'+x) == oldClass){
                            hasDeviceSpecificClass = true
                            $this.data('class-'+x, newClass);
                            if (classesChangedArr.indexOf(newClass) == -1){
                                classesChangedArr.push(newClass);
                            }
                        }
                        
                    }
                    // if $el DOES NOT have device specific classes, change the class
                    if (!hasDeviceSpecificClass && $this.hasClass(oldClass)){
                        $this.removeClass(oldClass);
                        $this.addClass(newClass);
                        if (classesChangedArr.indexOf(newClass) == -1){
                            classesChangedArr.push(newClass);
                        }
                    }
                }
            }
        });

        // if ($el.hasClass(oldClass)){
        //     $el.removeClass(oldClass).addClass(newClass);
        // }
        // // replace device breakpoint specific classes
        // for(var x in responsiveUtils.getBreakpoints()){
        //     if ($el.attr('data-class-'+x) == oldClass){
        //         $el.attr('data-class-'+x, newClass);
        //     }
        // }
    }

    this.findAnims = function($el, selector){
        selector = selector || '';
        // console.log($el.find('.anim'+selector))

        // the classes:
        // '.not-animated' will stop self and any children anims being animated
        // '.children-not-animated' will stop any children anims being animated
        var $anims = [];
        if ($el){
            $anims = $el.find('.anim'+selector).not('.rolehidden').filter(function(){
                return ($(this).closest('.not-animated').length === 0 && $(this).parents('.children-not-animated').length === 0);
            });
        }
        // console.log($anims)
        return $anims;
    }
