var SVGUtility = function(){

    this.svgs = [
		'plus',
		'minus',
		'plusminus',
		'plustick',
		'tick',
		'cross',
		'tickcross',
		'circle'
	];
    
    if (InlineSVG) {
        // add the Inline SVG
        this.svgs = this.svgs.concat(Object.keys(InlineSVG));
    }
    
    this.createSVG = function(name, elType){
		// console.log('createSVG', name, elType)
        elType = elType ? elType : 'div';
        var htmlStr = '';
        
        // check if RTL language
        if (languageConfig.languages[currentLanguage].direction == 'rtl'){
            if (name == 'chevron-right'){
                name = 'chevron-left';
            }
        }
		var svgName = name.toLowerCase();

		htmlStr += '<'+elType+' class="svg-'+svgName+'">';
			htmlStr += '<'+elType+' class="svgWrap">';
				htmlStr += '<'+elType+' class="no-svg"></'+elType+'>';
        if (InlineSVG[svgName]) {
            // does this SVG exist within the InlineSVG object?
			
				htmlStr += InlineSVG[svgName];

        } else {
	        switch(name.toLowerCase()){
	            case 'plus':
					htmlStr += '<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" style="enable-background:new 0 0 100 100;" xml:space="preserve">';
						htmlStr+='<path class="path" d="M50,5 L50,95"/>';
						htmlStr+='<path class="path" d="M5,50 L95,50" />';
					htmlStr += '</svg>';
	                break;
	            case 'minus':
					htmlStr += '<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" style="enable-background:new 0 0 100 100;" xml:space="preserve">';
						htmlStr+='<path class="path" d="M5,50 L95,50" />';
					htmlStr += '</svg>';
	                break;
	            case 'plusminus':
					htmlStr += '<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" style="enable-background:new 0 0 100 100;" xml:space="preserve">';
						htmlStr+='<path class="plus path" d="M50,5 L50,95"/>';
						htmlStr+='<path class="minus path" d="M5,50 L95,50" />';
					htmlStr += '</svg>';
	                break;
	            case 'plustick':
					htmlStr += '<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" style="enable-background:new 0 0 100 100;" xml:space="preserve">';
						htmlStr+='<path class="plus path" d="M50,5 L50,95"/>';
						htmlStr+='<path class="plus path" d="M5,50 L95,50" />';
						htmlStr += '<path class="tick path" d="M5,60 L40,85 L95,15"/>';
					htmlStr += '</svg>';
	                break;
	            case 'tick':
					htmlStr += '<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" style="enable-background:new 0 0 100 100;" xml:space="preserve">';
						htmlStr += '<path class="tick path" d="M5,60 L40,85 L95,15"/>';
					htmlStr += '</svg>';
	                break;					
				case 'cross':
					htmlStr += '<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" style="enable-background:new 0 0 100 100;" xml:space="preserve">';
						htmlStr += '<path class="cross path path1" d="M5,5 L95,95" />';
						htmlStr += '<path class="cross path path2" d="M5,95 L95,5" />';
					htmlStr += '</svg>';
	                break;
	            case 'tickcross':
					htmlStr += '<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" style="enable-background:new 0 0 100 100;" xml:space="preserve">';
						htmlStr += '<path class="tick path" d="M5,60 L40,90 L95,10"/>';
						htmlStr += '<path class="cross path path1" d="M5,5 L95,95" />';
						htmlStr += '<path class="cross path path2" d="M5,95 L95,5" />';
					htmlStr += '</svg>';
	                break;
				case 'circle':
					htmlStr += '<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" style="enable-background:new 0 0 100 100;" xml:space="preserve">';
						htmlStr += '<circle class="st2" cx="50" cy="50" r="45"/>';
					htmlStr += '</svg>';
					break;
	        }
	    }
		
			htmlStr += '</'+elType+'>';
		htmlStr += '</'+elType+'>';
        return htmlStr;
    }
    
    this.animateSVG = function(name, $el){
        var $svg = $el.find('.svgWrap').parent();
        // console.log('SVG animate', name, $el, $svg)
        var timeline = new TimelineLite();
        switch(name.toLowerCase()){
            case 'menu':
//                timeline.from($el.find(".menuWrap .menu-bullet"), .5, {drawSVG:"0%"});
                timeline.staggerFrom($svg.find('.menu-bullet'), .3, {drawSVG:"0%"}, .2);
                timeline.staggerFrom($svg.find('.menu-item'), .3, {drawSVG:"0%"},.2);
                
                break;
            case 'resource':
//                timeline.from($el.find(".resourceWrap path"), 2, {drawSVG:"0%"});
                timeline.from($svg.find('polygon'), 2, {drawSVG:"0%"});
                timeline.from($svg.find('line'), .3, {drawSVG:"0%"}, .2);
                
                break;
            case 'help':
                timeline.from($svg.find('path'), 1, {drawSVG:"0%"});
                timeline.from($svg.find('line'), .1, {drawSVG:"0%"});
                timeline.from($svg.find('circle'), 1, {drawSVG:"0%"});
                
                break;
            case 'language':
//                timeline.from($el.find(".languageWrap circle"), .5, {drawSVG:"0%"});
//                timeline.from($el.find(".languageWrap ellipse"), .5, {drawSVG:"0%"},.3);
//                timeline.staggerFrom($el.find(".languageWrap line"), .3, {drawSVG:"0%"},.2);
                timeline.from($svg.find('path'), .5, {drawSVG:"0%"});
                timeline.staggerFrom($svg.find('line'), .3, {drawSVG:"0%"},.2);
                timeline.from($svg.find('circle'), .5, {drawSVG:"0%"});
                
                break;
            
            case 'audio':
                timeline.to($svg.find('#wave1'), 0.1, {opacity: 0})
                // timeline.to(".audioWrap #wave2", {duration: 0.3, morphSVG: ".audioWrap #crossLeft"})
                timeline.to($svg.find('#wave2')[0], 0.3, {morphSVG:$svg.find('#crossLeft')[0], ease: Sine.easeInOut})
                timeline.to($svg.find('#wave3')[0], 0.3, {morphSVG:$svg.find('#crossRight')[0], ease: Sine.easeInOut}, '-=0.3');
                break;
            case 'exit':
//                timeline.from($svg.find('polygon"), 2, {drawSVG:"0%"});
                timeline.from($svg.find('line'), .5, {drawSVG:"0%"}, .2);
                
                break;
            case 'plusminus':
                timeline.to($svg.find('.plus'), .5, {rotation: 90, transformOrigin:"50% 50%"});
                timeline.to($svg.find('.minus'), .5, {rotation: 180, transformOrigin:"50% 50%"},0);
                
                break;
            case 'plusminus-reverse':
                timeline.to($svg.find('.plus'), .5, {rotation: 0, transformOrigin:"50% 50%"});
                timeline.to($svg.find('.minus'), .5, {rotation: 0, transformOrigin:"50% 50%"},0);
                break;
            case 'plustick':
                timeline.from($svg.find('path.plus'), 1, {drawSVG:"0%"});
                //timeline.from($el.find(".plustickWrap path.tick"), 1, {drawSVG:"0%"});
                break;
            case 'tick':
                timeline.from($svg.find('path'), 1, {drawSVG:"0%"});
                break;
            case 'tickcross':
                timeline.from($svg.find('path'), 1, {drawSVG:"0%"});
                break;
            case 'bulb':
                //timeline.from($el.find(".bulbWrap #ring1"), 1, {drawSVG:"0%"});
                //timeline.from($el.find(".bulbWrap #ring2"), 1, {drawSVG:"0%"},.1);
                //timeline.from($el.find(".bulbWrap #ring3"), 1, {drawSVG:"0%"},0);
                timeline.from($svg.find('#bulb'), 1, {drawSVG:"0%"});
                timeline.from($svg.find('#screw1'), .3, {drawSVG:"0%"},.8);
                timeline.from($svg.find('#screw2'), .3, {drawSVG:"0%"});
                timeline.from($svg.find('#screw3'), .3, {drawSVG:"0%"});
                //timeline.from($el.find(".bulbWrap #filament"), 1, {drawSVG:"0%"});
                break;
            case 'magnify':
                timeline.from($svg.find('#stem'), .2, {drawSVG:"0%"});
                timeline.from($svg.find('#handle'), 1, {drawSVG:"0%"});
                timeline.from($svg.find('#glass'), 1, {drawSVG:"0%"});
                break;
            case 'callout':
//                timeline.from($el.find(".calloutWrap #bubble1"), 1, {drawSVG:"0%"});
//                timeline.from($el.find(".calloutWrap #bubble2"), 1, {drawSVG:"0%"});
//                timeline.from($el.find(".calloutWrap #line1"), .3, {drawSVG:"0%"}, 1.5);
//                timeline.from($el.find(".calloutWrap #line2"), .3, {drawSVG:"0%"}, 1.6);
//                timeline.from($el.find(".calloutWrap #line3"), .3, {drawSVG:"0%"}, 1.7);
                
                break;
            case 'casestudy':
                timeline.staggerFrom($svg.find('.page-line'), .3, {drawSVG:"0%"},.1);
                timeline.from($svg.find('.outline'), 1, {drawSVG:"0%"}, '-=.8');
                timeline.from($svg.find('.spine'), .3, {drawSVG:"0%"}, '-=.2');
                
                break;
            case 'warning':
                timeline.from($svg.find('#exclamation'), .8, {drawSVG:"0%"});
                timeline.from($svg.find('circle'),.3, {drawSVG:"0%"}, .2);
                timeline.from($svg.find('#triangle'), 1.5, {drawSVG:"0%"});
                
                break;
            case 'padlock':
                timeline.from($svg.find('rect'), .8, {drawSVG:"0%"});
                timeline.from($svg.find('.keyhole'), .8, {drawSVG:"0%"}, .2);
                timeline.from($svg.find('.lock'), .5, {drawSVG:"0%"});
                
                break;
            case 'rewind':
                timeline.staggerFrom($svg.find('.arrow'), 1, {drawSVG:"0%"},.5);                
                break;
            case 'thumbsup':
                timeline.from($svg.find('path'), 2, {drawSVG:"0%"});                
                break;
            case 'ldquo':
                timeline.staggerFrom($svg.find('path'), 1, {drawSVG:'0%'},.3);                 
                timeline.staggerFrom($svg.find('path'), .5, {fill:'transparent'},.3);
                // timeline.staggerTo($el.find('.ldquoWrap path'), .5, {stroke:'transparent'},'-=.5');               
                break;
            case 'rdquo':
                timeline.staggerFrom($svg.find('path'), 1, {drawSVG:'0%'},.3);                
                timeline.staggerFrom($svg.find('path'), .5, {fill:'transparent'},.3);      
                // timeline.staggerTo($el.find('.rdquoWrap path'), .5, {stroke:'transparent'},'-=.5');            
                break;
            
            case 'pencil':
                timeline.staggerFrom($svg.find('path'), 1, {drawSVG:'0%'},.3);                
                timeline.staggerFrom($svg.find('line'), .5, {drawSVG:'0%'},.3);      
                // timeline.staggerTo($el.find('.rdquoWrap path'), .5, {stroke:'transparent'},'-=.5');            
                break;
            
            case 'tickdisc':
                timeline.from($svg.find('circle'), .3, {drawSVG:"0%"});
                timeline.from($svg.find('path'), 1, {drawSVG:"0%"}, .2);
                break;

            case 'chevron-down':
                timeline.to($el.find("path"), .3, {rotation: 0, transformOrigin:"50% 50%"});
                break;

            case 'chevron-down-reverse':
                timeline.to($el.find(".path"), .3, {rotation: 180, transformOrigin:"50% 50%"});
                break;
        }
        return timeline;
    }

    this.preview = function(){
        var elType = 'div';
        var htmlStr = '<'+elType+' class="svgPreview">';
        for (var i = 0; i < this.svgs.length; i++){
            htmlStr += '<'+elType+' class="svgPreviewItem'+(InlineSVG[this.svgs[i]] ? ' svgPreviewExternal' : ' svgPreviewInternal')+'">';
                htmlStr += '<'+elType+' class="svgPreviewItemSVG">';
                    htmlStr += this.createSVG(this.svgs[i]);
                htmlStr += '</'+elType+'>';
                htmlStr += '<'+elType+' class="svgPreviewItemLabel">'+this.svgs[i]+'</'+elType+'>';
            htmlStr += '</'+elType+'>';
        }
        htmlStr += '</'+elType+'>';
        $('body').append(htmlStr);
    }
}