/**
 * @fileoverview This Plug-in is used to create the Modal. 
 * @version 2.0
 */

(function( $ ){
    var $alertModal
    var $el;
    var options;

    var hiddenMethod = null;

	var methods = {
        init : function( _options ) {

            var defaults = {
                title: "",
				message: "",
				confirm: "Continue",
				cancel: "Cancel",
                class: "",
                size: "sm", // sm, md, lg
				onConfirm : null,
				onCancel : null
            };

            console.log('Alert modal init');

            options = $.extend(defaults, _options);

            var html = getAlertHTML();
            if (html != ''){
                $("#content-wrapper").append(html);

            }

            $alertModal = $('#alertModal');
            //var navTextXML=xmlData.navigationTextXML;

            // $alertModal.removeClass().addClass('modal fade');
            // removed color for HSBC
            // $alertModal.addClass(options.color);
            // if (options.data.class){
            //     $alertModal.addClass(options.data.class);
            // }

            var classes = [];
            if (options.class){
                classes = options.class.split(' ');
            }

                //     $alertModal.addClass(options.data.class);
                // }
            if (options.size == 'sm' || options.size == 'small'){
                classes.push('modal-sm');
            } else if (options.size == 'lg' || options.size == 'large'){
                classes.push('modal-lg');
            }

            $alertModal.find('.modal-dialog').removeClass().addClass('modal-dialog '+classes.join(' '));
            
            if (options.title){
                $alertModal.find('.modal-title').html(options.title);
                // $alertModal.find('.modal-title').focus(); // removed from Atos report
            } else {
                $alertModal.find('.modal-title').html('');                
            }

            $alertModal.find('.modal-body').html(options.message);

            
			$alertModal.find('.btn.confirmBtn .btnText').html(defaults.confirm);
			if (!defaults.cancel){
                $alertModal.find('.btn.closeBtn').hide();
			}else{
                $alertModal.find('.btn.closeBtn').show();
				$alertModal.find('.btn.closeBtn .btnText').html(defaults.cancel);
			}

            $alertModal.find('.btn.confirmBtn').bind('click', function () {
                // console.log('ALERT confirm');
                hiddenMethod = 'confirm';
                if (defaults.onConfirm)
                    defaults.onConfirm();
                // return false;
            });
            $alertModal.find('.btn.closeBtn').bind('click', function () {
                // console.log('ALERT close');
                hiddenMethod = 'close';
                // note - below has been removed as onClose replaced by onCancel, and now handled by onHide (so can also be fired on click of overlay)
                // if (defaults.onClose)
                //     defaults.onClose();
            });
            // add event listeners for modal show
            $alertModal.on('show.bs.modal', onShow);

            $(window).resize(function(){		
                
                // if (componentController){
                //     componentController.resizeContent();
                // }
                $.Alert('resize');

            });
            /*setTimeout(function(){
//                console.log('delay 2000')
                // componentController.setupComplete();
                if (componentController){
                    componentController.displayComponents();
                }
//                $(window).trigger('resize');
                //$.Alert('resize');
            },250);*/

            $alertModal.modal();
        },
        show : function() { 
            //var navTextXML=xmlData.navigationTextXML;
            /*$('.ExitYes .btntext').html(navTextXML.closeCourse[0].buttonText[0].text[0].Text); 
            $('.ExitNo .btntext').html(navTextXML.closeCourse[0].buttonText[0].text[1].Text);			
           $('.Exitparent').show(); */
            console.log('MODAL SHOW');
        },
        hide : function() { 
            //$('.Exitparent').hide(); 
            console.log('MODAL HIDE');

        },
        resize : function() { 
        }
    }

    function onShow(e){
        // console.log('Alert Modal onShow');

        // get focussed element
		$el = $(document.activeElement);

        $alertModal.off('show.bs.modal', onShow);
        $alertModal.on('hidden.bs.modal', onHide);

        // hide the main content when modal is shown
        $('.ui-container').attr('aria-hidden', 'true');
    }

    function onHide(e){
        console.log('Alert Modal onHide');

        if ($el){
            $el.focus();
        }

        $alertModal.off('hidden.bs.modal', onHide);

        // unhide the main content when modal is shown
        $('.ui-container').attr('aria-hidden', 'false');
        
        if (hiddenMethod != 'confirm' && options.onCancel){
            options.onCancel();
            options.onCancel = null;
        }
    }    

	function updatePageLayOut(){
		
	}
	/**
	 * This Plug-in contains a set of methods that are used to create the Exit window.
	 * @class This Plug-in is used to create the Exit window.
	 * @param text {String} Exit message text.
	 * @param yes {String} Yes button text.
	 * @param no {String} No button text.
	 * @param onYes {callback} Executes when user selects Yes.
	 * @param onNo {String} Executes when user selects No.
	 * @example
	 * How to Use this plug-in
		 $.Exit({
				text:"Exit message",
				yes:"Yes",
				no:"No",
				onYes:function(){
				
				},
				onNo:function(){
					
				}			
			});
	 */
	$.fn.Alert = function( method ) {		
		if ( methods[method] ) {
		  return methods[ method ].apply( this, Array.prototype.slice.call( arguments, 1 ));
		} else if ( typeof method === 'object' || ! method ) {
		  return methods.init.apply( this, arguments );
		} else {
		  $.error( 'Method ' +  method + ' does not exist on Alert JS' );
		}    
	
	};	
	$.extend({Alert: $.fn.Alert});
	
	/**
	 * @function getModalHTML
	 * @description This function returns Exit html structure.
	 * @returns data {String}
	 */ 
	getAlertHTML = function() { 
//		var htmlStr ='<div class="Exitparent"><div class="ExitPopWindow roundedBorder" ><div class="ExitText" tabindex="1" ></div><a class="roundedBorder ExitYes a-button" href="javascript:void(0);" tabindex="2" ><span class="btntext ">Yes</span></a><a class="roundedBorder ExitNo a-button" href="javascript:void(0);" tabindex="3" ><span class="btntext">No</span></a></div></div>';
        var htm = ''
        //<!-- Modal -->
            htm+='<div id="alertModal" class="modal alert-modal fade" tabindex="-1" role="dialog" aria-labelledby="alertModalLabel">';
                htm+='<div class="modal-dialog" role="document">';
                    htm+='<div class="modal-content">';
                        // htm+='<button class="btn closeBtn btn-close-container" data-dismiss="modal" aria-label="'+courseData.data.settings[0].navigation[0].btn_Close[0].Text+'">';
                        //     htm+='<div class="btn-close">';
                        //         htm+= svgUtils.createSVG('cross')
                        //     htm+='</div>';
                        // htm+='</button>';
                        htm+='<div class="modal-header">';
                            htm+='<h2 class="modal-title" id="alertModalLabel"></h2>';
                        htm+='</div>';
                    htm+='<div class="modal-body">';
                    htm+='</div>';
                    htm+='<div class="modal-footer">';
                        htm+='<button class="btn confirmBtn" data-dismiss="modal"><div class="btnText"></div></button>';
                        htm+='<button class="btn closeBtn" data-dismiss="modal"><div class="btnText"></div></button>';
                    htm+='</div>';
                htm+='</div>';
            htm+='</div>';
        
        if ($('.modal#alertModal').length > 0){
            return "";
		}else{
            return htm;  
		}
	};

})( jQuery );