// Avoid `console` errors in browsers that lack a console.
(function () {
    var method;
    var noop = function () {};
    var methods = [
        'assert', 'clear', 'count', 'debug', 'dir', 'dirxml', 'error',
        'exception', 'group', 'groupCollapsed', 'groupEnd', 'info', 'log',
        'markTimeline', 'profile', 'profileEnd', 'table', 'time', 'timeEnd',
        'timeStamp', 'trace', 'warn'
    ];
    var length = methods.length;
    var console = (window.console = window.console || {});

    while (length--) {
        method = methods[length];

        // Only stub undefined methods.
        if (!console[method]) {
            console[method] = noop;
        }
    }
}());

/**
 * @function cssNumber
 * @description This function will return css property value.
 * @param prop {String} CSS property 
 * @return {Number} value.
 * @example
 * var lf = $('.myDiv').cssNumber('left');
 *    
 */
jQuery.fn.cssNumber = function (prop) {
    var v = parseInt(this.css(prop), 10);
    return isNaN(v) ? 0 : v;
};

// plugin function to disable button and also set aria-disabled attribute
jQuery.fn.disable = function () {
    return this.each(function () {
        if ($(this).attr('tabindex') == undefined) {
            $(this).attr('tabindex', '0');
        }
        $(this).addClass(navigationButton.DISABLED).attr('aria-disabled', 'true');//prop('disabled', true);
    });
};

// plugin function to enable button and also set aria-disabled attribute
jQuery.fn.enable = function () {
    return this.each(function () {
        $(this).removeClass(navigationButton.DISABLED).attr('aria-disabled', 'false');//prop('disabled', false);
    });
};