var GameAPIController = function () {
    var self = this;
    
    this.gameAPIAnswers = {};
    
    function init(){
        //console.log('GameAPIController::init ');
        
        if (gameAPIConfig.enabled){
            self.getAnswersFromGameAPI();
        }
    }
    
    this.getGameAPIAnswer = function(questionNum){
        return gameAPIConfig.enabled ? this.gameAPIAnswers[questionNum] : null;
    }
    
    this.getGameAPIAnswers = function(){
        return gameAPIConfig.enabled ? this.gameAPIAnswers : null;
    }
    
    /**
     * @function saveToGameAPI
     * @description  This function saves information to the gameapi
     */
    this.saveAnswerToGameAPI = function(questionNum, responseArray, callbackFunction){
        //user_id, question_id, response, selecedQuestion) {
        var data = {
			user_id: storageController.studentID,
			question_id: questionNum,
            response: responseArray.toString(), 
			name: '',
			email: '',
			course_name: LaunchConfig.course.id, 
			variation:languageConfig.defaultLanguage,
			version: LaunchConfig.course.version
		};
        console.log('GameAPIController::saveAnswerToGameAPI', data);
		$.ajax({
			type: 'POST',
			url: gameAPIConfig.rootURL + 'addAnswer',
//			dataType: 'json',
			data: data, // URI encode data for request
			success: function(response, xhr, type, textStatus) {
				console.log(response);//, xhr, type, textStatus);
                if (callbackFunction){
                    self.getAnswerFromGameAPI(questionNum, callbackFunction);
                } 
			},
			error: function(xhr, type, textStatus, errorThrown) {
				console.log(xhr, type, errorThrown, textStatus);
			}
		});
    }
    
    this.getAnswerFromGameAPI = function(questionNum, callbackFunction){
        console.log('GameAPIController::getAnswerFromGameAPI', questionNum);
        var data = {
            course_name: LaunchConfig.course.id, 
			variation:languageConfig.defaultLanguage,
			version: LaunchConfig.course.version
		};
		$.ajax({
			type: 'POST',
			url: gameAPIConfig.rootURL + 'answer/'+questionNum,
			data: data, // URI encode data for request
//			dataType: 'json',
			success: function(response){
				//console.log('Success: ', response);
                var questionAPIData = JSON.parse(response);
                self.gameAPIAnswers[questionNum] = questionAPIData;
                
                //self.gameAPIAnswers = JSON.parse(response);
                
                if (callbackFunction){
                    callbackFunction(questionAPIData);
                } 
			},
			error: function(xhr, type){
			   //console.log(xhr, type);
			}
		});
    }
    
    this.getAnswersFromGameAPI = function(callbackFunction){
        //console.log('AssessmentController::getAnswersFromGameAPI');
        //user_id, question_id, response, selecedQuestion) {
        var data = {
            course_name: LaunchConfig.course.id, 
			variation:languageConfig.defaultLanguage,
			version: LaunchConfig.course.version
		};
		$.ajax({
			type: 'POST',
			url: gameAPIConfig.rootURL + 'answers',
			data: data, // URI encode data for request
//			dataType: 'json',
			success: function(response){
                
                self.gameAPIAnswers = JSON.parse(response);
                if (self.gameAPIAnswers == null){
                    self.gameAPIAnswers = [];
                }
                
                if (callbackFunction){
                    callbackFunction(response);
                } 
			},
			error: function(xhr, type){
			   console.log(xhr, type);
			}
		});
    }
    
    init();
}