var DebugController = function (opts) {
    var self = this;
    this.$debug;
    this.$slideSelect;

    this.init = init;
    this.update = update;

    function init() {
        self.$debug = $('.debug');

        self.$debug.html(getHTML());

        // find the slide select dropdown
        self.$slideSelect = self.$debug.find('select');

        // get the role names
        var rolesArray = courseData.getRoles();
        var abbrArray = rolesArray.map(obj => obj.abbr).filter(element => {
            return element !== undefined;
        });
        // console.log('DebugController abbrArray', abbrArray);

        for (var i = 0; i < navigationController.pageArray.length; i++) {
            for (var j = 0; j < navigationController.pageArray[i].slides.length; j++) {
                // lets check if the slide has any roles and display these in the dropdown
                var slideRoleArray = [];
                if (abbrArray.length){
                    if (navigationController.pageArray[i].slides[j].roles.length != rolesArray.length) {
                        for (var k = 0; k < navigationController.pageArray[i].slides[j].roles.length; k++) {
                            if (rolesArray[navigationController.pageArray[i].slides[j].roles[k]].abbr){
                                // push the role abbreviation if found
                                slideRoleArray.push(rolesArray[navigationController.pageArray[i].slides[j].roles[k]].abbr);
                            } 
                        }
                    }                
                
                    slideRoleArray = checkRoleAbbreviationsForSlide(abbrArray, slideRoleArray);
                    
                } else {
                    // no roles so just push the slide roles numbers
                    slideRoleArray = navigationController.pageArray[i].slides[j].roles;
                }

                var xmlPathArray = navigationController.pageArray[i].slides[j].xmlPath.replace('.xml', '').split('/');
                // remove the first item(s) for the xmlPath
                const xmlPath = xmlPathArray.length > 2 ? xmlPathArray.slice(2).join('/') : xmlPathArray.slice(1).join('/');
                // create the ref
                var ref = 'm' + navigationController.pageArray[i].module + 'p' + navigationController.pageArray[i].page + 's' + j;
                // add the slide to the dropdown
                self.$slideSelect.append('<option value="' + ref + '" data-module="' + navigationController.pageArray[i].module + '"data-page="' + navigationController.pageArray[i].page + '" data-slide="' + j + '">' + ref + ' | ' + xmlPath + (slideRoleArray.length ? ' (' + slideRoleArray.join(',')+')' : '') + '</option>');
            }
        }

        self.$slideSelect.on('change', function () {
            // navigationController.changePageToFile($("option:selected", this).val());
            var $option = $("option:selected", this);
            navigationController.changePageToData($option.data('module'), $option.data('page'), $option.data('slide'));
        });

        if (navigationConfig.pageLock) {
            self.$debug.find('.pageLockToggle').addClass('locked');
        }
        self.$debug.find('.pageLockToggle').on('click', function () {
            navigationConfig.pageLock = !navigationConfig.pageLock;
            $(this).toggleClass('locked');
            navigationController.replayPage();
        });

        // use the ?animdebug= parameter from the URL - show animation debug?
        var animDebugURLParameter = parseInt(utils.getURLParameter('animdebug'));
        if (!isNaN(animDebugURLParameter)) {
            $('body').addClass('anim-debug-on');
            self.$debug.find('.animToggle').addClass('active');
        }
        self.$debug.find('.animToggle').on('click', function () {
            $('body').toggleClass('anim-debug-on');
            $(this).toggleClass('active');
            navigationController.replayPage();
        });
    }

    function getHTML() {

        var htmlStr = '<div class="debug-container container" aria-hidden="true">';
        htmlStr += '<div class="row">';
        htmlStr += '<div class="col-sm-12">';
        htmlStr += 'DEBUG: ';
        htmlStr += '<a class="debug_btn_Back debug_Btn" href="javascript:void(0);" title="Go to previous page">Back</a>';
        htmlStr += ' | ';
        htmlStr += '<a class="debug_btn_Reload debug_Btn" href="javascript:void(0);" title="Reload browser and return at current page">Reload</a>';
        htmlStr += ' | ';
        htmlStr += '<a class="debug_btn_Next debug_Btn" href="javascript:void(0);" title="Go to next page">Next</a>';
        htmlStr += ' | ';
        htmlStr += 'Template: <span class="slide_template"></span>';
        htmlStr += ' | ';
        htmlStr += 'Slide: ';
        htmlStr += '<select></select>';
        htmlStr += '<a class="debug_Btn pageLockToggle" href="javascript:void(0);" title="Toggle page lock on/off"></a>';
        htmlStr += '<a class="debug_Btn animToggle" href="javascript:void(0);" title="Toggle animation debug on/off"></a>';
        htmlStr += '<div class="debug-icon-wrapper">';
        if (liveEditConfig.enabled) {
            htmlStr += '<div class="icon-liveedit active" title="LiveEdit enabled"></div>';
        } else {
            htmlStr += '<div class="icon-liveedit" title="LiveEdit disabled"></div>';
        }
        if (LaunchConfig.cdn.enabled && !LaunchConfig.cdn.updatesOnly) {
            htmlStr += '<div class="icon-livecdn active" title="LiveContent enabled"></div>';
        } else {
            htmlStr += '<div class="icon-livecdn" title="LiveContent disabled"></div>';
        }
        if (LaunchConfig.cdn.enabled && LaunchConfig.cdn.updatesOnly) {
            htmlStr += '<div class="icon-liveupdate active" title="LiveUpdate enabled"></div>';
        } else {
            htmlStr += '<div class="icon-liveupdate" title="LiveUpdate disabled"></div>';
        }
        htmlStr += '</div>';

        htmlStr += '</div>';
        htmlStr += '</div>';
        htmlStr += '</div>';

        return htmlStr;
    }

    function update() {
        //var locationStr = window.location.substr(0, window.location.indexOf('?'));
        var searchParams = location.search.split('&');
        // console.log('DebugController', searchParams)

        var pageFound = false;
        var langFound = false;
        for (var i = 0; i < searchParams.length; i++) {
            if (searchParams[i].indexOf('page=') != -1) {
                // only update the page number - keep other URL search paramaters
                var tmp = searchParams[i].split('=');
                tmp[1] = navigationController.currentPage;
                searchParams[i] = tmp.join('=');
                pageFound = true;
            }
            if (searchParams[i].indexOf('lang=') != -1) {
                searchParams[i] = 'lang=' + currentLanguage;
                langFound = true;
            }
        }

        if (!pageFound) {
            searchParams.push('page=' + navigationController.currentPage);
        }
        if (!langFound && currentLanguage != languageConfig.defaultLanguage) {
            // allow refresh in current language
            searchParams.push('lang=' + currentLanguage);
        }
        // console.log('DebugController', location.pathname, searchParams.join('&'))
        self.$debug.find('.debug_btn_Reload').attr('href', location.protocol + '//' + location.host + location.pathname + searchParams.join('&'));
        //'?debug=1&page='+navigationController.currentPage);

        self.$debug.find('.slide_template').html(navigationController.pageArray[navigationController.currentPage].slides[0].template);
        var currentPos = navigationController.pageArray[navigationController.currentPage];
        var ref = 'm' + currentPos.module + 'p' + currentPos.page + 's' + 0;
        self.$slideSelect.val(ref);
    }

    this.showDebug = function (arg) {
        arg = arg == undefined ? true : arg;

        if (arg) {
            $('body').addClass('debug-on');
            self.$debug.removeClass('hidden');
        } else {
            $('body').removeClass('debug-on');
            self.$debug.addClass('hidden');
        }
    }

    this.hideDebug = function (arg) {
        self.showDebug(false);
    }

    function checkRoleAbbreviationsForSlide(abbrArray, slideRoleArray) {
        // Check if there are any items in abbrArray that are not present in slideRoleArray
        const matchingItems = abbrArray.filter(item => {
            const prefix = item.split("-")[0]; // Get the prefix before the dash
            return (
                slideRoleArray.indexOf(item) === -1 && // Exclude items already present in slideRoleArray
                slideRoleArray.some(element => element.startsWith(prefix + '-')) // Check if there are matching items in slideRoleArray
            );
        });

        // remove any duplicates
        matchingItems - _.uniq(matchingItems);

        if (matchingItems.length > 0) {
            // found other items so keep as is
            return slideRoleArray;
        } else {
            var returnArray = _.uniq(slideRoleArray.map(item => item.split('-')[0]));
            // remove any duplicates
            returnArray - _.uniq(returnArray);
            return returnArray;// Return just the prefixes from slideRoleArray
        }
    }
};