    this.searchObj = function(node, termArray, obj){
        if (!obj){
            obj = {path:[], terms:[]}
        }
        
        if (node._childorder){            
            //if (node._childorder.length){            
                for (var i = 0; i < node._childorder.length; i++){
                    //console.log(node)
                    var newObj = $.extend(true,{},obj);                

                    var nodeName = node._childorder[i].node;
                    var nodeData = node[nodeName][node._childorder[i].pos];

                    newObj.path.push(nodeName+node._childorder[i].pos);
                    if (nodeName == 'module' || nodeName == 'page' || nodeName == 'slide'){
                        newObj[nodeName] = node._childorder[i].pos;
                    }

                    if (nodeData.Text){
                        //if (nodeData.Text.toLowerCase().indexOf(term) != -1){
                        var found = false;
                        for (var j = 0; j < termArray.length; j++){
                            if (nodeData.Text.toLowerCase().indexOf(termArray[j].toLowerCase()) != -1){
                                newObj.terms.push(termArray[j]);
                                found = true;
                            }
                        }
                        if (found && newObj.module != undefined && newObj.page != undefined && newObj.slide != undefined){
                            // only store results found within slide
                            newObj.text = nodeData.Text;
                            resultsArray.push(newObj);
                        }
                    } else{
                       this.searchObj(nodeData, termArray, newObj);
                    }
                }
            //}
        }
    }
    
