    function getEscaped(str) {
        return str.replace(/[\\[*+.()?^{|$]/g, "\\$&");
    }
    
    function snippet(str, terms){
        var escapedString = str.replace(/<.*?>/g, "")
        var escaped = "(" + terms.map(function(term) {
         return getEscaped(term)
        })
        .reduce(function (accStr, nextTerm) {
          return accStr + "|" + nextTerm;
        }) + ")";
        var regExp = new RegExp(escaped, "gi");
        // returns an array containing all matches
        //console.log(escapedString, escapedPhrase, escapedString.match(regExp))
        var wordArray = escapedString.split(" ");


        var matchPositions = [];

        for (var i = 0; i < wordArray.length; i++) {

          if (wordArray[i].match(regExp)) {
            matchPositions.push(i);  
          }
        }
        //    matchPositions = [2,43,71,79,86,89,106]
        //    matchPositions = [2,5, 6 ,7,43,47, 71,79,86,89,106]
        var snippetComponents = [];
        var limit = 5;

        if (matchPositions.length == 0) {
          return null;
        } else if (matchPositions.length == 1) {
          snippetComponents = [[matchPositions[0]]];
        } else {
          var newSlice = true;
          var sliceStart = 0; 
          var sliceEnd;
          for (var i = 0; i < matchPositions.length; i++) {
            sliceEnd = i;
            // Handle last element
            if (!matchPositions[i+1]) {
              if (matchPositions[i] - matchPositions[i - 1] > limit) {
                snippetComponents.push(matchPositions.slice(sliceStart, sliceEnd));
                snippetComponents.push([matchPositions[i]]);
              } else {
                  snippetComponents.push(matchPositions.slice(sliceStart, sliceEnd + 1));
              }
            }

            // End slice if current match position is greater than word limit beyond the previous match
            else if(i > 0 && matchPositions[i] - matchPositions[i - 1] > limit) {
              snippetComponents.push(matchPositions.slice(sliceStart, sliceEnd));
              newSlice = true;
              sliceStart = i;
            }
          }
        }

        // Create snippets from snippetComponents
        var snippets = snippetComponents.map(function(components) {
           // Assemble base snippet string

            var snippet = [];
            snippet.push((wordArray[components[0]]));
            if (components.length > 1) {
                for (var i = components[0] + 1; i <= components[components.length - 1]; i++){
                    snippet.push(wordArray[i]);
                }
        //            snippet = snippet.join(" ");
            }

            /* Sanitise snippet
            * add words to either side up to limit or sentence end
            * append ... if necessary
            */
            var prepend = false; 
            var append = false;

            var initialLimit = limit;
            // Beginning of snippet
            for (var i = 1; i <= initialLimit; i++) {
                var index = components[0] - i;
                if (wordArray[index] !== undefined && !wordArray[index].match(/\S/)){
                        initialLimit++;
                    } 
                if (wordArray[index] === undefined) {
                    break;
                } else if (wordArray[index].match(/\.\s*$/)) {
                    break;
                } else {
                    snippet.unshift(wordArray[index]);
                    //console.log("index", index, wordArray[index]);
                    if (i == limit && wordArray[index - 1] && !wordArray[index - 1].match(/\.\s*$/)) {
                        prepend = true;
                    }
                }
            }
            initialLimit = limit;
            // End of snippet
            if (!snippet[snippet.length - 1].match(/\.\s*$/)) {
                for (var i = 1; i <= initialLimit; i++) {
                    var index = components[components.length - 1] + i;
                    if (wordArray[index] !== undefined && !wordArray[index].match(/\S/)){
                        initialLimit++;
                    } 
                    if (wordArray[index] === undefined) {
                        break;
                    } else if (wordArray[index].match(/\.\s*$/)) {
                        break;
                    } else {
                        snippet.push(wordArray[index]);
                        if (i == limit && !wordArray[index].match(/\.\s*$/) && wordArray[index + 1]) {
                            append = true;
                        }
                    }
                }
            }

            snippet = snippet.join(" ");
            if (prepend) snippet = "..." + snippet;
            if (append) snippet += "...";

            return snippet;
        });
        return snippets;//{wordArray, matchPositions, snippetComponents, snippets};             
    }
    
    function spanWrap(str, term) {
        var escapedTerm = getEscaped(term);
        var regExp = new RegExp(escapedTerm, "gi");
        var spanWrapped = str.replace(regExp, '<span class="term">$&</span>')
        return spanWrapped;  
    }
    
    function highlightOnPage(){
        // remove existing highlights
        removeHighlights();
        
        for (var i = 0; i < terms.length; i++){

            var rgxp = new RegExp(terms[i], 'gi');
            // var escapedTerm = terms[i].replace(/[\\[*+.()?^{|$]/g, "\\$&");
            // search for nodes of type 3 and check for term
            var $elements = $('.slide *')
                                .contents()
                                .filter(function() {
                                    return this.nodeType === 3 && $(this).parent().html().match(rgxp); //Node.TEXT_NODE
                                }).parent();
            var repl = '<span class="searchmarker">$&</span>';
            
            if ($elements.length){
                $elements.each(function(){
                    $(this).html($(this).html().replace(rgxp, repl));
                })
            }
        }
        
        //$('.pagesContainer *:contains('+term+')').
    }

