var TitleController = function(opts) {
    var self = this;
    var $title;
    var $titleText;
    
    this.init = init;
    this.change = change;
    
    init();
    
    function init(){
        $title = $('.pageTitle');
        $titleText = $('.pageTitleText');
    }
    
    function change(_text){
        $titleText.stop().fadeTo(300, 0, function(){
            //$(this).css("visibility", "hidden");   
            $title.removeClass('notitle');
            if (_text.length == 0){
                $title.addClass('notitle');
            }
            $titleText.html(_text);
            $titleText
                .css('opacity', 0)
                //.css('visibility', 'visible')
                .fadeTo(300, 1);
        }); // duration, opacity, callback
    }
};