var AssessmentController = function (_id, _config) {
    var self = this;
    
    this.id = _id;
    this.config = _config;
    
    this.xmlID = _config.assessment || this.id;
    //this.questionNumArray = [];
    
    this.SELECTED = 'r';
    this.CORRECT = 'c';
    
    this.qVars = {
        ANSWERS: 'answers',
        ATTEMPTS: 'attempts',
        MIN: 'min',
        MAX: 'max',
        SCORE: 'score',
        CORRECT: 'correct'
    }
    
    this.data = {
        questions: [],
        correct: 0,
        num: 0,
        score: {
            min: 0,
            max: 0,
            raw: 0,
            scaled: 0
        },
        perc: 0,
        pass: false
    }
    this.moduleBankArray = [];
    //this.questionDataArray = [];
    //this.selectedArray = [];
    //this.answerArray = [];
    //this.correctArray = [];
    
    this.numQuestions       = _config.num;
    this.questionBankArray   = _config.banks; 
    this.passingPerc        = _config.passingPerc;
    this.scoring          = _config.scoring; 
    
    this.assessmentXMLData;
    
    //this.score = 0;
    //this.perc = 0;
    //this.isPass = false;
    
    this.gameAPIData = {};
    
    function init(){
        //console.log('AssessmentController::init ');
    }
    
    this.getData = function(){
        //console.log('AssessmentController::getData');

        return this.reduceData(this.data);
    }

    this.reduceData = function(data){
        // check length of data and adjust variable names here
        return data;
    }

    this.expandData = function(data){
        return data;
    }
    
    this.updateFromLMSData = function(dataObj){
        //console.log('AssessmentController::updateFromLMSData');
        //self.data.correct = _correctArray;
        
        self.data = self.expandData(dataObj);
        
        self.calculateScore();
    }
    
    this.setupAssessment = function(){
        console.log('AssessmentController::setupAssessment', courseData.data.assessment.assessment[0]);
        
        var assessmentBankData;
        // find the assessment in the XML
        for (var i = 0; i < courseData.data.assessment.assessment.length; i++){
            if (courseData.data.assessment.assessment[i].id == self.xmlID){
                self.assessmentXMLData = courseData.data.assessment.assessment[i];
                assessmentBankData = self.assessmentXMLData.banks[0];
                break;
            }
        }

        if (self.data.questions.length == 0){
            if (self.questionBankArray.length){
                // we're pulling from the question banks in assessment.xml
                self.numQuestions = 0;
                self.data.questions = [];
                //console.log(self.questionBankArray.length)
                for (var i = 0; i < self.questionBankArray.length; i++){
                    // check number to draw
                    var numToDraw = (assessmentBankData.bank[i].draw) ? parseInt(assessmentBankData.bank[i].draw) : self.questionBankArray[i];
                    var moduleNo = (assessmentBankData.bank[i].module) ? parseInt(assessmentBankData.bank[i].module) : -1;
                    if (moduleNo != -1){
                        self.moduleBankArray[moduleNo] = i;
                    }
                    self.numQuestions += numToDraw;

                    //console.log('NUM QUESTIONS',self.numQuestions)
                    // prepopulate array
                    var bankArray = [];
                    for (var j = 0; j < assessmentBankData.bank[i].questions[0].question.length; j++){
                        bankArray[j] = {
                            b:i,
                            q:j,
//                            answers:[],
//                            selected:[],
//                            min: 0,
//                            max: 0,
//                            score:0,
//                            correct:0
                        };
                        
//                        if (moduleNo != -1){
//                             bankArray[j].module = moduleNo;
//                        }
                    }
                    var randomArray = [];
                    //console.log(numToDraw, assessmentBankData.bank[i].questions[0].question.length)
                    if (numToDraw <= assessmentBankData.bank[i].questions[0].question.length){
                        while (randomArray.length < numToDraw){
                            // random select from bank
                            randomArray = randomArray.concat(bankArray.splice(Math.floor(Math.random() * bankArray.length), 1)); 
    //                        /console.log('RANDOM',randomArray)
                        }
                    }
                    // store in the self.data.questions
                    self.data.questions = self.data.questions.concat(randomArray);
                }
                //console.log('QUESTIONS', self.data.questions);
            } else {

                for (var i = 0; i < self.numQuestions; i++){

                    self.data.questions.push ({
                        q:i
//                        answers:[],
//                        selected:[],
//                        min: 0,
//                        max: 0,
//                        score:0,
//                        correct:0
                    });

                }

                // shuffle question order?
                if (self.config.shuffle){                    
                    self.data.questions.shuffle();
                }
            }
            for (var i = 0; i < self.numQuestions; i++){

                self.data.questions[i][self.qVars.ANSWERS] = [];
                self.data.questions[i][self.qVars.ATTEMPTS] = [];
                self.data.questions[i][self.qVars.MIN] = 0;
                self.data.questions[i][self.qVars.MAX] = 0;
                self.data.questions[i][self.qVars.SCORE] = 0;
                self.data.questions[i][self.qVars.CORRECT] = 0;
//                        q:i,
//                        answers:[],
//                        selected:[],
//                        min: 0,
//                        max: 0,
//                        score:0,
//                        correct:0
//                    });

                }
        
        }
        
        self.numQuestions = self.data.questions.length;
        
        for (var i = 0; i < self.questionBankArray.length; i++){
            // check number to draw
            var moduleNo = (assessmentBankData.bank[i].module) ? parseInt(assessmentBankData.bank[i].module) : -1;

            if (moduleNo != -1){
                if (self.moduleBankArray[moduleNo] == undefined){
                    self.moduleBankArray[moduleNo] = [];
                }
                self.moduleBankArray[moduleNo].push(i);
            }
        }

        // which qVars to use based on num of questions
        // if (self.numQuestions > 10){
        //     self.qVars = {
        //         ANSWERS: 'a',
        //         ATTEMPTS: 's',
        //         MIN: 'l',
        //         MAX: 'u',
        //         SCORE: 'n',
        //         CORRECT: 'c'
        //     }
        // }
    }
    
    this.initQuestion = function(qNum, answers){
        //if (self.questionDataArray[qNum] == undefined){
        //    self.questionDataArray.push(qNum);
        //}
        //var qNum = id; //self.questionNumArray.indexOf(id);
        // console.log('AssessmentController::initQuestion', qNum, answers);

        if (self.data.questions[qNum][self.qVars.ANSWERS].length == 0){
            // only initialise if not done
            self.data.questions[qNum][self.qVars.ANSWERS] = answers.slice();
        }
    }
    
    this.getAnswers = function(qNum){
        //var qNum = self.questionNumArray.indexOf(id);
        return self.data.questions[qNum][self.qVars.ANSWERS];
    }
    
    this.storeAnswers = function(qNum, selectedAnswers){//, isCorrectArray){
//        var qNum = self.questionNumArray.indexOf(id);
        // console.log('AssessmentController::storeAnswers', qNum, selectedAnswers);
        
//        console.log(self.data.questions[qNum]);
        var questionXMLData = this.getQuestionXMLData(qNum);
        
        var attemptsData = self.data.questions[qNum][self.qVars.ATTEMPTS];
        
        // console.dir(attemptsData);
        // console.log('AssessmentController::storeAnswers', qNum, JSON.stringify(attemptsData), selectedAnswers);
        if (self.config.storeAllAttempts){
            attemptsData.push(selectedAnswers.slice());
        } else {
            attemptsData[0] = selectedAnswers.slice();
        }
        // console.log('AssessmentController::storeAnswers', qNum, JSON.stringify(attemptsData));
        
        var numCorrectOptions = 0;
        var correctCount = 0;
        var incorrectCount = 0;
        var isCorrectArray = [];
        var maxScore = 0;
        
        var questionOptionScore = questionXMLData.optionScore ? parseInt(questionXMLData.optionScore) : self.scoring.correct;

        if (self.data.questions[qNum][self.qVars.CORRECT] != 1){
            // if not already correct store user's attempt
        
            for (var i = 0 ; i < attemptsData[attemptsData.length-1].length; i++){
                // check if using a component or not
                var questionXMLDataOptions = questionXMLData.component ? questionXMLData.component[0].options[0] : questionXMLData.options[0];
                var optionScore = questionXMLDataOptions.option[i].score ? parseInt(questionXMLDataOptions.option[i].score) : questionOptionScore;
                //console.log(optionScore);
                // check if answer was correct
                isCorrectArray[i] = (self.data.questions[qNum][self.qVars.ANSWERS][i] == attemptsData[attemptsData.length-1][i]) ? 1 : 0;
                if (self.data.questions[qNum][self.qVars.ANSWERS][i] != 0){
                    maxScore += optionScore;
                    // correct option
                    numCorrectOptions++;
                    if (attemptsData[attemptsData.length-1][i] != 0){
                        // user was correct
                        correctCount += optionScore;
                    }
                } else {
                    // incorrect option
                    if (!self.scoring.ignoreIncorrect){
                        maxScore += optionScore;
                    }
                    if (attemptsData[attemptsData.length-1][i] != 0){
                        // user was incorrect
                        incorrectCount += optionScore;
                    } else {
                        if (!self.scoring.ignoreIncorrect){
                            // user was correct in not answering this
                            correctCount += optionScore;
                        }                    
                    }               
                }
            }
            self.data.questions[qNum][self.qVars.MIN] = 0;
            self.data.questions[qNum][self.qVars.MAX] = maxScore;
            if (self.scoring.ignoreIncorrect){
                // score per question - +1 correct, -1 incorrect, but can't get less than 0
                self.data.questions[qNum][self.qVars.SCORE] = Math.max(correctCount-incorrectCount, 0);            
            } else {
                // score per question - incorrect answers score points too. no negative points for incorrect
                self.data.questions[qNum][self.qVars.SCORE] = correctCount;
            }
            self.data.questions[qNum][self.qVars.CORRECT] = (isCorrectArray.indexOf(0) == -1) ? 1 : 0;
        }
        
        self.calculateScore();
        
        storageController.setAssessmentData();
    }
    
    this.calculateScore = function(){
        self.data.num = self.data.questions.length;
        self.data.correct = 0;
        self.data.score = {
            raw: 0,
            min: 0,
            max: 0,
            scaled: 0
        }
        for (var i = 0; i < self.data.questions.length; i++){
//                console.log('calculateScore', i, self.data.questions[i].max)
            if (self.data.questions[i][self.qVars.MAX] == 0){
                // if question not done exit
                return false;   
            }
            self.data.correct += self.data.questions[i][self.qVars.CORRECT];
            self.data.score.raw += self.data.questions[i][self.qVars.SCORE];
            self.data.score.max += self.data.questions[i][self.qVars.MAX];
        }
        
        // note: score.scaled is the number of correct options
        self.data.score.scaled = parseFloat((self.data.score.raw/self.data.score.max).toFixed(2));
        // note: perc is the correct overall questions, rather than correct options
        self.data.perc = Math.round((self.data.correct/self.data.num)*100);
        
        if (self.data.perc >= self.passingPerc){
            self.data.pass = true;
        } else {
            self.data.pass = false;
        }
        //console.log('AssessmentController::storeAnswers SCORE:', self.data.score, self.data.perc);

        // the scores in this assessment have changed so may affect modules/pages/slides in the menu
        courseData.updateMenu();
        
        $(document).trigger('ASSESSMENT_SCORE_UPDATE');
        
        // if this assessment stores to the SCORM cmi.score.raw
        if (assessmentConfig.scormScoreAssementID == self.id){
            storageController.setScore(self.data.perc);
            
            storageController.checkCourseComplete();
//            // Depend on Assessment score >= 'passingScore.
//            if (courseConfig.storage.courseCompletion == 1){
//                storageController.setComplete();
//            }
        }
    }
    
    this.getBanksScores = function(){
        var bankArray = [];
        for (var i = 0; i < self.data.questions.length; i++){
            var bankNo;
            if (self.data.questions[i].b){
                // banks are defined
                bankNo = self.data.questions[i].b;
            } else {
                // no banks defined - default to 0
                bankNo = 0;
            }

            if (!bankArray[bankNo]){
                bankArray[bankNo] = {score: 0, total: 0}
            }
            bankArray[bankNo].total ++;
            bankArray[bankNo].score += self.data.questions[i][self.qVars.CORRECT];
        }
        return bankArray;
    }
    
    this.getBankScore = function(bankNo){
        var obj = {score: 0, total: 0};
        for (var i = 0; i < self.data.questions.length; i++){
            if (self.data.questions[i].b == undefined || self.data.questions[i].b == bankNo){
                obj.total ++;
                obj.score += self.data.questions[i][self.qVars.CORRECT];
            }
        }
        return obj;
    }
    
    this.isBankComplete = function(bankNo){
        var bankScoreObj = self.getBankScore(bankNo);
        return bankScoreObj.score == bankScoreObj.total;
    }
    
    this.isModuleBankComplete = function(moduleNo){
//        var isComplete = false;
//        if (!self.isBankComplete(self.moduleBankArray[moduleNo])){
//            isComplete = false;
//        }
        if (self.moduleBankArray[moduleNo]){
            var isComplete = false;
            //if (self.moduleBankArray[moduleNo].length > 1){
                // has submodules
                for (var i = 0; i < self.moduleBankArray[moduleNo].length; i++){
                    if (self.isBankComplete(self.moduleBankArray[moduleNo][i])){
                        isComplete = true;
                        break;
                    }
                }
            //} else {
            //    isComplete = self.isBankComplete(self.moduleBankArray[moduleNo][0]);
            //}
            return isComplete;
//            return self.isBankComplete(self.moduleBankArray[moduleNo]);
        } else {
            return false;
        }
    }
    
    this.showMarkingEnabled = function(){
        return self.config.showMarking;
    }
    
    this.showAnswersEnabled = function(){
        return self.config.showAnswer;
    }
    
    this.feedbackEnabled = function(){
        return self.config.feedback;
    }
    
    this.getQuestionData = function(qNum){
        return self.data.questions[qNum];
    }

    this.getQuestionXMLData = function(qNum){
        var questionXMLData;
        if (self.data.questions[qNum].b){
            questionXMLData = self.assessmentXMLData.banks[0].bank[self.data.questions[qNum].b].questions[0].question[self.data.questions[qNum].q].question[0];
        } else {
            questionXMLData = self.assessmentXMLData.banks[0].bank[0].questions[0].question[self.data.questions[qNum].q].question[0];
        }
        return questionXMLData;
    }

    this.getQuestionDataForNum = function(qNum){
        // qNum is the exact number (before any randomisation)
        for (var i = 0; i < self.data.questions.length; i++){
            if (self.data.questions[i].q == qNum){
                return self.getQuestionData(i);
            }
        }
        return false;
    }

    this.getQuestionXMLDataForNum = function(qNum){
        // qNum is the exact number (before any randomisation)
        for (var i = 0; i < self.data.questions.length; i++){
            if (self.data.questions[i].q == qNum){
                return self.getQuestionXMLData(i);
            }
        }
        return false;
    }

    this.getBankXMLData = function(bNum){
        return self.assessmentXMLData.banks[0].bank[bNum];
    }
    
    this.checkAssessmentAgainstObject = function(obj){
        if (obj.perc){
            // is the perc in this assessment < the required percentage
            return self.data.perc >= obj.perc;
        } else if (obj.score){
            // is the score in this assessment < the required score
            return self.data.score >= obj.score;
        }
        return false;
    }

    init();
}