var AnimationController = function () {
    var self = this;

    self.groups = [];
    self.anims = [];

    function init(){
        // unrelated to this file but lets set some gsap animation config here
        gsap.config({
            nullTargetWarn: false
        });
    }

    function onInView(event, visible){
        
        if (visible) {
            var $anim = $(this);
            $anim.off('inview', onInView);
            $anim.addClass('animinview');
            // $anim.addClass('animready');

            var animIndex = $anim.data('anim-index');
            
            var anim = self.anims[animIndex];
            anim.inview = true;
            
            // console.log('AnimationController::onInView', animIndex);

            // recalculate animation delays
            setAnimDelays();
        }
    }

    function animationStarted(animIndex){
        var anim = self.anims[animIndex];
        // console.log('AnimationController::animationStarted', animIndex, anim.$el.is(':visible'));
        if (!anim.started){
            // console.log('AnimationController::animationStarted', animIndex);

            anim.started = true;

            anim.$el.addClass('animating');
            anim.$el.triggerHandler('inview_anim_start');

            // add timeout for when animation is complete
            createAnimCompleteSetTimeout(animIndex, anim.duration);
        }
    }

    function createAnimCompleteSetTimeout(animIndex, duration){
        // if (self.anims[animIndex].delayTimeout){
        //     clearTimeout(self.anims[i].delayTimeout);
        // }
        var anim = self.anims[animIndex];
        setTimeout(function(){
            
            // console.log('AnimationController::createAnimCompleteSetTimeout', animIndex);
            // if (anim.$el.hasClass('component-text')){
                // console.log('AnimationController::createAnimCompleteSetTimeout', animIndex, duration);
            // }
            if (!anim.animated){
                // console.log('AnimationController::animationStarted', animIndex);
                anim.animated = true;
                anim.$el.removeClass('animating');
                anim.$el.addClass('animated');
                anim.$el.triggerHandler('inview_anim_complete');

                // need to figure out if this is a component and if so, trigger the component animation complete
                if (anim.componentIndex){
                    var slideObj = contentController.getSlideForElement(anim.$el);
                    if (slideObj){
                        // console.log('inview anim complete slide found', anim.$el.attr('id'), anim.componentIndex);
                        var component = slideObj.componentController.getComponentObject(anim.componentIndex);
                        
                        // lets change the instruction if has i or instruction attribute
                        var instruction;

                        if (component.dataNode.i){
                            instruction = parseInt(component.dataNode.i);
                        } else if (component.dataNode.instruction){
                            instruction = parseInt(component.dataNode.instruction);
                        }

                        if (instruction){
                            // change the instruction
                            slideObj.componentController.changeInstruction(instruction);
                        }
                    }
                }
            }
        }, duration);
    }

    function setAnimDelays(){
        // var totalDelay = 0;
        // var animCount = 0;
        for (var i = 0; i < self.groups.length; i++){
            self.groups[i].totalDelay = 0;
            self.groups[i].count = 0;
        }
        for (var i = 0; i < self.anims.length; i++){
            var animIndex = i;
            var anim = self.anims[i];
            var animGroup = self.groups[anim.group];
            if ((courseConfig.animate.inview && !anim.inview) || anim.animated){// || responsiveUtils.isElementHidden(anim.$el)){
                continue;
            }
            animGroup.count++;
            var thisDelay = 0;
            if (animGroup.count > 1){
                // if not first item, add delay
                thisDelay = self.anims[i].delay;
                
                if (courseConfig.animate.sequence && self.anims[i].stagger){
                    // if staggering animation, increment delay for later elements
                    animGroup.totalDelay += self.anims[i].delay;
                    thisDelay = animGroup.totalDelay;
                }
            }

            if (!anim.started){
                setAnimDelay(i, thisDelay);

                // add delay before actual CSS animation starts
                createAnimDelaySetTimeout(animIndex, thisDelay);
            }
        }
    }

    function createAnimDelaySetTimeout(animIndex, delay){
        // console.log('AnimationController::createAnimDelaySetTimeout', animIndex);

        clearTimeout(self.anims[animIndex].delayTimeout);
        
        var anim = self.anims[animIndex];

        if (!responsiveUtils.isElementHidden(anim.$el)){
            anim.$el.addClass('animready');
            // self.anims[animIndex].totalDelay = delay;
            anim.delayTimeout = setTimeout(function(){
                animationStarted(animIndex);
            }, delay);
        } else {
            // console.warn('AnimationController: el hidden', anim.$el.selector, animIndex);
        }
    }

    function setAnimDelay(animIndex, delay){
        var anim = self.anims[animIndex];

        htmlUtils.addAnimationDelayCSS(anim.$el, delay);
    }

    this.setupAnimations = function($elements){
        // console.log('AnimationController::setupAnimations');
        // add group
        self.groups.push({
            $ref: $elements
        })
        
        var delay = 0;
        if (courseConfig.animate.sequence){
            // default sequence delay
            delay = courseConfig.animate.delay;
        }        

        self.$elements = htmlUtils.findAnims($elements); 
        // console.log('AnimationController::setupAnimations', self.$elements.length);
        
        // sort the anims into an animation order here.
        // self.$elements.sort(function sort_li(a, b){
        //     return ($(b).data('position')) < ($(a).data('position')) ? 1 : -1;    
        // }) // sort elements
                //   .; // append again to the list
// sort function callback


        //var animCount = 0;
        if (self.$elements.length){
            self.$elements.each(function(index){
                var $el = $(this);

                $el.data('anim-index', index);
                var animGroupIndex = 0;

                // console.log('AnimationController::setupAnimations', index, responsiveUtils.isElementHidden($el))
                var thisDelay = utils.css_time_to_milliseconds($el.css('animation-delay')) || delay;
                var animDelay = $el.data('anim-delay');
                var stagger = true;
                if (animDelay){
                    if (typeof(animDelay) == 'string'){
                        if (animDelay.indexOf('+=') != -1){
                            thisDelay = parseInt(animDelay.split('+=').join(''))*1000;
                            // found '+=' so animation staggered
                        } else if (animDelay.indexOf('-=') != -1){
                            // found '-=' so animation staggered
                            thisDelay = -(parseInt(animDelay.split('-=').join('')))*1000;
                        }
                    } else {
                        // found just number so absolute delayed animation
                        stagger = false;
                        // thisDelay = parseInt(animDelay.split('=').join(''));
                        thisDelay = parseInt(animDelay)*1000;
                    }
                }

                var $closestHiddenElement = responsiveUtils.getClosestHiddenElement($el);
                // if (responsiveUtils.isElementHidden($el)){
                if ($closestHiddenElement.length){
                    // console.log('AnimationController::setupAnimations isElementHiddenByParent',$el)
                    // remove stagger for any elements hidden
                    // stagger = false;
                    for (var i = 1; i < self.groups.length; i++){
                        if (self.groups[i].$ref.is($closestHiddenElement)){
                            animGroupIndex = i;
                            break;
                        }
                    }
                    if (animGroupIndex == 0){
                        self.groups.push({
                            $ref: $closestHiddenElement
                        }); 
                        animGroupIndex = self.groups.length-1;
                    }
                }

                self.anims[index] = {
                    $el: $el,
                    componentIndex: $el.data('componentindex'),
                    inview: false,
                    started: false,
                    animated: false,
                    group: animGroupIndex,
                    stagger: stagger,
                    delay: thisDelay,
                    duration: utils.css_time_to_milliseconds($el.css('animation-duration'))
                }

                if ($('body').hasClass('anim-debug-on')){
                    var htmlStr = '';
                    htmlStr += '<div class="anim-debug">';
                        htmlStr += '<span class="anim-debug-num" title="anim index">'+index+'</span>-';
                        htmlStr += '<span class="anim-debug-group" title="group">'+animGroupIndex+'</span>';
                        htmlStr += '<div class="anim-debug-item anim-debug-item-anim" title="animation"></div>';
                        htmlStr += '<div class="anim-debug-item anim-debug-item-inview" title="inview"></div>';
                    htmlStr += '</div>';

                    $el.append(htmlStr);
                }
            });
            // /utils.css_time_to_milliseconds($el.css('animation-delay'))+
            
            setAnimDelays();
            
            if (courseConfig.animate.inview){
                //$anims.addClass('animinview');
                self.$elements.on('inview', onInView);
            } else {
                // start all when first element inview
                self.$elements.eq(0).on('inview', onInView);
            }
        }
    }

    this.triggerAnimation = function(el){
        var $el = (typeof(el) == 'number') ? self.anims[el].$el : el;
        var animIndex = (typeof(el) == 'number') ? el : $el.data('anim-index');

        setAnimDelays();

        // animationStarted(animIndex);
    }

    init();
}