function Slide(){
    var self = this;
    var $pageContainer = $('.pageContainer');
    this.currentPage;
    this.slideNum;
    this.slideID;
    this.navXMLData;
    this.langXMLData;
    this.data;
    this.$slide;
    this.templateObject;
    this.xmlFilename;
    this.saveData;
    this.preloadImagesArray = [];
    
    this.componentController;
    
    this.loaded = false;
    this.complete = false;
    this.viewed = false;
    this.templateComplete = false;
    this.componentsComplete = false;
    this.slideCompletionDelay = navigationConfig.slideCompletionDelay*1000;
    this.slideCompletionDelayPassed = (this.slideCompletionDelay > 0) ? false : true;
    
    this.fullheight = false;
    
    this.hidden = true;
    this.mandatory = false;
    this.shownByComponent = false;
    this.hiddenForCurrentRole = true;
    this.hiddenForCurrentLanguage = true;
    
    this.init = init;
    this.preloadImage = preloadImage;
    this.slideReady = slideReady;
    this.slideLoaded = slideLoaded;
//    this.slideElementsAnimInComplete = slideElementsAnimInComplete;
    this.slideComponentsComplete = slideComponentsComplete;
    this.slideContentComplete = slideContentComplete;
    this.checkComplete = checkComplete;
    this.showContent = showContent;
    this.hideContent = hideContent;
    this.showElementContents = showElementContents;
    this.hideElementContents = hideElementContents;
    this.generateComponents = generateComponents;
    this.getSaveData = getSaveData;
    this.setSaveData = setSaveData;
    this.unloadContent = unloadContent;
    this.resizeContent = resizeContent;
    this.breakpointChanged = breakpointChanged;
    this.resumeContent = resumeContent;
    this.isHiddenForUser = isHiddenForUser;
	this.isLastSlide = isLastSlide;
    
    function init(_slideNum){
        self.slideNum = _slideNum;
        self.slideID = 'm'+navigationController.moduleIndex+'p'+navigationController.pageIndex+'s'+self.slideNum;
        //console.log('Slide::init', _slideNum);
        
        self.currentPage = navigationController.currentPage;
        self.pageData = navigationController.pageArray[self.currentPage];
        self.slideData = navigationController.pageArray[self.currentPage].slides[self.slideNum];
        self.id = navigationController.pageArray[self.currentPage].slides[self.slideNum].id;
                
//        console.log('Slide::init slideData', self.slideData);
        //self.navXMLData = xmlData.navXML.module[navigationController.moduleIndex].page[navigationController.pageIndex].slide[self.slideNum];
        //self.langXMLData = xmlData.langXML.module[navigationController.moduleIndex].page[navigationController.pageIndex].slide[self.slideNum];
        self.navXMLData = courseData.getSlideData(navigationController.moduleIndex, navigationController.pageIndex, self.slideNum); 
        self.langXMLData = courseData.getSlideLangData(navigationController.moduleIndex, navigationController.pageIndex, self.slideNum); 
        self.template = navigationController.pageArray[self.currentPage].slides[self.slideNum].template;//self.navXMLData.template;
        //console.log('self.xmlFilename', self.xmlFilename);
        self.title = ''
        if (self.langXMLData.title) {
            self.title = (self.langXMLData.title[0].Text) ? self.langXMLData.title[0].Text : self.title;
        } 
        
//        console.log('Slide::init langXMLData', self.langXMLData.test[0].Text)
        if (navigationConfig.debug){
            debugController.update();
        }
        
        //self.hidden = (self.navXMLData.hide == "1") ? true : false;
        if (!navigationConfig.pageLock){
            self.mandatory = (self.navXMLData.mandatory == "true") ? true : false;
        } else {
            self.mandatory = (self.navXMLData.mandatory == "false") ? false : true;

        }
        // is this slide shown by component? If so dont auto show.
        self.shownByComponent = (self.navXMLData.shownByComponent == "true") ? true : false;
        if (!navigationConfig.pageLock){
            self.shownByComponent = false;
        }
        
        // console.log('Slide::init template', self.template);
        // self.templateObject = eval("new "+self.template+"()");	
        self.templateObject = new AvailableClasses.templates[self.template];
        
        // if loading css
        //self.$slide = $('<div id="slide'+self.slideNum+'" class="slide"><div id="cssContainer'+self.slideNum+'" class="cssContainer"></div><div class="template '+self.template+'"></div></div>');
        
        var classStr = '';
//        if (self.langXMLData.fullheight){
//            self.fullheight = true;
//            classStr += ' fullheight';
//        }
//        if (self.langXMLData.fullheight){
//            classStr += ' fullheight';
//        }
        
        if (self.langXMLData.class){
            classStr += ' '+self.langXMLData.class;
        }
        
        self.langXMLData.nodeName = 'slide';
        var htmlStr = '<div id="slide'+self.slideNum+'" class="slide scrollStop '+htmlUtils.getClasses(self.langXMLData)+'" data-slide="'+self.slideNum+'" '+htmlUtils.getAttributes(self.langXMLData)+' role="region" tabindex="-1" aria-label="">';
            htmlStr += '<div class="template '+self.template+'">';
                htmlStr += '<div class="content-bkg"></div>';
                htmlStr += '<div class="content-wrap">';
                htmlStr += '</div>';
            htmlStr += '</div>';
        htmlStr += '</div>';
        
        self.$slide = $(htmlStr);
        $pageContainer.append(self.$slide);
        
        loadSlide();
    }
    
    function loadSlide(){

        $(document).trigger(courseEvent.SLIDE_LOAD_START, [self]);
        
        utils.testConnection();
        //console.log('Slide::loadSlide', navigationController.moduleIndex, navigationController.pageIndex, self.slideNum); 
        //console.log('Slide::loadSlide', self.pageData.module, self.pageData.page, self.slideNum); 
        self.data = courseData.getSlideLangData(navigationController.moduleIndex, navigationController.pageIndex, self.slideNum); 
        //console.log('Slide::loadSlide', self.data.test[0].Text)
        loadTemplateHTML();
    }
    
    function loadTemplateXML(){
        //console.log(xmlData.langPath+self.navXMLData.xmlPath)
        
        // load XML data
        utils.loadXMLData(langPath+'xml/'+self.navXMLData.xmlPath, function(_xml){
            self.data = _xml;
            
            //console.log('1 loadSlide', _xml);
            //console.log('2 loadSlide', courseData.getSlideLangData(navigationController.moduleIndex, navigationController.pageIndex, self.slideNum));
            //var classStr = self.xmlData.
            self.$slide.find('.template').addClass('slide'+self.xmlFilename);
            
            loadTemplateHTML();
        });
    }
    
    function loadTemplateHTML(){
        if (self.template == 'CUSTOM'){
            self.$slide.find('.template .content-wrap').html('<div class="content-wrap-inner"></div>');
            self.templateObject.init(self);
        } else {
            // load any HTML templates
            self.$slide.find('.template .content-wrap').load(utils.getURL('content/templates/html/'+self.template+'.html'), function(response, status, xhr) {
                //console.log('ContentController::loadTemplateHTML', status);
                if (status == "success") {	

                    // if loading css
                    //loadTemplateCSS();
                    self.templateObject.init(self);
                }
            });
        }
    }
    
//    function loadTemplateCSS(){
//        window.loadFile(['content/templates/css/'+self.template+'.css'], 'slidecss', 'cssContainer'+self.slideNum, function () {
//            self.templateObject.init(self);
//		});	        
//    }
    
    function preloadImage(src){
        src = utils.getImagePath(src);
        if (self.preloadImagesArray.indexOf(src) == -1){
            self.preloadImagesArray.push(src);
        }
    }
    
    function slideReady(){
        // console.log('Slide::slideReady', self.slideNum);
        
        // slide background image
        var $bkgContainer = self.$slide.find('.template .content-bkg');
        $bkgContainer.addClass('no-bkg');

        if (self.data.contentBackground){
            self.setSlideBackground(self.data.contentBackground[0]);
        }

        if (self.componentController){
            self.componentController.setupComponents();
        }

        // find any background images applied in CSS or any images
        self.$slide.find('*').each(function(){
            var bkgImage = $(this).css('background-image').replace('url(','').replace(')','').replace(/\"/gi, "");;
            // check if any <img> tags have a src attribute
            var imageSrc = (this.nodeName == 'IMG') ? $(this).attr('src') : undefined;
            if (bkgImage != 'none'){
                // make sure we dont have a gradient in the background-image 
                if (bkgImage.indexOf('gradient(') == -1) {
                    preloadImage(bkgImage);
                }
            } else if (imageSrc){
                // console.log('SRC:', this.nodeName, imageSrc);
                // preload any image src
                preloadImage(imageSrc);
            }
        });

        // if not set, update slide aria-label to the highest heading level
        // console.log('heading aria', self.$slide.attr('aria-label'))
        var ariaLabel = self.title;
        if (ariaLabel == ''){
            var $heading = self.$slide.find(':header').first();
            ariaLabel = $heading.text();
        }
        if (ariaLabel != ''){
            // we have a title, so create the aria-label
            ariaLabel = courseData.getAccessibleLabel('articleTitleContent').split('{title}').join(ariaLabel);
        } else {
            ariaLabel = courseData.getAccessibleLabel('articleContent');
        }

        self.$slide.attr('aria-label', ariaLabel);
        // || courseData.getAccessibleLabel('articleContent')
        
        
        self.$slide.append('<span class="slideMetaID">SLIDE ID: '+self.slideID+'</span>')
        
//        if (self.preloadImagesArray.length > 0){
//            $.imgpreload(self.preloadImagesArray, {
////                each: function(){
////                    console.log('Slide::image preloaded', $(this).data('loaded'));
////                },
//                all: function(){
////                    //console.log('Slide::images preloaded');
//                    slideLoaded();
//                }
//            });
//        } else {
//            slideLoaded();
//        }
        //console.log('IMAGES - self.preloadImagesArray.length ', self.preloadImagesArray.length )
        if (self.preloadImagesArray.length > 0){
            if (courseConfig.imagePreload == false){
                slideLoaded();
            }
            $.imgpreload(self.preloadImagesArray, {
                each: function(){
                    //console.log('Slide::image preloaded', $(this).data('loaded'));
                },
                all: function(){
//                    //console.log('Slide::images preloaded');

                    if (courseConfig.imagePreload){
                        slideLoaded();
                    }
                }
            });
        } else {
            slideLoaded();
        }
    }
    
    function slideLoaded(){
        //console.log('SLIDE::slideLoaded', self.currentPage, self.slideNum);
        self.loaded = true;
        
        $(document).trigger(courseEvent.SLIDE_LOAD_COMPLETE, [self]);
    }
    
//    function slideElementsAnimInComplete(){
//        // timer to allow minimum time before slide completes
//        setTimeout(function(){
//            self.slideCompletionDelayPassed = true;
//            self.checkComplete();
//        }, self.slideCompletionDelay);
//    }
    
    function slideComponentsComplete(){
        // console.log('slideComponentsComplete', self.slideNum)
        self.componentsComplete = true;
        self.checkComplete();
    }
    
    function slideContentComplete(){
        self.templateComplete = true;
        self.checkComplete();
    }
    
    function checkComplete(){
        //console.log('Slide:checkComplete', self.slideNum, self.templateComplete, self.componentsComplete, self.slideCompletionDelayPassed);
        
        if (!self.complete){
            // console.log('Slide:checkComplete', self.slideNum, self.templateComplete, self.componentsComplete, self.slideCompletionDelayPassed);
            var isComplete = false;
            
            if (self.navXMLData.reentryComplete == 'false'){
                // this slide is not complete on users reentering so don't set isComplete - user must complete slide
            } else if (storageController.isSlideComplete(self.currentPage, self.slideNum) || self.navXMLData.complete == "true" || (self.viewed && self.navXMLData.complete == "view")){
                // console.log('Slide:checkComplete - setting complete');
                isComplete = true;
                // force no delay
                self.slideCompletionDelayPassed = true;
            }
            
            if (isComplete || (self.templateComplete && self.componentsComplete)){
                if(self.slideCompletionDelayPassed){
        //            console.log('SLIDE::checkComplete', self.currentPage, self.slideNum);
                    self.complete = true;
                    self.$slide.addClass('complete');

                    if (courseConfig.googleAnalytics.enabled){
                        gtag('event', 'Complete', {
                          'event_category' : 'Slide',
                          'event_label' : self.slideID//navigationController.moduleIndex+' '+navigationController.pageIndex+' '+self.slideNum
                        });
                    }

                    storageController.setSlideComplete(self.currentPage, self.slideNum);

                    if (self.componentController){                        
                        // let's change any instruction to last available
	                    self.componentController.changeInstruction(99, false);
                    }

                    //storageController.saveData();
                    
                    navigationController.currentSlide = self.slideNum;
                    $(document).trigger(courseEvent.SLIDE_COMPLETE);
                } else {
                    setTimeout(function(){
                        self.slideCompletionDelayPassed = true;
                        self.checkComplete();
                    }, self.slideCompletionDelay);                
                }
            }
        }
    }
    
    function showContent(){
        //console.log('Slide::showContent()', self.slideNum)
        self.hidden = false;
        self.$slide.fadeIn();
        
        if (self.componentController){
            self.componentController.displayComponents();
        }
        
        //titleController.change(self.langXMLData.Text);
        if (self.data.instructionText){
            instructionController.change(self.data.instructionText[0].text[0].Text);
        }
        
        storageController.setSlideVisited(self.currentPage, self.slideNum);
        
        // Google analytics - track slide view
        // self.$slide.on('inview_anim_complete', slideInviewAnimComplete);
        // WARN - elements no longer bubble 'inview_anim_complete' event, so set viewed manually
        self.viewed = true;
        
//        var $anims = self.$slide.find('.anim').last().on('inview_anim_complete', function(){
//            setTimeout(function(){
//                self.slideCompletionDelayPassed = true;
//                self.checkComplete();
//            }, self.slideCompletionDelay);
//        });
        
        //if (storageController.isSlideComplete(self.currentPage, self.slideNum) || self.navXMLData.complete == "true"){
            //console.log('Slide:slide is complete')
            //self.complete = true;
            checkComplete();
        //}
        
        self.templateObject.slideDisplayed();
            
        $(document).trigger(courseEvent.SLIDE_SHOW_CONTENT);
    }
    
    function hideContent(){
        self.$slide.slideUp(function(){
            self.hidden = true;
            $(document).trigger(courseEvent.SLIDE_HIDE_CONTENT);
        });
    }
    
    function slideInviewAnimComplete(){
        self.$slide.off('inview_anim_complete', slideInviewAnimComplete);
        self.viewed = true;
        
            //self.complete = true;
        checkComplete();
//        gtag('event', 'screen_view', {
//          'app_name': courseData.getCourseTitle(),
//          'screen_name' : navigationController.moduleIndex+' '+navigationController.pageIndex+' '+self.slideNum
//        });
        if (courseConfig.googleAnalytics.enabled){
            gtag('event', 'Viewed', {
              'event_category' : 'Slide',
              'event_label' : self.slideID //navigationController.moduleIndex+' '+navigationController.pageIndex+' '+self.slideNum
            });
        }
    }
    
    function showElementContents($el){
        // show an element and find any components within
        //console.log('Slide:showElementContents', $el.find('.component:visible'));
        
        if (self.componentController){
            self.componentController.revealComponents($el.find('.component:visible'));
        }
    }
    
    function hideElementContents($el){
        // show an element and find any components within
        //console.log.log('Slide:hideElementContents', $el.find('.component:visible'));
        
        if (self.componentController){
            //self.componentController.hideComponents($el.find('.component:visible'));
        }
    }
    
    function generateComponents(node){
        //console.log.log('Slide::generateComponents()', self.slideNum)
        // load component controller
        if (!self.componentController){            
            self.componentController = new ComponentController();
            self.componentController.init(self);
        }
        return self.componentController.generateComponents(node.components[0]);
    }
    
    function getSaveData(){
        return storageController.getSlideData(self.currentPage, self.slideNum);
    }
    
    function setSaveData(data){
        //console.log.log('Slide:setSaveData', data, self.currentPage, self.slideNum)
        storageController.setSlideData(self.currentPage, self.slideNum, data);
        storageController.saveData();
    }    
    
    function unloadContent(){
        if (self.componentController){
            self.componentController.unloadContent();
        }
        
        if (self.templateObject.unloadContent){
            self.templateObject.unloadContent();
        }
    }
    function resizeContent(){        
        //self.$slide.css('min-height')
        
        self.$slide.find('.templateBackgroundFixed').height(self.$slide.find('.template').height());
        
        if (self.componentController){
            self.componentController.resizeContent();
        }
        
        if (self.templateObject.updateLayout){
            self.templateObject.updateLayout();
        }
    }

    function breakpointChanged(){
        if (self.componentController){
            self.componentController.breakpointChanged();
        }
        if (self.templateObject.breakpointChanged){
            self.templateObject.breakpointChanged();
        }
    }

    function resumeContent(){        
        if (self.componentController){
            self.componentController.resumeContent();
        }
    }
    
    function isHiddenForUser(){
        return (this.hiddenForCurrentRole || this.hiddenForCurrentLanguage);
    }

    function isLastSlide(){
        return (contentController.lastSlideNum == self.slideNum); 
    }
    
    this.setSlideBackground = function(xmlRef){
        var remainingBreakpoints = Object.keys(responsiveUtils.getBreakpoints());

        if (xmlRef.img){
            if (xmlRef.img.length){
                for (var i = 0; i < xmlRef.img.length; i++){
                    var breakpoints = xmlRef.img[i].breakpoints ? xmlRef.img[i].breakpoints.toLowerCase().split(',') :  Object.keys(responsiveUtils.getBreakpoints());
                    self.addSlideDeviceBackground(xmlRef.img[i], breakpoints);
                    remainingBreakpoints = _.difference(remainingBreakpoints, breakpoints);
                }
            }
        } 
        // console.log('Slide::setSlideBackground remainingBreakpoints', remainingBreakpoints);

        var $bkgContainer = self.$slide.find('.template .content-bkg');
        for (var i = 0; i < remainingBreakpoints.length; i++){
            $bkgContainer.addClass('no-bkg-'+remainingBreakpoints[i]);
        }
    }

    this.addSlideDeviceBackground = function(imgXMLRef, breakpoints){
        // console.log('Slide::content-bkg', 'addSlideDeviceBackground');
        

        var $bkgContainer = self.$slide.find('.template .content-bkg');

        var classArr = [];
        for (var i = 0; i < breakpoints.length; i++){
            classArr.push('visible-'+breakpoints[i]);
        }

        var $bkgElement = $('<div class="'+classArr.join(' ')+'"></div>')
        $bkgContainer.append($bkgElement);
        
        var imgPath = imgXMLRef.path;
        //console.log('Slide::addSlideDeviceBackground imgPath', imgPath)
        if (imgPath){
            var cssObj = {'backgroundImage': 'url('+utils.getImagePath(imgPath)+')'};
            var bgPosition = imgXMLRef.position;
            var bgSize = imgXMLRef.size;
            var fixed = imgXMLRef.fixed;
            if (bgPosition){
                cssObj.backgroundPosition = bgPosition;
            }
            if (bgSize){
                cssObj.backgroundSize = bgSize;
            }
            if (fixed == 'true'){
                $bkgElement.addClass('backgroundFixed');
            }
    //                if (fixed == 'true'){
    //                    //cssObj.backgroundAttachment = 'fixed';
    //                    var $templateBackground = $('<div class="templateBackgroundFixed"></div>');
    //                    $templateBackground.css(cssObj);
    //                    self.$slide.find('.template').prepend($templateBackground);
    //                } else {
    //                }
            $bkgElement.css(cssObj);
            // console.log(cssObj)

            $bkgContainer.removeClass('no-bkg');

        }
    }
}