    this.isSubmenuItemComplete = function(itemIndex, submenuRef){
        var submenu = self.getSubmenu(submenuRef);
        if (submenu){
            if (submenu.items[itemIndex]){
                return submenu.items[itemIndex].complete;
            } 
        }
        return false;
    }
    
